<style scoped>
  .teamBox{text-align: left;background: #f0f0f0;height:100%;overflow:hidden;}
  .headBox{height:60px;}
  .newsRelList{background:#fff;}
  .thImg{overflow:hidden;}
  .thImg img{float:left;margin:.1rem;width:1rem;height:.68rem;}
  .scxc-content li {
    width: 30%;
    height: .68rem;
    float: left;
    margin-right: 5%;
    margin-bottom: .18rem;
  }
  .xcImages .onSheet{
    background: url("../../../../static/img/load-pic.jpg") #ccc center no-repeat;
    background-size:auto 100% !important;
  }
  .xcImages{
    width: 98%;
    margin: 0 auto;
    padding:10px 0;
    text-align: left;
    font-size: 0;
  }
  .xcImages .item{
    height: 70px;
    width: 31.333333%;
    background-color: #f8f8f8;
    display: inline-block;
    margin: 0.1rem 1% 0 1%;
    background-size: cover;
    background-position:center center;
    position: relative;
    text-align: center;
  }
  .xcImages .item .icon-camera {
    font-size: 45px;color: #cfcfcf;
  }
  .xcImages .item .text {
    display: block; font-size: 14px; color: #8f8f8f;    line-height: 20px;
  }
  .xcImages .item .shanchu{
    display: block;
    position: absolute;
    right: -.04rem;
    top: -.04rem;
    color: red;
  }
  .thSel{
    position: absolute;
    top: 8px;
    right: 0;
    opacity: 0;
    width:70vw;height:.32rem;border:none;background: none;font-size:16px;color:#888;text-align-last: right !important;text-align: right !important;}
   .td-notic{
     height: 100%;
     margin-bottom: 0.5rem;
   }
</style>
<style>
.newsRelList .newsRelTxtarea .mint-field-core{text-align: left;}
.newsRelList .mint-cell-value span{margin-right:0;}
.newsRelList .mint-cell-value input.mint-field-core{text-align: right;}
</style>
<template>
  <!--<transition name="slide">-->
  <div class="teamBox">
      <mt-header title="团队通知编辑">
        <mt-button  slot="left" icon="back" @click="backTeam">返回</mt-button>
        <!--<mt-button slot="right" @click="release">发布</mt-button>-->
      </mt-header>
    <div class="newsRelList hdBody">
      <mt-field label="标题" placeholder="请填写" v-model="messageTit" :attr="{ maxlength: 20 }"></mt-field>
      <mt-cell title="推送团队">
        <select class="thSel" v-model="groupId">
          <template v-for="item in teamSel">
            <option :value="item.id" v-if="item.id == groupId" selected>
              {{item.name}}
            </option>
            <option :value="item.id" v-else>
              {{item.name}}
            </option>
          </template>
        </select>
        <label>{{groupName}}</label>
      </mt-cell>
      <mt-field type="textarea" class="newsRelTxtarea" v-model="messageCont" placeholder="通知内容....." rows="4"></mt-field>
      <div class="scxc-content">
        <div class="xcImages">
        <div class="item" @click="lookImg(idx)" v-for="(image,idx) in imageList.minImages" v-bind:style="{backgroundImage: 'url('+image+')'}">
          <i @click="deleImage($event,idx)" class="shanchu iconfont icon-shanchu"></i>
        </div>
        <div class="item up-img" v-if="imageList.minImages.length<9?true:false">
          <i class="iconfont icon-camera" @click="openSheet"></i>
          <span class="text">上传照片</span>
        </div>
      </div>
      </div>
    </div>
    <mt-actionsheet :actions="[
       {name:'拍照',method:() => selectCamera()},
       {name:'从相册中选择',method:() => selectPicture()}
     ]" v-model="sheetVisible">
    </mt-actionsheet>
    <div class="send-btn" >
        <mt-button type="primary"  size="large" @click="release">发布通知</mt-button>
    </div>
  </div>
  <!--</transition>-->
</template>
<script>
  import {LoginUser} from '@/common/js/common';
  import { MessageBox } from 'mint-ui';
  import { Toast } from 'mint-ui';
  import { Indicator } from 'mint-ui';
  export default {
    name: 'newsRelease',
    data () {
      return {
        msg: '消息发布',
        teamSel:[],
        messageTit:'',
        groupId:'',
        groupName:'',
        messageCont:'',
        imageUrl:[],
        imageList:{
          minImages:[],
          maxImages:[]
        },
        sheetVisible:false
      }
    },
    created:function(){
      this.loginUser=LoginUser.getUser();
    },
    watch:{
      groupId:function(newVal,oldVal){
         this.groupId = newVal;
         for(let i=0;i<this.teamSel.length;i++){
           if(this.groupId==this.teamSel[i].id){
             this.groupName = this.teamSel[i].name;
             return;
           }
         }
      }
    },
    activated:function(){
      Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      });
      this.messageTit='';
      this.isCheckIn=true;
      this.groupId='';
      this.messageCont='';
      this.imageUrl=[];
      this.imageList.minImages=[];
      this.imageList.maxImages=[];
      this.teamSel='';
      let it = this;
      //团队列表
      this.$axios({
        method: 'post',
        url: '/group/getGroupInfoList',
        data:this.$qs.stringify({
          userId:it.loginUser.userId,
          pageNum:0,
          pageSize:1000
        })
      }).then(function (response) {
        Indicator.close();
        if(response.data.msg=='success'){
           if(response.data.data.list&&response.data.data.list.length>0){
             it.teamSel = response.data.data.list;
           }else{
             it.teamSel = [{name:'无',id:''}];
           }
           it.groupId = it.teamSel[0].id;
           it.groupName = it.teamSel[0].name;
        }
      }).catch(function(){
        Indicator.close();
        throw new Error('网络异常');
      });
    },
    methods:{
      backTeam:function(){
        //this.$router.push('/msgList/teamCircular');
        this.$router.goBack();
      },
      openSheet:function(){
        this.sheetVisible = true;
      },
      selectPicture:function(){
        //appData = this.helpInfo.images;
        appMethod = this.insertImg;
        this.sheetVisible = false;
        window.location.href = "myapp://parameter?type=TakeAlbum&method=fileImg&imageNum="+(9-this.imageList.maxImages.length);
      },
      selectCamera:function(){
        appMethod = this.insertImg;
        this.sheetVisible = false;
        window.location.href = "myapp://parameter?type=TakePhoto&method=fileImg&imageNum="+(9-this.imageList.maxImages.length);
      },
      lookImg:function(idx){
        let data = {
          ImagesUrlArray:this.imageList.maxImages,
          ImageIndex:idx
        };
        window.location.href = 'myapp://parameter?type=ShowImage&imageData='+JSON.stringify(data);
      },
      deleImage:function(e,index){
        e.stopPropagation();
        MessageBox.confirm('确定删除吗?').then(action => {
          this.imageList.minImages.splice(index,1);
          this.imageList.maxImages.splice(index,1);
          //this.imageList.photoNames.splice(index,1);
          this.imageUrl.splice(index,1);
        })
      },
      insertImg:function(data){
        for(let i=0;i<data.data.length;i++){
          this.imageUrl.push(data.data[i].photoName);
          this.imageList.minImages.push(data.data[i].thumbnailURL);
          this.imageList.maxImages.push(data.data[i].sourcePhotoURL);
        }
        appMethod = null;
      },
      release:function () {
        if(this.messageTit===''){
          let instance = Toast('请填写标题');
          setTimeout(() => {
            instance.close();
          }, 1000);
          return;
        }
        if(this.groupId===''){
          let instance = Toast('请选择团队');
          setTimeout(() => {
            instance.close();
          }, 1000);
          return;
        }
        if(this.messageCont===''){
          let instance = Toast('请填写内容');
          setTimeout(() => {
            instance.close();
          }, 1000);
          return;
        }
        let it = this;
        MessageBox.confirm('确定发布消息吗?').then(action => {
          Indicator.open({
            text: '消息发布中...',
            spinnerType: 'fading-circle'
          });
          this.$axios({
            method: 'post',
            url: '/notice/publishNotice',
            data: {
              userId:it.loginUser.userId,
              messageTit:it.messageTit,
              messageCont:it.messageCont,
              groupId:it.groupId,
              imageUrl:it.imageUrl
            }
          }).then(function (response) {
            Indicator.close();
            if (response.data.code >= 0) {
              let instance = Toast('成功');
              setTimeout(() => {
                instance.close();
                it.backTeam();
              }, 1000);
            }
          }).catch(function(){
            let instance = Toast('发布失败');
            setTimeout(() => {
              instance.close();
            }, 1000);
            Indicator.close();
            throw new Error('网络异常');
          });
        });
      }
    }
  }
</script>




