<style scoped>
  .warningBox{text-align: left;height:100%;overflow:hidden;background:#fff;}
  .headBox{height:60px;}
.warningList{}
  .warningList ul{padding:0 .17rem;/*height: calc(100vh - 40px);overflow-y:scroll;*/}
  .warningList li{overflow:hidden;padding:0.1rem 0;border-bottom:1px solid #d9d9d9;}
  .listT img{width: 1.15rem;height: .75rem;float:right;margin-top:.1rem;}
  .listL{
    text-align: left;
    overflow: hidden;
    height: .9rem;
    line-height: .2rem;
  }
  .listTit{
    display: block;
    word-wrap:break-word;
    display:-webkit-box;
    -webkit-line-clamp:1;
    -webkit-box-orient:vertical;
    overflow:hidden;
    color:#333;
    font-size:.16rem;
    font-weight:bold;
    height: 0.2rem;
    line-height: 0.2rem;
  }
  .listTit em{font-style: normal;color:#0e82e8;}
  .listCont{
    margin-top:.06rem;
    color: #999;
    word-wrap:break-word ;
    display:-webkit-box;
    -webkit-line-clamp:3;
    -webkit-box-orient:vertical;
    overflow:hidden;
    font-size:.14rem;
    line-height: 0.2rem;
  }
  .listB{overflow:hidden;margin-top:.1rem;}
  .listB1{float:left;font-size:.14rem;color:#0e82e8;}
  .listB2{float:right;font-size:.13rem;color:#999;}
</style>
<style>

</style>
<template>
  <!--<transition name="slide">-->
  <div class="warningBox hdBody" >
    <mt-header title="预警信息">
      <mt-button  slot="left" icon="back" @click="backHome">返回</mt-button>
    </mt-header>
    <div class="no-data-pub" v-if="warningList.length<=0">
      <i class="iconfont  icon-zanwuxiaoxi"></i>
      <p class="text">暂无地区预警消息</p>
    </div>
    <div class="warningList hdBody" v-else>
      <mt-loadmore :bottom-method="loadBottom" :top-method="loadTop" :bottom-all-loaded="allLoaded" :autoFill="false" ref="loadmore">
        <div class="j_loadmore">
          <ul>
            <li @click="clickList(idx)" v-for="(item,idx) in warningList">
              <div class="listT">
                <div class="listL">
                  <div class="listTit"><em v-if="item.category !== ''">{{item.category}} | </em>{{item.title}}</div>
                  <div class="listCont">{{item.fcontent}}</div>
                </div>
              </div>
              <div class="listB">
                <div class="listB1"></div>
                <div class="listB2">{{item.date}}</div>
              </div>
            </li>
          </ul>
        </div>
      </mt-loadmore>
    </div>
  </div>
  <!--</transition>-->
</template>
<script>
  import {LoginUser} from '@/common/js/common';
  import { Loadmore,Indicator } from 'mint-ui';
  export default {
    name: 'warningList',
    data () {
      return {
        userId:0,
        pageNum:1,
        pageSize:20,
        allLoaded:false,
        warningList:[]
      }
    },
    components:{Loadmore},
    activated:function(){
       this.userId = LoginUser.getUser().userId;
       this.queryList();
    },
    methods:{
      loadTop:function() {
        this.pageNum = 1;
        this.allLoaded = false;
        this.queryList();
        this.$refs.loadmore.onTopLoaded();
      },
      loadBottom:function(){
        this.pageNum++;
        this.queryList();
      },
      postion:function(){
        if(this.pageNum===1){
          this.$nextTick(function() {
            var dom = document.getElementsByClassName('mint-loadmore');
            dom[0].parentNode.scrollTop = 0;
          })
        }
      },
      backHome:function(){
        this.$router.goBack();
//          if(this.$route.query.param){
//            this.$router.push('/msgList?storage=true&param='+this.$route.query.param);
//          }else if(this.$route.query.isMsg){
//            this.$router.push('/msgList?param=home');
//          }else{
//            this.$router.push('/home');
//          }
      },
      queryList:function(){
        Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        });
        let _this = this;
        this.$axios({
          method: 'post',
          url: 'warningInfo/selectWarningInfo',
          data:this.$qs.stringify({
            userId:this.userId,
            pageNum:_this.pageNum,
            pageSize:_this.pageSize
          })
        }).then(function (response) {
          Indicator.close();
          if(response.data.msg=='success'){
            if(!response.data.data.list||response.data.data.list.length<=0){
              _this.allLoaded = true;
            }
            if(_this.pageNum===1){
              _this.warningList = response.data.data.list;
            }else{
              _this.warningList = _this.warningList.concat(response.data.data.list);
            }
          }
          if(!_this.$refs.loadmore) return;
          _this.$refs.loadmore.onBottomLoaded();
          _this.postion();
        }).catch(function(){
          Indicator.close();
          throw new Error('网络异常');
        });
      },
      clickList:function(idx){
         this.$router.push('/home/warningDet?warningInfoId='+this.warningList[idx].warningInfoId);
      }
    }
  }
</script>




