<style>
  .tdxc  .mint-cell-title {
    text-align: left
  }

  .tdxc  .mint-field-core {
    text-align: right
  }

  .tdxc  .cityName .city  .mint-field-core{
    text-align: left;
  }
  .tdxc  textarea.mint-field-core{
    text-align: left;
  }

</style>
<template>
  <!--<transition name="slide">-->
    <div class="tdxc">
      <mt-header :title="title">
        <mt-button icon="back" slot="left" @click="backPage">返回</mt-button>
        <mt-button slot="right" @click="saveXc()">保存</mt-button>
      </mt-header>
      <div class="tdxcContent">
        <div class="mint-cell" @click="mdCityFn()"><span class="mint-cell-mask"></span>
          <div class="mint-cell-left"></div>
          <div class="mint-cell-wrapper">
            <div class="mint-cell-title"><!---->
              <div><i class="indexicon icon-indicator"></i> <span>终点城市</span></div>
            </div>
            <div class="mint-cell-value is-link"><span>{{dayAddObj.fcityTo}}</span></div>
          </div>
          <div class="mint-cell-right"></div>
          <i  class="mint-cell-allow-right"></i>
        </div>
        <div>{{$route.params.index }}</div>
        <div class="mint-cell" @click="jtfsFn()" ><span class="mint-cell-mask"></span>
          <div class="mint-cell-left"></div>
          <div class="mint-cell-wrapper">
            <div class="mint-cell-title"><!---->
              <div><i class="indexicon icon-indicator"></i> <span>交通方式</span></div>
            </div>
            <div class="mint-cell-value is-link"><span>{{dayAddObj.fvehicle}}</span></div>
          </div>
          <div class="mint-cell-right"></div>
          <i  class="mint-cell-allow-right"></i>
        </div>
        <mt-field label="入驻酒店" class="tdxc-input"  placeholder="填写" v-model="dayAddObj.fhotel"></mt-field>
        <mt-field label="集合地点" class="tdxc-input"  placeholder="填写" v-model="dayAddObj.fassemblyAt"></mt-field>
        <div class="xcms">行程描述</div>
        <div class="xcms-content">
          <mt-field  placeholder="记录这一天的行程安排" type="textarea" rows="8"
                     v-model="dayAddObj.fdesc"></mt-field>
        </div>
      </div>
      <mt-actionsheet :actions="[
           {name:'火车',method:() => selectjtfs('火车')},
           {name:'飞机',method:() => selectjtfs('飞机')},
           {name:'轮船',method:() => selectjtfs('轮船')},
           {name:'汽车',method:() => selectjtfs('汽车')},
           {name:'其他',method:() => selectjtfs('其他')}
         ]" v-model="sheetVisible">
      </mt-actionsheet>
    </div>
  <!--</transition>-->
</template>
<script>
  import { MessageBox } from 'mint-ui';
  import { Toast } from 'mint-ui';
  export default {
    name: 'xcDetail',
    data () {
      return {
        sheetVisible:false,
        areaShow:false,
        title:"",
        dayAddObj:{
          fcityTo:"",
          fassemblyAt:"",
          fhotel:"",
          fvehicle:"",
          fdesc:"",
          fcreateAt:""
        }
      }
    },
    activated:function(){
      let xcCityInfo = JSON.parse(localStorage.getItem('xcCityInfo'));
      localStorage.removeItem('xcCityInfo');
      if(this.$route.params.name=="xiugai"){
        //console.log(this.$route.params.dayObj);
        this.dayAddObj=this.$route.params.dayObj;
        this.title='D' +this.$route.params.dayNum+'行程'
      }else if(xcCityInfo&&xcCityInfo.name=="dayxiugaiCity"){
        let citys = JSON.parse(localStorage.getItem('citys'));
        //console.log(citys);
        if(!citys.length){return}
        if((citys[citys.length-1].cityId)&&(citys[citys.length-1].cityId!="")){
          this.dayAddObj.fcityTo = citys[citys.length-1].name;
        }else if((citys[citys.length-1].cityId=="")&&(citys[citys.length-2].cityId=="")){
          this.dayAddObj.fcityTo = citys[citys.length-3].name;
        }else if((citys[citys.length-1].cityId=="")){
          this.dayAddObj.fcityTo = citys[citys.length-2].name;
        }
      }
    },
    methods: {
      verificationFn:function(){
        var xcInfo=this.dayAddObj;
        if(xcInfo.fcityTo==""){
          this.toastFn("请填写目的城市");
          return false;
        }
        if(xcInfo.fvehicle==""){
          this.toastFn("请选择交通方式");
          return false;
        }
        if(xcInfo.fhotel==""){
          this.toastFn("请填写入住酒店");
          return false;
        }
        if(xcInfo.fassemblyAt==""){
          this.toastFn("请填写集合地点");
          return false;
        }
        if(xcInfo.fdesc==""){
          this.toastFn("请填写行程描述");
          return false;
        }
      },
      toastFn:function(text){
        let instance = Toast(text);
        setTimeout(function () {
          instance.close();
        }, 2000);
      },
      backPage:function() {
        MessageBox.confirm('确定放弃当前行程编辑?').then(action => {
           //this.$router.push({name: 'xingchengxiugai', params: {name:"dayxiugaiBack"}});
          let dayMod = {name:"dayxiugaiBack"};
          localStorage.setItem('dayMod',JSON.stringify(dayMod));
          this.$router.goBack();
        });
      },
      jtfsFn:function(){

        this.sheetVisible=true;
      },
      selectjtfs(tt){
        //this.jtfsName=tt
        this.dayAddObj.fvehicle = tt;
      },
      mdCityFn(){
        this.$router.push({path:'/citySelect',query:{name:"xiugaidayCity"}});
      },
      saveXc: function(){
        let isValidity=this.verificationFn();
        if(isValidity==false){return}
        //this.$router.push({name:'xingchengxiugai',params:{name: "dayxiugai",dayAddObj: this.dayAddObj}});
        let dayMod = {name: "dayxiugai",dayAddObj: this.dayAddObj};
        localStorage.setItem('dayMod',JSON.stringify(dayMod));
        this.$router.goBack();
      }
    }
  }
</script>

<style scoped>
  .mint-popup{
    display: block;
    width: 100%;
  }

  .tdxcContent {
    overflow-y: auto;
    height: calc(100vh - .62rem);
    background: #f0f0f0;
    -webkit-overflow-scrolling: touch;
    overflow-x: hidden;
  }
  .xcms{
    line-height: .45rem;
    font-size: .14rem;
    color: #666666;
    text-align: left;
    padding-left: .1rem;
  }
</style>




