<style scoped>
  .msgBody{
    width: 100%;
    height: calc(100vh);
    text-align: left;
    overflow: hidden;
    background-color: #f0f0f0;
  }
  .mint-cell-text{
    color: #666;
  }
  .mint-cell-value{
    color: #000;
  }
  .noticesInfo{
    background-color: #fff;
  }
  .info{
    border-top: solid 11px #f0f0f0;
    position: relative;
    top: -1px;
  }
  .imgs{
    width: 96%;
    margin: -10px auto 0 auto;
    padding-bottom: 10px;
  }
  .imgs .item{
    display: inline-block;
    width: 33.33333333%;
    height: 0.9rem;
    margin-top: 10px;
  }
  .imgs .item div{
    display: block;
    width: 96%;
    height: 100%;
    margin: 0 auto;
    background-size: cover;
    background-position: center center;
  }
  .sign, .signed{
    width: 0.8rem;
    height: 0.8rem;
    line-height: 0.8rem;
    border-radius: 0.4rem;
    font-size: 0.2rem;;
    display: block;
    margin: 20px auto;
    color: #ff8c05;
    border:solid 1px #ff8c05;
    text-align: center;
    background-color: #fff;
  }
  .sign{
    color: #fff;
    border:solid 1px transparent;
    background-color: #ff8c05;
  }

  .location{
    margin-top: 20px;
    display: block;
    text-align: center;
    color: #5783b9;
    margin-bottom: 30px;
  }
  .cont-text{
    display: block;
    width: 94vw;
    word-wrap:break-word !important;
    padding:10px 0 !important;
    line-height: 0.22rem;
  }
  .notice-color{
    color: #666;
  }
  .noticeTitle{
    display: inline-block;
    width: 0.38rem;
  }
  .title-cont{
    line-height: 0.22rem;
    padding: 0.05rem 0;
  }
</style>
<style>
  .noticesInfo .mint-cell-wrapper{
    padding: 0 14px !important;
  }
  .info .mint-cell:nth-child(2)  .mint-cell-text{
    display: block;
    padding: 14px 0 !important;
  }
  .msgBody{
    color: #666;
  }
</style>
<template>
  <!--<transition name="slide">-->
  <div class="msgBody">
    <mt-header title="团队通知查看">
      <mt-button slot="left" icon="back" @click="backPage">返回</mt-button>
    </mt-header>
    <div class="hdBody">
      <div class="noticesInfo">
        <a class="mint-cell">
          <div class="mint-cell-wrapper">
            <div class="mint-cell-title">
              <span class="mint-cell-text">发起人</span>
            </div><div class="mint-cell-value notice-color">{{noticeInfo.fuserName}}</div>
          </div>
        </a>
        <a class="mint-cell">
          <div class="mint-cell-wrapper">
            <div class="mint-cell-title">
              <span class="mint-cell-text">所属团队</span>
            </div><div class="mint-cell-value notice-color">{{noticeInfo.groupName}}</div>
          </div>
        </a>
        <!--<mt-cell title="发起人">{{noticeInfo.fuserName}}</mt-cell>-->
        <!--<mt-cell title="所属团队">{{noticeInfo.groupName}}</mt-cell>-->
      </div>
      <div class="noticesInfo info">
        <a class="mint-cell">
          <div class="mint-cell-wrapper">
            <div class="mint-cell-title">
              <span class="mint-cell-text noticeTitle">标题</span>
            </div><div class="mint-cell-value notice-color title-cont">{{noticeInfo.ftitle}}</div>
          </div>
        </a>
        <!--<mt-cell title="标题">{{noticeInfo.ftitle}}</mt-cell>-->
        <!--<mt-cell title="消息内容"></mt-cell>-->
        <!--<mt-cell :title="noticeInfo.fcontent" ></mt-cell>-->
        <a class="mint-cell">
          <div class="mint-cell-wrapper">
            <div class="mint-cell-title">
              <span class="mint-cell-text notice-color">消息内容</span>
            </div>
          </div>
        </a>
        <a class="mint-cell">
          <div class="mint-cell-wrapper">
            <div class="mint-cell-title">
              <span class="mint-cell-text cont-text notice-color">{{noticeInfo.fcontent}}</span>
            </div>
          </div>
        </a>
        <div class="imgs">
          <div class="item" @click="clickImage(idx)" v-for="(imgUrl,idx) in noticeInfo.nailImagUrl">
            <div v-bind:style="{backgroundImage: 'url('+imgUrl+')'}"></div>
          </div>
        </div>
      </div>
      <span class="sign" @click="signClick(true)" v-if="noticeInfo.isGroupSign&&!noticeInfo.isSign">签到</span>
      <span class="signed" @click="signClick(false)" v-if="noticeInfo.isGroupSign&&noticeInfo.isSign">已签</span>
    </div>
  </div>
  <!--</transition>-->
</template>
<script>
  import {LoginUser} from '@/common/js/common';
  import { Toast } from 'mint-ui';
  import { Indicator } from 'mint-ui';
  export default {
    name: 'teamNoticeInfo',
    data () {
      return {
        noticeInfo:{}
      }
    },
    activated:function(){
      this.queryDetail();
    },
    methods:{
      backPage:function(){
        if(this.$route.query.isMsg){
          this.$router.push('/home');
        }else{
          this.$router.goBack();
        }
      },
      queryDetail:function(){
        Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        });
        let _this = this;
        let url = '';
        let params = {
          userId:LoginUser.getUser().userId,
          id:this.$route.query.id
        };
        if(this.$route.query.type==8){
          url = '/tsysNotice/getDetailOfApp';
          params.sysToId = this.$route.query.ftoGroupId;
        }else{
          url = '/notice/getDetailOfApp';
        }
        this.$axios({
          method: 'post',
          url: url,
          data:this.$qs.stringify(params)
        }).then(function (response) {
          Indicator.close();
          if(response.data.msg=='success'){
            _this.noticeInfo = response.data.data;
          }
        }).catch(function(){
          Indicator.close();
          throw new Error('网络异常');
        });
      },
      clickImage:function(idx){
        lookImags(idx,this.noticeInfo.sourceImagUrl);
      }
    }
  }
</script>




