<template>
  <!--<transition name="slide">-->
      <div class="msgBody">
        <mt-header title="行程详情">
          <mt-button icon="back" slot="left" @click="backPage">返回</mt-button>
        </mt-header>
        <div class="notices">
          <div class="box">
            <p class="border-bottom"><span class="fl title">行程</span><span class="fr name">{{zxcInfo.ftname}}</span></p>
            <p class="border-bottom"><span class="fl title">性质</span><span class="fr name">{{zxcInfo.ftype}}</span></p>
            <p class="border-bottom"><span class="fl title">目的地</span><span class="fr name">{{zxcInfo.fcityTo}}</span></p>
            <p class="border-bottom"><span class="fl title">集合地点</span><span class="fr name">{{zxcInfo.fassemblyAt}}</span></p>
            <p class="border-bottom"><span class="fl title">交通方式</span><span class="fr name">{{zxcInfo.fvehicle}}</span></p>
            <p><span class="fl title">入住酒店</span><span class="fr name">{{zxcInfo.fhotel}}</span></p>
          </div>
          <div class="gray"></div>
          <div class="box description">
            <h3>行程描述</h3>
            <p class="detail">{{zxcInfo.fdesc}}</p>
            <div class="photoBox">
              <div class="xcImages">
                <!--<div class="item" v-for="item in zxcinfo.fphoto" v-bind:style="{backgroundImage: 'url('+item.furl+')'}"></div>-->
              </div>
            </div>
          </div>
        </div>
        <div class="scxc-btn">
          <button @click="lookAllFn" class="bt2">查看行程全部日程</button>
        </div>
      </div>
  <!--</transition>-->
</template>
<script>
  import {LoginUser} from '@/common/js/common';
  export default {
    name: 'xingchengtixing',
    data () {
      return {
        ftravelId:'',
        zxcInfo:{
          ftname: "",
          ftype: "",
          fcityTo: "",
          fvehicle: "",
          fassemblyAt:"",
          fhotel: "",
          fdesc: ""
        }
      }
    },
    activated(){
     let fid=this.$route.query.fid;
      let name=this.$route.query.name;
      if(name&&name=="ckBack"){
        return;
      }else{
        this.fid=fid;
        let userId=LoginUser.getUser().userId;
        this.queryList(fid,userId);
      }
    },
    methods: {
      queryList(fid,userId){
        let _this = this;
        this.$axios({
          method: 'get',
          url: 'ttravel/selectTravelSub?userId='+userId+'&&fid='+fid,
        }).then(function(response){
          if(response.data.msg=='success'){
            _this.zxcInfo = response.data.data;
            _this.ftravelId=response.data.data.ftravelId;
            _this.ftype=response.data.data.ftype;
          }
        });
      },
      backPage(){
        //this.$router.push("xchengtixing")
        this.$router.goBack();
      },
      lookAllFn(){
        let routerInfo = {name: "xchengtixingDetail", ftravelId: this.ftravelId,ftype:this.ftype};
        localStorage.setItem("routerInfo",JSON.stringify(routerInfo));
        this.$router.push({name: "xcInfo"});
        //this.$router.push({name: "xcInfo", params: {name: "xchengtixingDetail", ftravelId: this.ftravelId,ftype:this.ftype}})
      }
    }
  }
</script>

<style scoped>

  .notices {
    width: 100%;
    height: calc(100vh - .8rem);
    text-align: left;
    overflow-y: scroll;
    background-color: #ffffff;
    -webkit-overflow-scrolling: touch;
    overflow-x: hidden;
  }

  .notices .box {
    padding: 0 0.15rem;
    /*margin-top: 0.16rem;*/
    background-color: #fff;
    overflow: hidden;
  }

  .notices .box p {
    line-height: .48rem;
    font-size: .16rem;
    overflow: hidden;
  }

  .notices .box p .title {
    color: #333333;
  }

  .notices .box p .name {
    color: #999999;
  }

  .border-top {
    border-top: 1px solid #d9d9d9;
  }

  .border-bottom {
    border-bottom: 1px solid #d9d9d9;
  }
.gray{
  background: #f0f0f0;
  height: .1rem;
}
  .fr {
    float: right;
  }

  .fl {
    float: left;
  }

  .description h3{
    line-height: .46rem;
    font-size: .16rem;
    color: #333333;
  }
 .notices .description .detail{
    line-height: .2rem;
    color: #999999;
   margin-bottom: .2rem;
  }
  .photoBox {
    overflow: hidden;
  }

  .xcImages{
    width: 98%;
    margin: 10px auto;
    text-align: left;
    font-size: 0;
  }
  .xcImages .item{
    height: 70px;
    width: 31.333333%;
    display: inline-block;
    margin: 0.1rem 1% 0 1%;
    background-size: cover;
    background-position: center center;
  }

  .scxc-btn {
    position: fixed;
    height: .5rem;
    width: 100%;
    bottom: 0;
    left: 0;
  }

  .scxc-btn button {
    display: inline-block;
    width: 100%;
    margin: 0;
    padding: 0;
    height: 100%;
    border: none;
    outline: none;
    color: #FFFFFF;
    background: #16a3e4;
    font-size: .16rem;
    text-align: center;
    line-height: .5rem;
  }
</style>




