
<style>
  .msgBody .mint-cell-label{
    white-space:nowrap;
    overflow:hidden;
    text-overflow:ellipsis;
    width: 94%;
    padding: 2px 0;
  }
  .msgBody .signInfo .mint-cell-wrapper{
    padding: 4px 14px;
  }
  .help .mint-cell:last-child .mint-cell-title{
    padding-bottom: 13px !important
  }
  .msgBody .signInfo .mint-cell-title{
    padding-left: 50px !important;
    min-height:32px;
  }
  .msgBody .signInfo .mint-cell .mint-cell-wrapper,.msgBody .signInfo .mint-cell{
    background-image: none;
  }
</style>
<style scoped>
  .msgBody{
    width: 100%;
    height: calc(100vh);
    text-align: left;
    overflow:hidden;
  }
  .pic {
    display: block;
    position: absolute;
    top: 0.13rem;
    left: 8px;
    width: 0.5rem;
    height: 0.5rem;
    border-radius: 0.25rem;
    border:solid 1px #16a4e4;
    background-size: cover;
  }
  @media only screen and (-webkit-min-device-pixel-ratio: 3), only screen and (min-device-pixel-ratio: 3) {
    .pic {
      top: 0.08rem;
    }
  }

  .signed{
    color: #000;
    border-bottom: solid 1px #f0f0f0;
    background-color:#f0f0f0 ;
  }
  .signed label{
    padding-left: 10px;
    color: #666;
  }
  .signInfo:last-child{
    border-top: solid 10px #f0f0f0;
  }
  .headIcons{
    width: 100%;
    margin: 0 auto;
  }
  .headIcons .info-icon{
    display: inline-block;
    width: 20%;
    height: 0.6rem;
    margin-top: 0.15rem;
  }
  .headIcons .info-icon i{
    display: block;
    margin: 0 auto;
    width: 0.52rem;
    height: 0.52rem;
    border-radius: 0.26rem;
    background-size: 100% 100%;
  }
  .headIcons .info-icon label{
    font-size: 0.1rem;
    display: block;
    text-align: center;
    line-height: 24px;
  }
  .team-name{
    border-bottom: 10px solid #f0f0f0;
  }
  .signicon, .signedicon{
    width: 0.8rem;
    height: 0.8rem;
    line-height: 0.8rem;
    border-radius: 0.4rem;
    font-size: 0.2rem;;
    display: block;
    margin: 20px auto;
    color: #ff8c05;
    border:solid 1px #ff8c05;
    text-align: center;
    background-color: #fff;
  }
  .signicon{
    color: #fff;
    border:solid 1px transparent;
    background-color: #ff8c05;
  }
  .msg_sign{margin-top:.5rem;}
  .green{color:#33cc33;}
  .green label{color:#33cc33;}
  .red{color:#c60;}
  .red label{color:#c60;}
  .location{
    margin-top: 20px;
    display: block;
    text-align: center;
    color: #333;
    margin-bottom: 10px;
  }
  .label{
    padding: 0 0.1rem;
    color: #888;
  }
  .singinfo .mint-cell{padding:0 10px;}
  .singinfo .mint-cell .mint-cell-text em.zhiwei{font-style: normal;color:#999;font-size:80%;padding-left:.1rem;}
  .singinfo .mint-cell .mint-cell-text em.time{font-style: normal;color:#999;font-size:80%;padding-left:.1rem;float:right;margin-right: -10px;}
</style>
<template>
  <!--<transition name="slide">-->
  <div class="msgBody">
    <mt-header title="签到详情">
      <mt-button slot="left" icon="back" @click="backPage">返回</mt-button>
    </mt-header>
    <div class="hdBody singinfo">
      <a class="mint-cell">
        <div class="mint-cell-wrapper">
          <div class="mint-cell-title">
            <span class="mint-cell-text">团队名称</span>
          </div>
          <div class="mint-cell-value">
            <span>{{groupName}}</span>
          </div>
        </div>
      </a>
      <div class="help signInfo">
        <a class="mint-cell signed">
          <div class="mint-cell-wrapper green">已签到
            <label>（{{signList.signs.length}}人）</label>
          </div>
        </a>
        <a class="mint-cell" v-for="signItem in signList.signs">
          <div class="mint-cell-wrapper">
            <div class="mint-cell-title">
              <i class="pic" :style="{backgroundImage: 'url('+signItem.url+')'}"></i>
              <span class="mint-cell-text">{{signItem.name|NoData}}<em class="zhiwei" v-if="signItem.sendSign">(发起人)</em><em class="time">{{signItem.createTime | FormartDate}}</em></span>
              <span class="mint-cell-label">位置：{{signItem.address}}</span>
            </div>
          </div>
        </a>
      </div>
      <div class="help signInfo">
        <a class="mint-cell signed">
          <div class="mint-cell-wrapper red">未签到
            <label>（{{signList.noSigns.length}}人）</label>
          </div>
        </a>
        <div class="headIcons">
          <span class="info-icon" v-for="sign in signList.noSigns">
             <i :style="{backgroundImage: 'url('+sign.url+')'}"></i>
             <label>{{sign.name|NoData}}</label>
          </span>
        </div>
      </div>
      <div class="msg_sign" v-cloak>
        <div v-if="!isSign" @click="signClick">
          <mt-button size="small" type="primary" class="signicon" >签到</mt-button>
        </div>
        <mt-button size="small" type="primary" class="signedicon" v-if="isSign">已签</mt-button>
        <span class="location">
          <i class="iconfont icon-shape3"></i>
          {{noticeAddress.locationName}}
        </span>
      </div>
    </div>
  </div>
  <!--</transition>-->
</template>
<script>
  import { Toast } from 'mint-ui';
  import {LoginUser} from '@/common/js/common';
  import { Indicator } from 'mint-ui';
  export default {
    name: 'signInfo',
    data () {
      return {
        isSign:false,
        noticeAddress:{locationName:'',latitude:'',longitude:''},
        groupName:'',
        signList:{
          signs:[],
          noSigns:[]
        }
      }
    },
    activated:function(){
      this.querySignList();
      appMethod = this.getLocation;
      window.location.href = 'myapp://parameter?type=GetCurrentLocation&method=getAddress';
    },
    methods:{
      backPage:function(){
        if(this.$route.query.isMsg){
          this.$router.push('/home');
        }else{
          //this.$router.push('/msgList/teamNotice');
          this.$router.goBack();
        }
      },
      getLocation:function(data){
        this.noticeAddress.locationName = data.data.LocationName;
        this.noticeAddress.latitude = data.data.Latitude;
        this.noticeAddress.longitude = data.data.Longitude;
      },
      querySignList:function(){
        Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        });
        this.signList.noSigns = [];
        this.signList.signs = [];
        let _this = this;
        this.$axios({
          method: 'post',
          url: '/sign/signDetailOfApp',
          data:this.$qs.stringify({
            userId:LoginUser.getUser().userId,
            noticeId:_this.$route.query.noticeId,
            groupId:_this.$route.query.groupId
          })
        }).then(function (response) {
          Indicator.close();
          //console.log(response)
          if(response.data.msg=='success'){
            for(let i=0;i<response.data.data.list.length;i++){
              let signObj = response.data.data.list[i];
              if(i==0){ //第一条数据是签到信息的发起人
                signObj.sendSign = true;
              }
              if(!signObj.url||signObj.url===''){
                signObj.url =  'data:image/png;base64,';
              }
              _this.groupName = response.data.data.groupName;
              if(signObj.type!==true){//未签到
                _this.signList.noSigns.push(signObj);
              }else{//已签到
                _this.signList.signs.push(signObj);
                if(signObj.name == LoginUser.getUser().name){
                  _this.isSign = true;
                }
              }
            }
          }
        }).catch(function(){
          Indicator.close();
          throw new Error('网络异常');
        });
      },
      signClick:function(){//签到
        Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        });
        let _this = this;
        let noticeId = this.$route.query.noticeId;
        let groupId = this.$route.query.groupId;
        this.$axios({
          method: 'post',
          url: '/sign/sign',
          data:this.$qs.stringify({
            userId:LoginUser.getUser().userId,
            noticeId:noticeId,
            groupId:groupId,
            latitude:_this.noticeAddress.latitude,
            longitude:_this.noticeAddress.longitude,
            address:_this.noticeAddress.locationName
          })
        }).then(function (response) {
          Indicator.close();
          if(response.data.msg=='success'){
            let instance = Toast('签到成功');
            _this.isSign = true;
            setTimeout(() => {
              instance.close();
            }, 2000);
            _this.querySignList();
          }
        }).catch(function(){
          Indicator.close();
          throw new Error('网络异常');
        });

      }
    }
  }
</script>




