<style scoped>
  .aboutMe{
    width: 100%;
    height: calc(100vh);
    text-align: left;
    background-color: #fff;
  }
  .aboutMe .box img:first-child{
     display: block;
     margin: 10% auto 0 auto;
     width: 20%;
  }
  .aboutMe .box img:last-child{
    display: block;
    width: 30%;
    position: absolute;
    bottom: 3%;
    left: 50%;
    margin-left: -15%;
  }
  .aboutMe .info{
     text-align: center;
     margin-top: 0.2rem;
  }
  .aboutMe .info span{
     display: block;
     line-height: 0.24rem;
  }
  .aboutMe .info span:first-child{
     font-weight: 500;
     font-size: 0.2rem;
     background-image: -webkit-gradient(linear, 0 0, right 0, from(rgba(22, 164, 228, 0.6)), to(#304fea));
     -webkit-background-clip: text;
     -webkit-text-fill-color: transparent;
  }
  .aboutMe .content{
     width: 80%;
     margin: 10% auto 0 auto;
     text-align: center;
     word-wrap: break-word;
     word-break: break-all;
  }
  .aboutMe .content div{
    margin-top: 10%;
  }
  .aboutMe .content span{
     display: block;
     line-height: 0.24rem;
  }
  .aboutMe .content span:first-child{
    font-size: 0.18rem;
    color: blue;
  }
  .aboutMe .content span:last-child{
    color: #666;
  }
</style>
