      <mt-button slot="left" icon="back" @click="closePage">返回</mt-button>
    </mt-header>
    <div class="box">
        <img src="../../../../static/img/logo2.png">
        <div class="info">
           <span>安易行</span>
           <span>V{{version}}</span>
        </div>
        <div class="content">
          {{content}}
          <div>
             <span @click="serviceAgreement()">《服务协议》</span>
             <span>中交星宇科技有限公司  版权所有</span>
          </div>
        </div>
        <img src="../../../../static/img/about_logo.png">
    </div>
  </div>
    <!--</transition>-->
</template>
<script>
  export default {
    name: 'aboutMe',
    data () {
      return {
         content:'',
         version:''
      }
    },
    created:function(){
      appMethod = this.getAppVersion;
      window.location.href = 'myapp://parameter?type=AppVersion&method=getAppVersion';
    },
//    activated:function(){//路由返回时被调用
//      let thisTemp = this;
//      this.$axios({
//         method: 'post',
//         url: 'tmessage/getAboutUs'
//      }).then(function (response) {
//         thisTemp.content = response.data.data.content;
//      });
//    },
    methods:{
      closePage:function(){
        this.$router.goBack();
      },
      serviceAgreement:function(){
        this.$router.push('/my/serviceAgreement');
      },
      getAppVersion:function(data){
         this.version = data.version;
      }
    }
  }
</script>




