<template>
    <div class="footerTab">
        <div class="tab-item border-top-1" @click="pushPage()">
           <router-link to='/home'>
             <i class="iconfont icon-shouye"></i>
             <span>首页</span>
          </router-link>
        </div>
         <div class="tab-item border-top-1" @click="pushPage()">
           <em v-if="trip!=0"></em>
           <router-link to='/xcheng'>
           <i class="iconfont icon-hangcheng"></i>
           <span>行程管理</span>
           </router-link>
        </div>
        <div class="tab-item border-top-1" @click="pushPage()">
          <em v-if="team!=0"></em>
           <router-link to='/teamManage'>
             <i class="iconfont icon-tuandui2"></i>
            <span>团队管理</span>
          </router-link>
        </div>
        <div class="tab-item border-top-1" @click="pushPage()">
          <router-link to='/my'>
            <i class="iconfont icon-wode"></i>
            <span>我的</span>
          </router-link>
        </div>
    </div>
</template>
<script>
  import { Toast } from 'mint-ui';
  import {LoginUser} from '@/common/js/common';
export default{
  name:'footerTab',
  data () {
    return {
      loginUser:{},
      trip:0,
      team:0
    }
  },
  created:function(){

    this.loginUser = LoginUser.getUser();
    this.getTripNum();
    this.getTeamNum();
  },
  methods:{
    pushPage:function(){
      this.getTripNum();
      this.getTeamNum();
    },
    getTripNum:function(){
      let _this=this;
      this.$axios({
        method: 'post',
        url: '/ttravel/countUserTravel',
        data:this.$qs.stringify({userId:_this.loginUser.userId})
      }).then(function (response) {
        //console.log(response);
        _this.trip=response.data.data;
      });
    },
    getTeamNum:function(){
      let _this=this;
      this.$axios({
        method: 'post',
        url: '/tmessage/countMessages',
        data:this.$qs.stringify({userId:_this.loginUser.userId,ftype:3})
      }).then(function (response) {
        //console.log(response);
        _this.team=response.data.data;
      });
    }
  }
}
</script>

<style scoped>
   .footerTab {
    width: 100% !important;
    overflow: hidden;
    height: .6rem;
    line-height: .6rem;
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: 888;
}
 .footerTab .tab-item {
	  width:25%;
  	height:100%;
  	float:left;
   	background-color: #fafafa;
    text-align: center;
    position: relative;
 }

 .footerTab .tab-item a{
  color: #666;
 }
.footerTab .tab-item  a.active{
   color: #f33;
}
.tab-item em{position: absolute;top:.1rem;right:30%;width:.08rem;height:.08rem;border:1px solid #fff;border-radius:50%;background:linear-gradient(to bottom,#ff641f 0%,#ff1306 100%);display: block;}
.tab-item i{
  display:block;
  font-size:.26rem;
  line-height:normal;
  margin:.07rem auto 0 auto;
  }

 .tab-item span{
   display:block;
    height: .2rem;
    line-height: .2rem;
 }
   .footerTab .tab-item a.router-link-active{color: #2f86ce;}
   .footerTab span{
     font-size: 0.14rem;
   }
</style>



