import {LoginUser} from '@/common/js/common';
let socket={};
let WS_MSG = '';
let WS_TREM = null;
socket.websocket=window.WebSocket || window.MozWebSocket;
socket.wsUrl = '';
socket.isConnect = false;
function doClose(){
  socket.isConnect = false;
  exitWebSocket('断开连接');
}

function doError() {
  socket.isConnect = false;
  exitWebSocket('连接异常');
}

function doMessage(message){
  //alert('doMessage:'+message.data);
  doReciveEvent(message.data);
}

function doSend(message) {
  // alert('doSend');
  if (socket.websocket != null) {
    socket.websocket.send(JSON.stringify(message));
  }
}
function doOpen(){
  socket.isConnect = true;
  doSend(WS_MSG);
}
//初始话 WebSocket
socket.init = function(msg) {
  WS_MSG = msg;
  socket.connect();
  WS_TREM = setInterval(function () {
    if(socket.websocket.onclose){
      appSocket.websocket.close();
    }
    if(!LoginUser.getUser()){
      clearInterval(WS_TREM);
      WS_TREM = null;
      return;
    }
    socket.connect();
  },10000);
};
socket.connect = function () {
  try {
    if (window.WebSocket) {
      if(socket.isConnect){
        socket.websocket.onclose();
      }
      socket.websocket = new WebSocket(encodeURI(socket.wsUrl));
      socket.websocket.onopen = doOpen;
      socket.websocket.onerror = doError;
      socket.websocket.onclose = doClose;
      socket.websocket.onmessage = doMessage;
    }
  }catch(e){
    doError('无法连接WebSocket');
  }
};
socket.close = function() {
  try {
    if (window.WebSocket) {
      if(socket.isConnect){
        socket.websocket.onclose();
      }
    }
  }catch(e){
    doError('无法连接WebSocket');
  }
};
/* tcp连接异常或者关闭 */
function exitWebSocket(msg){
  socket.isConnect = false;
  //console.log(msg);
}
/* 接收到S端发送的消息 */
function doReciveEvent(message){
  /* 特殊处理，异常重启时被运用 如： file:///android_asset/index.html */
  if(window.location.href.indexOf('file:')!=-1) return;

  if(message!=1){
    clearInterval(WS_TREM);
    WS_TREM = null;
    let dom = document.getElementById('shadowId');
    dom.style.opacity = 0;
    dom.style.display = 'block';
    document.getElementById('logOutOkId').onclick = function () {
      socket.websocket.onclose();
      dom.style.display = 'none';
      appRouter.push('/login');
    };
    window.location.href = "myapp://parameter?type=LoginOut";
    LoginUser.clear();//清除用户缓存
    appSocket.websocket.close();
  }
}

export default socket;



