<template>
  <div class="xcsj">
    <div class="no-data-pub" v-if="xcList.length<=0">
      <i class="iconfont  icon-zanwuxiaoxi"></i>
      <p class="text">暂无程随记信息</p>
      <p class="sub-text">点击“发布随记”试试吧</p>
    </div>
    <div class="xcsjContent  xcBody" v-else>
        <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom"  :autoFill="false"  :bottom-all-loaded="allLoaded" ref="loadmore">
            <ul class="container">
                    <li  v-for="(sj,idx) in xcList">
                       <div class="moudle module1">
                         <div class="module-left">
                           <div class="left">
                             <img :src="sj.url|NoImg">
                           </div>
                           <div class="right">
                             <p class="name">{{sj.name}}</p>
                             <p class="do">{{sj.ftitle}}</p>
                           </div>
                         </div>
                         <div class="module-right">{{sj.fcreateAt}}</div>
                       </div>
                       <div class="moudle module2">
                         {{sj.fcontent}}
                       </div>
                       <div class="moudle module3">
                           <div class="xcImages">
                               <div class="item" v-for="(image,idx) in sj.listPhoto" @click="lookImg(idx,sj.listPhoto)"  v-bind:style="{backgroundImage: 'url('+image.thumbnailURL+')'}"></div>
                           </div>
                       </div>
                       <div class="moudle module4">
                         <i class="iconfont icon-shape3"></i>
                         <span class="areaName">{{sj.faddress}}</span>
                         <i @click="shareInfo(sj)" class="iconfont icon-iconziti17 fr"></i>
                         <i v-if="userId==sj.fuserId" @click="delIdx(sj.fid)" class="iconfont icon-shanchu1 fr"></i>
                       </div>
                     </li>

              </ul>
       </mt-loadmore>
    </div>
      <button id="btnn" class="addBtn" @click="fbsj">
          发布<br>随记
      </button>
  </div>
</template>
<script>
  import {LoginUser} from '@/common/js/common';
  import { Toast } from 'mint-ui';
  import { MessageBox } from 'mint-ui';
  import { Indicator } from 'mint-ui';
  export default {
    name: 'xingchengsuiji',
    data () {
      return {
        xcList:[],
          userId:LoginUser.getUser().userId,
        shareData:"",
          pageNum:1,
          pageSize:10,
          allLoaded:false
      }
    },
    activated(){
        this.xcList=[];
        this.pageNum=1;
        this.queryList();
    },
      created:function(){
          this.xcList=[];
          this.pageNum=1;
          this.queryList();
      },
    methods:{
        loadTop:function() {
            this.pageNum = 1;
            this.allLoaded = false;
            this.queryList();
            this.$refs.loadmore.onTopLoaded();
        },
        loadBottom:function(){
            this.pageNum++;
            this.queryList();
        },
        postion:function(){
            if(this.pageNum===1){
                this.$nextTick(function() {
                    var dom = document.getElementsByClassName('mint-loadmore');
                    dom[0].parentNode.scrollTop = 0;
                })
            }
        },
        queryList(){
            Indicator.open({
              text: '加载中...',
              spinnerType: 'fading-circle'
            });
            let _this = this;
            this.$axios({
                method: 'post',
                url: 'ttravel/selectWeibo',
                data: _this.$qs.stringify({
                    userId: LoginUser.getUser().userId,
                    pageNum: _this.pageNum,
                    pageSize: _this.pageSize
                })
            }).then(function (response) {
                Indicator.close();
                if(response.data.msg=='success'){
                    if(!response.data.data.list||response.data.data.list.length<=0){
                        _this.allLoaded = true;
                    }
                    if(_this.pageNum===1){
                        _this.xcList = response.data.data.list;
                    }else{
                        _this.xcList = _this.xcList.concat(response.data.data.list);
                    }
                }
                if(!_this.$refs.loadmore) return;
                _this.$refs.loadmore.onBottomLoaded();
                _this.postion();
            }).catch(function(){
              Indicator.close();
              throw new Error('网络异常');
            });
        },
        lookImg:function(idx,imgArr){
            let ImagesUrlArray=[];
            imgArr.forEach(function(item){
                ImagesUrlArray.push(item.sourcePhotoURL)
            })
            let data = {
                ImagesUrlArray:ImagesUrlArray,
                ImageIndex:idx
            };
            window.location.href = 'myapp://parameter?type=ShowImage&imageData='+JSON.stringify(data);
        },
      fanhui:function() {
        //this.$router.push("/xcheng")
        this.$router.goBack();
      },
      fbsj:function(){
        this.$router.push({name:'fabusuiji'});
      },
      shareInfo(item){
          let shareData=JSON.stringify(
            {
              title:item.ftitle,
              content:item.fcontent,
              imageUrl:item.url,
              shareUrl:this.baseURL+'slb/info.html?param='+LoginUser.getUser().userId+'_'+item.fid
            }
          );
          window.location.href="myapp://parameter?type=OpenShare&shareData="+shareData;
      },
      delIdx(idx){
        let thisTemp=this;
        MessageBox.confirm('确定要删除吗?').then(action => {
            Indicator.open({
              text: '删除中...',
              spinnerType: 'fading-circle'
            });
            thisTemp.$axios({
              method: 'post',
              url: '/ttravel/deleteWeibo',
              data:thisTemp.$qs.stringify({
              userId:LoginUser.getUser().userId,
              fid:idx
            })
          }).then(function(response){
             Indicator.close();
             if(response.data.msg=='success'){
               let instance = Toast('删除成功');
               setTimeout(() => {
                 instance.close();
                 thisTemp.queryList();
               }, 2000);
             }
          }).catch(function(){
            Indicator.close();
            throw new Error('网络异常');
          });
      });

      }

    }
  }
</script>

<style scoped>
  .fr{
    float: right;
  }
  .xcsjContent{
    overflow-y: auto;
    height: calc(100vh - .62rem);
    background: #f0f0f0;
      -webkit-overflow-scrolling: touch;
      overflow-x: hidden;
 }
  .xcsjContent ul{
    display: block;
    background: #f0f0f0;
    padding-top: .1rem;
    padding-bottom: .6rem;
  }

  .xcsjContent li{
    background: #FFFFFF;
    overflow: hidden;
    padding: 0 .15rem;
      margin-bottom: .2rem;
  }
  .xcsjContent li .module1{
    padding-top: .1rem;
    margin-bottom: .1rem;
    overflow: hidden;
  }
  .xcsjContent li .module1 .module-left{
    width: 70%;
    float: left;
  }
  .xcsjContent li .module1 .module-left .left{
    display: inline-block;
    float: left;
     width: .48rem;
    height: .48rem;
    margin-right: .12rem;
     }
  .xcsjContent li .module1 .module-left .left img{
    width: 100%;
    height: 100%;
  }
  .xcsjContent li .module1 .module-left .right{
    float: left;
    text-align: left;
    display: inline-block;
  }
  .xcsjContent li .module1 .module-left .right .name{
    line-height: .28rem;
    font-size: .14rem;
    color: #b5b5b5;

  }
  .xcsjContent li .module1 .module-left .right .do{
    line-height: .14rem;
    font-size: .14rem;
    color: #333333;
  }
  .xcsjContent li .module1 .module-right{
    width: 30%;
    float: left;
    color: #999999;
    font-size: .12rem;
    text-align: right;
    line-height: .3rem;
  }
  .xcsjContent li .module2{
    overflow: hidden;
    line-height: .18rem;
    color: #999999;
    font-size: .12rem;
    text-align: left;
  }

  .xcsjContent li .module2 .all{
      color:blue;
  }
  .xcsjContent li .module3{
    overflow: hidden;
  }
  .marginRight2{
    margin-right: 2%;
  }
  .xcsjContent li .module3 span{
    display:block;
    width: 32%;
    float: left;
    overflow: hidden;
  }
  .xcsjContent li .module3 img{
    width: 100%;
  }
  .icon-shanchu1{
    margin-right: .1rem;
    font-size: .2rem;
  }
  .xcsjContent li .module4{
    text-align: left;
    line-height: .2rem;
    overflow: hidden;
    color: #999999;
    margin-bottom: .1rem;
  }
 .mint-popup{
   display: block;
   width: 100%;
 }
  .xcImages{
    width: 98%;
    margin: 10px auto;
    text-align: left;
    font-size: 0;
  }
  .xcImages .item{
    height: 70px;
    width: 33.333333%;
    display: inline-block;
      border: 0.03rem solid #ffffff;
      box-sizing: border-box;
    background-size: cover;
    background-position: center center;
  }
  .thImg{overflow:hidden;}
  .thImg img{float:left;margin:.1rem;width:1rem;height:.68rem;}
  .xcBody{
      height: calc(100vh - 1rem);
      overflow-y: auto;
      -webkit-overflow-scrolling: touch;
      overflow-x: hidden;
  }
  .container{
      display: block;
      background: red;
      min-height: calc(100vh - 1rem);
  }

  .addBtn{
      width: 0.7rem;
      height: 0.7rem;
      border-radius: .1rem;
      position: fixed;
      z-index: 1000;
      right: .2rem;
      bottom: .9rem;
      border: none;
      outline: none;
      color: #ffffff;
      font-size: .16rem;
      opacity: 0.7;
      background:-webkit-gradient(linear, 0% 0%, 100% 100%, from(#ffb621), to(#ff821b))
  }
  .icon-tianjia{
      color: #ffffff;
      font-size: .2rem;
  }
</style>




