<template>
   <div class="apparatusBox">
     <mt-header title="设备信息">
         <mt-button slot="left" icon="back" @click="backPage">返回</mt-button>
     </mt-header>
     <div class="apparatusLogo"><i class="iconfont icon-diannaoshouji"></i></div>
     <div class="apparatusCont">
       <mt-cell title="Mac地址">{{codeInfo.Mac}}</mt-cell>
       <mt-cell title="Wifi名称">{{codeInfo.WifiName}}</mt-cell>
       <mt-cell title="Wifi密钥">{{codeInfo.WifiPwd}}</mt-cell>
     </div>
     <div class="apparatusIcon">
       <input type="button" value="使用此设备网络并绑定" class="apparatusIcon1" @click="bingTwo()">
       <input type="button" value="仅使用此设备的wifi网络" class="apparatusIcon2" @click="linkWifi">
     </div>
   </div>
</template>
<script>
  import {LoginUser} from '@/common/js/common';
  import { Toast } from 'mint-ui';
  export default {
    name: 'apparatus',
    data () {
      return {
         codeInfo:{}
      }
    },
    created:function(){
       this.codeInfo=this.$route.query;
    },
    methods:{
      backPage:function(){
         if(this.$route.query.isHome){
            this.$router.push('/home');
         }else{
            this.$router.push('/my');
         }
      },
      bingTwo:function(){
          let _this = this;
         let data = JSON.parse(localStorage.getItem('twocData'));
         this.$axios({
           method: 'post',
           url: 'binding/bindingTwocPhone',
           data:this.$qs.stringify({
             userId:LoginUser.getUser().userId,
             twocPhone:data.data.SSID,
             wifiPwd:data.data.WifiPwd,
             wifiName:data.data.WifiName,
             mac:data.data.Mac
           })
         }).then(function (response) {
             if(response.data.code > 0){
               let instance = Toast('绑定成功');
               setTimeout(() => {
                 instance.close();
               }, 500);
               let userInfo = LoginUser.getUser();
               userInfo.isBang = 1;
               LoginUser.setUser(userInfo);
               _this.$router.push('/my/device');
             }else{
                 if(response.data.msg !== ''){
                   let instance = Toast(response.data.msg);
                   setTimeout(() => {
                     instance.close();
                   }, 2000);
                 }else if(response.data.msg === ''){
                   let instance = Toast("绑定失败");
                   setTimeout(() => {
                     instance.close();
                   }, 2000);
                 }
             }

         });
       },
      linkWifi:function(){
        if(this.codeInfo&&this.codeInfo.mac){
          window.location.href = 'myapp://parameter?type=ConnectionWiFi&wifiData='+JSON.stringify(this.twocData)
        }else{
          let instance = Toast('设备未绑定，无法连接WiFi');
          setTimeout(() => {
            instance.close();
          }, 2000);
        }
      }
    }
  }
</script>

<style scoped>
.apparatusBox{background:url('./apparatusBox.png') no-repeat center top;background-size:100% 1.42rem;position:relative;}
.mint-header{
  background: none;
}
  .apparatusLogo{border:1px solid #1081f3;background:#fff;width:1.15rem;height:1.15rem;line-height:1.3rem;border-radius:50%;position: absolute;left:50%;top:1.42rem;margin:-0.71rem auto auto -0.57rem;overflow:hidden;}
  .apparatusLogo i{font-size:.9rem;color:#2a81ee;}
  .apparatusCont{padding-top:1.6rem;}
  .apparatusIcon{margin-top:1rem;}
  .apparatusIcon1{width:80%;margin:.1rem auto;height:.42rem;line-height:.42rem;font-size:.18rem;border-radius:.21rem;color:#fff;background:linear-gradient(126deg,#0f5bf6 0%,#16a3e4 100%);box-shadow:0 0 9px #16a3e4;border:none; }
  .apparatusIcon2{width:80%;margin:.1rem auto;height:.42rem;line-height:.42rem;font-size:.18rem;border-radius:.21rem;color:#249ee8;background:#fff;border:1px solid #dfd6d6;}
</style>
<style>
.apparatusBox  .mint-cell-text{float:left;}
</style>




