<style>
  .tdxc  .mint-cell-title {
    text-align: left
  }

  .tdxc  .mint-cell-value.is-link {
    margin-right: 0;
  }

  .tdxc  .mint-field-core {
    text-align: right;
    background: none;
  }

  input[type='date']{
    background: #ffffff;
  }
</style>
<template>
  <!--<transition name="slide">-->
      <div class="tdxc xc-mod">
        <mt-header title="行程修改">
          <mt-button icon="back" slot="left" @click="backPage()">返回</mt-button>
          <mt-button slot="right" @click="finshXc()">
            保存
          </mt-button>
        </mt-header>

        <div class="tdxcContent">
          <div class="add-xc-box">
          <mt-field label="行程名称" class="tdxc-input" placeholder="填写" v-model="getDataInfo.fname"></mt-field>
          <mt-field v-if="tuandui" class="tdxc-input" label="出行人数" placeholder="填写" type="number" v-model="getDataInfo.fpersionNum"></mt-field>
          <div class="mint-cell" @click="selectCity(0)"><span class="mint-cell-mask"></span>
            <div class="mint-cell-left"></div>
            <div class="mint-cell-wrapper">
              <div class="mint-cell-title"><!---->
                <div><i class="indexicon icon-indicator"></i> <span>出发城市</span></div>
              </div>
              <div class="mint-cell-value is-link"><span>{{getDataInfo.fcityFrom}}</span></div>
            </div>
            <div class="mint-cell-right"></div>
            <i  class="mint-cell-allow-right"></i>
          </div>
          <div class="mint-cell" @click="selectCity(1)"><span class="mint-cell-mask"></span>
            <div class="mint-cell-left"></div>
            <div class="mint-cell-wrapper">
              <div class="mint-cell-title"><!---->
                <div><i class="indexicon icon-indicator"></i> <span>终点城市</span></div>
              </div>
              <div class="mint-cell-value is-link"><span>{{getDataInfo.fcityTo}}</span></div>
            </div>
            <div class="mint-cell-right"></div>
            <i  class="mint-cell-allow-right"></i>
          </div>
          <div class="date cfDate">
            <span>出发日期</span><input  v-model="cfDate" :disabled="cfDate<=todayDate?'disabled':false"   :min="todayDate" type="date">
            <div class="dateBox">{{cfDate}}</div>
          </div>
          <div class="date fhDate">
            <span>返程日期</span><input  v-model="zdDate"  :min="cfDate" type="date">
            <div class="dateBox">{{zdDate}}</div>
          </div>
          <div class="xcap-module">
            <div class="xingchenganpaiTitle">行程安排</div>
            <div class="jiluxc">
              <div class="jiluxc-title">
                每日行程安排<span class="detailText"> 点击编辑详情</span>
              </div>
              <div class="jiluxc-content">
                <ul class="jiluxc-contentC">
                  <li @click="xcDetailFn(idx)" v-for="(itemDay,idx) in days"   :class="{disabledStyle:(getDataInfo.fshared!=0&&itemDay.times<=todayTimes)?'disabledStyle':false,DaysActive:(getDataInfo.listTravelSub[idx]&&getDataInfo.listTravelSub[idx].fcityTo!='')?'DaysActive':false}">
                    <div class="djt">
                      第{{idx+1}}天
                    </div>
                    <div class="riqi">
                      {{itemDay.day}} - {{itemDay.week|fileterweek}}
                    </div>
                  </li>
                </ul>
              </div>
            </div>
            <div class="scxc">
              <div class="scxc-title">上传行程表单照片</div>
              <div class="scxc-content">
                <div class="xcImages">
                  <div class="item" @click="lookImg(idx)" v-for="(image,idx) in imageList.minImages" v-bind:style="{backgroundImage: 'url('+image+')'}"><i @click="deleImage($event,idx)" class="shanchu iconfont icon-shanchu"></i></div>
                  <div class="item onSheet"v-if="imageList.minImages.length<9?true:false" @click="openSheet"></div>
                </div>
              </div>
            </div>
            <!--<div class="scxc-btn">-->
              <!--<mt-button @click.native="selectPicture()" type="primary" class="btnStyle btnStyle1">行程单上传</mt-button>-->
              <!--<mt-button @click.native="selectCamera()" type="primary" class="btnStyle btnStyle2">行程单拍照</mt-button>-->
            <!--</div>-->
          </div>
        </div>
        <!--分行程添加-->
        <mt-actionsheet :actions="[
           {name:'拍照',method:() => selectCamera()},
           {name:'从相册中选择',method:() => selectPicture()}
         ]" v-model="sheetVisible">
        </mt-actionsheet>
          </div>
      </div>
   <!--</transition>-->
</template>
<script>
  import { Toast } from 'mint-ui';
  import { MessageBox } from 'mint-ui';
  import {LoginUser} from '@/common/js/common';
  export default {
    name: 'xingchengtianjia',
    data () {
      return {
        sheetVisible: false,
        cityType:"",
        days:[],
        cfDate:"",
        zdDate:"",
        todayDate:"",
        DaysActive:'',
        disabledStyle:'',
        todayTimes:"",
        tuandui:false,
        geren:false,
        disTime:false,
        userId:LoginUser.getUser().userId,
        geturl:"",
        updateurl:"",
        dayIndex:0,
        fid:"",
        isDisabled:false,
        imageList:{
          minImages:[],
          maxImages:[],
          photoNames:[]
        },
        oldDayData:{
          startDate:"",
          dayData:[]
        },
        getDataInfo:{
          fname:'',
          fpersionNum:'',
          fcityFrom:'',
          fcityTo:'',
          fstartAt:'',
          fbackAt:'',
          listTravelSub:[],
          listPhoto:[]
        }
      }
    },
    activated:function(){
      this.isDisabled=false;
      this.dateValite();
      let name=this.$route.params.name;
      let dayMod = localStorage.getItem('dayMod');
      localStorage.removeItem('dayMod');
      let xcCityInfo = JSON.parse(localStorage.getItem('xcCityInfo'));
      if(xcCityInfo&&xcCityInfo.name=='xiugaiCity'){
        this.dealWithCity();
        return;
       }
      if(name=="tuandui"){
        this.clearAll();
        this.fid=this.$route.params.fid;
        this.geturl="ttravel/selectTtravel";
        this.updateurl="ttravel/updateTtravel";
        this.tuandui=true;
        this.geren=false;
      }else if(name=="geren"){
        this.clearAll();
        this.fid=this.$route.params.fid;
        this.geturl="ttravel/selectTtravelGe";
        this.updateurl="ttravel/updateTtravelGe";
        this.tuandui=false;
        this.geren=true;
      }else if(dayMod&&dayMod.name=='dayxiugaiBack'){
        return;
      }else if(dayMod&&dayMod.name=='dayxiugai'){
        this.getDataInfo.listTravelSub[this.dayIndex]= dayMod.dayAddObj;
        return;
      }
      this.queryInfo();

    },
    methods: {
      dateValite:function(){
        let _this=this;
        let newDate=new Date();
        let month=(newDate.getMonth() + 1)>=10?(newDate.getMonth() + 1):("0"+(newDate.getMonth() + 1));
        let date=newDate.getDate()>=10?newDate.getDate():("0"+newDate.getDate());
        setTimeout(function(){
          _this.todayDate=newDate.getFullYear() + "-" +month + "-" + date;
          _this.todayTimes=new Date(newDate.getFullYear(),newDate.getMonth(),newDate.getDate()).getTime();
        },500)
      },
      toastFn:function(text){
        let instance = Toast(text);
        setTimeout(function () {
          instance.close();
        }, 2000);
      },
      queryInfo: function () {
        var _this = this;
        this.$axios({
          method: 'post',
          url: this.geturl,
          data:this.$qs.stringify({
            userId:this.userId,
            fid:this.fid
          })
        }).then(function (res) {
          if(res.data.msg=='success'||res.data.data){
              _this.getDataInfo = res.data.data;
            //console.log(_this.getDataInfo );
            if(_this.getDataInfo.listPhoto.length){
              _this.getDataInfo.listPhoto.forEach(function(item){
                _this.imageList.minImages.push(item.thumbnailURL);
                _this.imageList.maxImages.push(item.sourcePhotoURL);
                _this.imageList.photoNames.push(item.photoName);
              });
            }

            _this.cfDate=_this.getDataInfo.fstartAt;
            _this.zdDate=_this.getDataInfo.fbackAt;
            _this.initDate(1);

          }
        })
      },
      xcDetailFn:function(index){
        if(((this.days[index].times)<=(this.todayTimes))&&this.getDataInfo.fshared!=0){
          let instance = Toast('今日及今日之前子行程不能修改');
          setTimeout(() => {
            instance.close();
          }, 2000);
          return;
        }
        let _this=this;
        this.dayIndex=index;
        let dayNum=index+1;
        this.$router.push({name:'dayDetailxiugai',params:{name:'xiugai',dayObj:_this.getDataInfo.listTravelSub[index],dayNum:dayNum}});
      },
      backPage: function () {
        MessageBox.confirm('确定放弃当前行程修改?').then(action => {
          //this.$router.push({name:'xcInfo',params:{name:"xingchengxiugaiback"}});
          //this.$router.push({name:'xcInfo',params:{name:"xingchengxiugaiback"}});
          let routerInfo = {name:"xingchengxiugaiback"};
          localStorage.setItem('routerInfo',JSON.stringify(routerInfo));
          this.$router.goBack();
        });
      },
      selectCity:function(type){
        this.$router.push({path:'/xcheng/citySelect',query:{name:"XGcity",cityType:type}});
      },
      dealWithCity:function(){//处理起始城市
        let xcCityInfo = JSON.parse(localStorage.getItem('xcCityInfo'));
        localStorage.removeItem('xcCityInfo');
        if(!xcCityInfo) return;
        if(xcCityInfo.cityType==0){//出发城市
          let citys = JSON.parse(localStorage.getItem('citys'));
          let tt=this;
          if(!citys||citys.length<=0){return}
          if((citys[citys.length-1].cityId)&&(citys[citys.length-1].cityId!="")){
            tt.getDataInfo.fcityFrom = citys[citys.length-1].name;
          }else if((citys[citys.length-1].cityId=="")&&(citys[citys.length-2].cityId=="")){
            tt.getDataInfo.fcityFrom = citys[citys.length-3].name;
          }else if((citys[citys.length-1].cityId=="")){
            tt.getDataInfo.fcityFrom = citys[citys.length-2].name;
          }
        }else if(xcCityInfo.cityType==1){//终点城市
          let citys = JSON.parse(localStorage.getItem('citys'));
          if(!citys.length){return}
          if((citys[citys.length-1].cityId)&&(citys[citys.length-1].cityId!="")){
            this.getDataInfo.fcityFrom = citys[citys.length-1].name;
          }else if((citys[citys.length-1].cityId=="")&&(citys[citys.length-2].cityId=="")){
            this.getDataInfo.fcityFrom = citys[citys.length-3].name;
          }else if((citys[citys.length-1].cityId=="")){
            this.getDataInfo.fcityFrom = citys[citys.length-2].name;
          }
        }
      },
      verificationFn:function(){
        var xcInfo=this.getDataInfo;
        if(xcInfo.fname==""){
          this.toastFn("请填写行程名称");
          return false;
        }
        if((this.tuandui==true)&&xcInfo.fpersionNum==""){
          this.toastFn("请填写行程人数");
          return false;
        }
        if(xcInfo.fcityFrom==""){
          this.toastFn("请选择出发城市");
          return false;
        }
        if(xcInfo.fcityTo==""){
          this.toastFn("请选择终点城市");
          return false;
        }
        if(xcInfo.fstartAt==""){
          this.toastFn("请选择出发时间");
          return false;
        }
        if(xcInfo.fbackAt==""){
          this.toastFn("请选择返程时间");
          return false;
        }
        let isEvery=this.checkEveryDayFn();
      },
      checkEveryDayFn:function(){
        let isWc;
        this.getDataInfo.listTravelSub.forEach(function(item){
          if(item.fcityTo==""||item.fdesc==""){
            isWc=false;
            return;
          }
        });
        return isWc;
      },
      finshXc:function(){
        let isValidity=this.verificationFn();
        if(isValidity==false){return}
        if(this.isDisabled==true){return};
        this.isDisabled=true;
        let _this=this;
        let upDataInfo={
                  userId:LoginUser.getUser().userId,
                  fid:_this.fid,
                  fname:_this.getDataInfo.fname,
                  fpersionNum:_this.getDataInfo.fpersionNum,
                  fcityFrom:_this.getDataInfo.fcityFrom,
                  fcityTo:_this.getDataInfo.fcityTo,
                  fstartAt:_this.getDataInfo.fstartAt,
                  fbackAt:_this.getDataInfo.fbackAt,
                  upTtravelSub:_this.getDataInfo.listTravelSub,
                  photoNames:_this.imageList.photoNames
        };
        this.$axios({
          method: 'post',
          url: _this.updateurl,
          data:upDataInfo
        }).then(function (response) {
          if(response.data.msg=='success'){
            let instance = Toast('操作成功');
            setTimeout(() => {
              instance.close();
            let routerInfo = {name:"xingchengxiugaibaocun",fid:_this.fid};
            localStorage.setItem("routerInfo",JSON.stringify(routerInfo));
            _this.$router.push({name:'xcInfo'});
              //_this.$router.push({name:'xcInfo',params:{name:"xingchengxiugaibaocun",fid:_this.fid}});
          }, 2000);
          }else{
            let instance = Toast('操作失败');
            _this.isDisabled=false;
            setTimeout(() => {
              instance.close();
            return;
          }, 2000);
          }

        });
      },
      initDate: function (num) {
        if(num==1){       // 如果是页面初始化则移除旧的缓存
          localStorage.removeItem("localDayObj");
        }else{
          var getLocalDayObj=JSON.parse(localStorage.getItem("localDayObj"));  //如果是日程发生变化则获取缓存
        }

        var localDayObj={};   //每日行程集合缓存
        var newDayObj={};    //新生成的行程

        var _this = this;
        let stime = new Date(_this.getDataInfo.fbackAt).getTime() - new Date(_this.getDataInfo.fstartAt).getTime();
        let dayNums = stime / (24 * 60 * 60 * 1000);
        for (var i = 0; i <= dayNums; i++) {
            var itemDate = new Date(_this.getDataInfo.fstartAt);
            itemDate.setDate(itemDate.getDate(_this.getDataInfo.fstartAt) + i);
          let newTimes=new Date(itemDate.getFullYear(),itemDate.getMonth(),itemDate.getDate());
            var objDate = {
              day: (itemDate.getMonth() + 1) + "/" + itemDate.getDate(),
              week: itemDate.getDay(),
              times:newTimes
            };
            _this.days[i]=objDate;

            var newDate=itemDate.getFullYear()+'-'+(itemDate.getMonth()+1)+"-"+itemDate.getDate();
            if(num==1){    //页面初始化的时候
              var newObj={
                fcityTo: _this.getDataInfo.listTravelSub[i].fcityTo,
                fassemblyAt:_this.getDataInfo.listTravelSub[i].fassemblyAt,
                fhotel:_this.getDataInfo.listTravelSub[i].fhotel,
                fvehicle:_this.getDataInfo.listTravelSub[i].fvehicle,
                fdesc:_this.getDataInfo.listTravelSub[i].fdesc,
                fcreateAt:newDate
              };
              if(newObj.fcityTo!=""){
                localDayObj[newDate]=newObj;
              };
            }else{    //当行程日期发生变化的时候
              var newObj={
                fcityTo:"",
                fassemblyAt:"",
                fhotel:"",
                fvehicle:"",
                fdesc:"",
                fcreateAt:newDate
              }
              newDayObj[newDate]=newObj;
            }
        }
       if(num==1){   //如果是页面初始化的时候做新的缓存
         localStorage.setItem('localDayObj',JSON.stringify(localDayObj));
       }else{  //如果新生成的日程中的日程安排在缓存中存在，则用缓存中的日程替换新生成中的日程
         var newObjArr=[];
        for(var attr in newDayObj){
          if(getLocalDayObj[attr]){
            newDayObj[attr]=getLocalDayObj[attr];
          }
          newObjArr.push(newDayObj[attr])
        };
        _this.getDataInfo.listTravelSub=newObjArr;
       }
      },
      openSheet: function () {
        this.sheetVisible = true;
      },
      selectPicture:function(){
        //appData = this.helpInfo.images;
        appMethod = this.insertImg;   // 把insertImg方法指向到全局对象上
        this.sheetVisible = false;
        let limitNum=9-this.imageList.minImages.length;
        window.location.href = "myapp://parameter?type=TakeAlbum&method=fileImg&imageNum="+limitNum;   //调动app接口
      },
      selectCamera:function(){
        appMethod = this.insertImg; // 把insertImg方法指向到全局对象上
        this.sheetVisible = false;
        let limitNum=9-this.imageList.minImages.length;
        window.location.href = "myapp://parameter?type=TakePhoto&method=fileImg&imageNum="+limitNum;
      },
      lookImg:function(idx){
        let data = {
          ImagesUrlArray:this.imageList.maxImages,
          ImageIndex:idx
        };
        window.location.href = 'myapp://parameter?type=ShowImage&imageData='+JSON.stringify(data);
      },
      insertImg:function(data){     //相册，拍照调用成功后在fileImg方法里调用的回调函数，data为fileImg方法接受的参数
        for(let i=0;i<data.data.length;i++){
          this.imageList.photoNames.push(data.data[i].photoName);
          this.imageList.minImages.push(data.data[i].thumbnailURL);
          this.imageList.maxImages.push(data.data[i].sourcePhotoURL);
        }
        appMethod = null;
      },
      clearAll:function(){         //清空页面所有
        this.imageList.maxImages=[];
        this.imageList.minImages=[];
        this.imageList.photoNames=[];
        this.getDataInfo.fname="";
        this.getDataInfo.fpersionNum="";
        this.getDataInfo.fcityFrom="";
        this.getDataInfo.fcityTo="";
        this.getDataInfo.fstartAt="";
        this.getDataInfo.fbackAt="";
        this.getDataInfo.listTravelSub=[];
        this.getDataInfo.listPhoto=[];

      },
      deleImage:function(e,index){   //删除图片
        e.stopPropagation();
        MessageBox.confirm('确定删除吗?').then(action => {
        this.imageList.minImages.splice(index,1);
        this.imageList.maxImages.splice(index,1);
        this.imageList.photoNames.splice(index,1);
      });


      }
    },
    watch: {
      cfDate:function(value,oldval) {
        if(oldval==""){return};
        this.getDataInfo.fstartAt = value;
        this.days = []
        this.getDataInfo.listTravelSub=[]
        this.initDate(2);
      },
      zdDate:function(value,oldval) {
        if(oldval==""){return};
        this.getDataInfo.fbackAt = value;
        this.days = []
        this.getDataInfo.listTravelSub=[]
        this.initDate(3);
      }
    },
    filters: {
      fileterweek: function (value) {
        var newweek;
        switch (value) {
          case 1:
            newweek= "周一";
            break;
          case 2:
            newweek=  "周二";
            break;
          case 3:
            newweek=  "周三";
            break;
          case 4:
            newweek=  "周四";
            break;
          case 5:
            newweek=  "周五";
            break;
          case 6:
            newweek= "周六";
            break;
          case 0:
            newweek= "周日";
            break;

        }
        return newweek;
      }
    }
  }
</script>
