<style scoped>
  .myBody{
    width: 100%;
    height: calc(100vh - 48px);
    text-align: left;
    background-color: #f0f0f0;
  }
  .myBody .box:first-child{
    height: 35vh;
    /*border-bottom: solid 0.12rem #f0f0f0;*/
  }
  .myBody .box:first-child .item:first-child{
    height: 90%;
    background-image: url("../../../static/img/myHeader_bg.jpg");
    background-size: cover;
    position: relative;
    /*box-shadow: 0 4px 10px rgba(0,0,0,0.2);*/
    /*box-shadow: 0 0 4px 10px red;*/
  }
  .myBody .box:first-child .item:last-child{
    height: 10%;
    background:-webkit-gradient(linear, 0 0,0 95%, from(#cad9ec), to(#fff));
    position: relative;
  }
  .myBody .box .item .head{
    position: absolute;
    border-radius: 0.4rem;
    width: 0.8rem;
    height: 0.8rem;
    top: 22%;
    left: 50%;
    margin-left: -0.4rem;
    background-color: #fff;
    z-index: 2;
  }
  .myBody .box .item .head img{
    display: block;
    width: 90%;
    height: 90%;
    border-radius: calc(90%);
    margin: 5% auto 0 auto;
  }
  .detail{
    width:90%;
    position: absolute;
    top: 70%;
    left: 50%;
    margin-left: -45%;
    color: #fff;
  }
  .detail .left, .detail .rigth{
    width: 20%;
    height: 100%;
  }
  .detail .left{
    float: left;
  }
  .detail .rigth{
    float: right;
  }
  .detail .center{
    float: left;
    width: 100%;
    margin: 0 auto;
    height: 100%;
    text-align: center;
  }
  .detail .left .iconfont{
    font-size: 0.28rem;
    line-height: 0.52rem;
  }
  .detail .rigth .iconfont{
    font-size: 0.30rem !important;
    line-height: 0.48rem;
  }
  .detail .center span{
    display: block;
    line-height: 0.24rem;
    font-size: 0.2rem;
  }
  .detail .center span span{
    font-size: 0.8em;
    opacity: 0.8;
  }
  .icon-xinxi,.icon-dian{
    position: absolute;
    font-size: 0.3rem;
    top: 0.3rem;
    right: 0.2rem;
    color: #fff;
  }
  .icon-dian{
    position: absolute;
    font-size: 0.3rem;
    top: 0;
    right: 0;
    color: #ff3333;
  }
  .info{
    width: 90%;
    position: absolute;
    top: 42%;
    left: 50%;
    margin-left: -45%;
    text-align: center;
    color: #0f8ad0;
    line-height: 0.2rem;
    font-size: 0.16rem;
  }
  .info .left, .info .rigth{
    width: 30%;
    height: 100%;
  }
  .info .left{
    float: left;
  }
  .info .rigth{
    float: right;
  }
  .info .center{
    float: left;
    width: 40%;
    height: 100%;
  }
  .info div span{
    display: block;
  }
  .info div span span{
    color: #999;
  }
  .mint-cell-value span{
    font-size: 0.14rem;
  }
  .mint-cell-title .iconfont{
    margin-right: 4px;
    font-size: 0.2rem !important;
    vertical-align: middle;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  .icon-xinxi1{
    color: #1c7c7c;
    background-image: -webkit-gradient(linear, 0 0, 0 bottom, from(rgba(93, 197, 119, 0.8)), to(#1c7c7c));
  }
  .icon-shebei{
    color: #d8bf09;
    background-image: -webkit-gradient(linear, 0 0, 0 bottom, from(rgba(225, 197, 21, 0.8)), to(#d8bf09));
  }
  .icon-xiugai-copy{
    color: #44a879;
    background-image: -webkit-gradient(linear, 0 0, 0 bottom, from(rgba(74, 175, 120, 0.8)), to(#1866b0));
  }
  .icon-dingwei{
    color: #f55e53;
    background-image: -webkit-gradient(linear, 0 0, 0 bottom, from(rgba(240, 109, 86, 0.8)), to(#f55e53));
  }
  .icon-shezhi1{
    color: #1b6fb6;
    background-image: -webkit-gradient(linear, 0 0, 0 bottom, from(rgba(50, 187, 225, 0.8)), to(#1b6fb6));
  }
  .mint-cell{
    position: relative;
  }
  .mint-cell-value span span{
    display: inline-block;
    position: absolute;
    top: 18px;
    right: 24px;
  }
  .wh100{
    width:100% !important;height:100% !important;
  }
  .my .icon-msg{
    position: absolute;
    top: -4px;
    right: -10px;
  }
</style>
<style>
  .my .list:last-child .mint-cell,.my .list:first-child .mint-cell-wrapper,.my .xingcheng .mint-cell{
    background-image: none !important;
  }
  /*.my .list .mint-cell{*/
  /*background-image: linear-gradient(180deg, #d9d9d9, #d9d9d9 50%, transparent 50%);*/
  /*background-size: 120% 1px;*/
  /*}*/
  .my .mint-cell-title{
    padding: 14px 0 !important;
  }
  .my .primaryBtn .mint-button{
    width: 100%;
    border-radius: 0.25rem;
  }
  .my .setting{
    border-top: solid 10px #f0f0f0;
  }
</style>
<template>
  <div class="myBody my">
    <div class="box">
      <div class="item">
        <div class="detail">
          <div class="center">
            <span>{{userInfo.name}}<span>{{userInfo.companyName}}</span></span>
          </div>
        </div>
           <span class="head">
               <img :src="userInfo.url|NoImg">
            </span>
      </div>
      <div class="item"></div>
    </div>
    <div class="box">
      <div class="list"  @click="personInfo()">
        <mt-cell title="修改个人信息" is-link>
          <i slot="icon"  class="iconfont icon-xiugai-copy"></i>
        </mt-cell>
      </div>
      <!--<div class="list"  @click="equipment()">-->
        <!--<mt-cell title="绑定设备管理" is-link>-->
          <!--<span>绑定/解绑通信设备</span>-->
          <!--<i slot="icon"  class="iconfont icon-shebei"></i>-->
        <!--</mt-cell>-->
      <!--</div>-->
      <div class="list xingcheng" @click="myTrip()">
        <mt-cell title="我的行程记事" is-link>
          <i slot="icon"  class="iconfont icon-dingwei"></i>
        </mt-cell>
      </div>
      <div class="list setting" @click="setting()">
        <mt-cell title="设置" is-link>
          <i slot="icon"  class="iconfont icon-shezhi1"></i>
        </mt-cell>
      </div>
    </div>
    <!--<mt-popup class="wh100" v-model="popup.a1" position="right">-->
    <!--<PersonInfo ref="PersonInfo" v-on:close="changePop" :userInfo="userInfo"></PersonInfo>-->
    <!--</mt-popup>-->
  </div>
</template>
<script>
  import {LoginUser} from '@/common/js/common';
  export default {
    name: 'my',
    data () {
      return {
        userInfo:{companyName:'',url:''},
        type:3,
        msgCount:0,
        popup:{a1:false,b1:false}
      }
    },
    activated:function(){//路由返回时被调用
      this.userInfo = LoginUser.getUser();
      this.queryUserInfo();
      //this.queryCount();废除此方法
    },
    methods:{
      personInfo: function () {
        this.$router.push('/my/personInfo');
      },
      equipment:function(){
        this.$router.push('/my/device');
      },
      setting:function(){
        this.$router.push('/my/setting');
      },
      queryUserInfo:function(){
        let _this = this;
        this.$axios({
          method: 'post',
          url: 'register/getUserInfo',
          data:this.$qs.stringify({userId:_this.userInfo.userId})
        }).then(function (response) {
          _this.userInfo = response.data.data;
          localStorage.setItem('userInfo',JSON.stringify(_this.userInfo));
        });
      },
      myTrip:function(){
        this.$router.push('/my/myTrip');
      },
      queryCount:function(){
        let _this = this;
        this.$axios({
          method: 'post',
          url: 'groupMessage/messageOfLogin?t='+Math.random(),
          data:this.$qs.stringify({
            userId:_this.userInfo.userId,
            type:'all'
          })
        }).then(function (response) {
          _this.msgCount = response.data.data;
          if(_this.msgCount>=99) {
            _this.msgCount =_this.msgCount+'+';
          }
        });
      }
    }
  }
</script>




