<style scoped>
  .myBody{
     width: 100%;
     height: 100vh;
     text-align: left;
  }
  .box{
    border-top: 0.12rem solid #f0f0f0;
  }
  .mint-cell-value span{
     font-size: 0.9em;
  }
  .mint-cell-value input{
     border: solid 1px transparent;
     width: 70vw;
     padding: 0.04rem 0;
     border-radius: 3px;
     outline: none;
  }
  .list{
     position: relative;
  }
  .list span{
    color: #fff;
    display: block;
    position: absolute;
    top: 0.11rem;
    right: 0.14rem;
    height: 0.2rem;
    border-radius: 0.15rem;
    line-height: 0.2rem;
    padding: 0.02rem 0.1rem;
    background:-webkit-gradient(linear, 0 0, right 0, from(rgba(255,183,10,0.8)), to(#ff6a01));
  }
  .getTime {
    background: #ccc !important;
  }
  @media only screen and (-webkit-min-device-pixel-ratio: 3), only screen and (min-device-pixel-ratio: 3) {
    .list span{
      top: 0.09rem;
    }
  }
  @media only screen and (-webkit-min-device-pixel-ratio: 3.5), only screen and (min-device-pixel-ratio: 3.5) {
    .list span{
      top: 0.11rem;
    }
  }
</style>
<template>
  <!--<transition name="slide">-->
   <div class="myBody">
      <mt-header title="修改绑定手机号">
        <mt-button slot="left" icon="back" @click="closePage">返回</mt-button>
        <mt-button slot="right" @click="saveInfo">完成</mt-button>
      </mt-header>
      <div class="box">
        <div class="list">
          <mt-cell title="新手机号">
            <input v-model="phoneNumber" placeholder="请输入新绑定手机号">
          </mt-cell>
        </div>
        <div class="list">
          <mt-cell title="验证码">
            <span @click="getVerificationCode" v-show="getVCode">获取验证码</span>
            <span class="getTime" v-show="!getVCode">重新获取({{getTime}}s)</span>
            <input v-model="inputCode" placeholder="请输入验证码">
          </mt-cell>
        </div>
      </div>
   </div>
    <!--</transition>-->
</template>
<script>
  import { Toast } from 'mint-ui';
  export default {
    name: 'modPhone',
    data () {
      return {
        getVCode:true,
        getTime:60,
        phoneNumber:'',
        smsCode:'',
        inputCode:''
      }
    },
    activated:function() {
      this.phoneNumber = '';
      this.smsCode = '';
      this.inputCode = '';
    },
    methods:{
      phoneNumValidate:function (num) {
        let tel = /^1(3|4|5|7|8)\d{9}$/;
        return tel.test(num);
      },
      getVerificationCode:function () {
        let _this = this;
        if (!_this.getVCode){ return }
        let phoneNumber = _this.phoneNumber;

        if (!this.phoneNumValidate(phoneNumber)){
          let instance = Toast("请输入正确的手机号码");
          setTimeout(() => {
            instance.close();
          }, 1000);
          return false;
        }
        this.$axios({
          method: 'post',
          url: 'register/smsSend',
          data:{phoneNumber:phoneNumber}
        }).then(function (response) {
          if(response.data.msg=='success'){
            _this.smsCode = response.data.data;
            let instance = Toast("短信已发送,请注意查收");
            _this.getVCode = false;
            let countDown = setInterval(() => {
              _this.getTime--;
              if(_this.getTime<1){
                clearInterval(countDown);
                _this.getVCode = true;
                _this.getTime = 60;
              }
            },1000);
            setTimeout(() => {
              instance.close();
            }, 1000);
          }else{
            let instance = Toast(response.data.msg);
            setTimeout(() => {
              instance.close();
            }, 1000);
          }
        });
      },
      closePage:function(){
        //this.$router.push('/personInfo?phoneNum='+this.phoneNumber);
        localStorage.setItem('phoneNum',this.phoneNumber);
        this.$router.goBack();

      },
      saveInfo:function(){
        if (!this.phoneNumValidate(this.phoneNumber)){
          let instance = Toast("请输入正确的手机号码");
          setTimeout(() => {
            instance.close();
          }, 1000);
          return false;
        }
        if (!this.inputCode){
          let instance = Toast("请输入验证码");
          setTimeout(() => {
            instance.close();
          }, 1000);
          return false;
        }
        if (this.smsCode != this.inputCode){
          let instance = Toast("验证码不正确");
          setTimeout(() => {
            instance.close();
          }, 1000);
          return false;
        }
        this.closePage();
      }
    }
  }
</script>




