<style>
  .tdxc .mint-cell-title {
    text-align: left
  }

  .tdxc .mint-field-core {
    text-align: right
  }
  .tdxc textarea.mint-field-core{
    text-align: left;
  }
  .xcck .mint-cell-value span {
    margin-right: 0 !important;
  }
</style>
<template>
  <!--<transition name="slide">-->
    <div class="tdxc xcck">
      <mt-header :title="title">
        <mt-button icon="back" slot="left" @click="backPage">返回</mt-button>
      </mt-header>
      <div class="tdxcContent">
        <div class="mint-cell"  ><span class="mint-cell-mask"></span>
          <div class="mint-cell-left"></div>
          <div class="mint-cell-wrapper">
            <div class="mint-cell-title"><!---->
              <div><i class="indexicon icon-indicator"></i> <span>终点城市</span></div>
            </div>
            <div class="mint-cell-value is-link"><span>{{dayInfo.fcityTo}}</span></div>
          </div>
          <div class="mint-cell-right"></div>
        </div>
        <div class="mint-cell"  ><span class="mint-cell-mask"></span>
          <div class="mint-cell-left"></div>
          <div class="mint-cell-wrapper">
            <div class="mint-cell-title"><!---->
              <div><i class="indexicon icon-indicator"></i> <span>交通方式</span></div>
            </div>
            <div class="mint-cell-value is-link"><span>{{dayInfo.fvehicle}}</span></div>
          </div>
          <div class="mint-cell-right"></div>
        </div>
        <mt-field label="入驻酒店" disabled="disabled"  placeholder="暂无" v-model="dayInfo.fhotel"></mt-field>
        <mt-field label="集合地点" disabled="disabled"  placeholder="暂无"  v-model="dayInfo.fassemblyAt"></mt-field>
        <div class="xcms">行程描述</div>
        <div class="xcms-content">
          <mt-field disabled="disabled"  placeholder="暂无" type="textarea" rows="8"   v-model="dayInfo.fdesc"></mt-field>
        </div>
      </div>
    </div>
  <!--</transition>-->
</template>
<script>
  export default {
    name: 'xcDetail',
    data () {
      return {
        dayInfo:{
          fcityTo:"",
          fvehicle:"",
          fhotel:"",
          fassemblyAt:"",
          fdesc:""
        },
        title:""
      }
    },
    activated(){

      let _this=this;
      let Index=parseInt(_this.$route.params.index)+1;
      this.title="D"+Index+"行程";
      //this.clearAll();    //页面首次进来之后先清除所有数据
      if(!this.$route.params.dayInfo){
        return;
      }
      this.dayInfo=this.$route.params.dayInfo;
    },
    methods: {
      backPage: function () {
        //this.$router.push({name:"xcInfo",params:{name:"dayDetailchakan"}});
        let routerInfo = {name:"dayDetailchakan"};
        localStorage.setItem('routerInfo',JSON.stringify(routerInfo));
        this.$router.goBack();
      },
      clearAll:function(){
        this.dayInfo.fcityTo="";
        this.dayInfo.fvehicle="";
        this.dayInfo.fhotel="";
        this.dayInfo.fassemblyAt="";
        this.dayInfo.fdesc="";
      }
    }
  }
</script>
<style>
  .xcms-content .mint-cell-value{
    width: 100%;
    max-width: 100%;
  }
  .xcms-content .mint-field-state.is-default{
    display: none;
  }
  .xcms-content  .mint-field-core{
    text-align: left;
  }
</style>
<style scoped>

  .tdxcContent {
    overflow-y: auto;
    height: calc(100vh - .62rem);
    background: #f0f0f0;
    -webkit-overflow-scrolling: touch;
    overflow-x: hidden;
  }
  .tdxcContent   .mint-cell-mask:active::after{
    opacity: 0;
  }
  .xcms{
    line-height: .45rem;
    font-size: .14rem;
    color: #666666;
    text-align: left;
    padding-left: .1rem;
  }
</style>




