<style scoped>
  .warningBox{text-align: left;height:100%;background:#fff;overflow:hidden;}
  .headBox{height:60px;}
  .warningDet{padding:0 .18rem;}
  .warningDetTit{color:#333;font-size:.24rem;line-height:.34rem;padding-top:.2rem;}
  .warningDetTit span{color:#0e82e8;}
  .warningDetUntit{font-size:.15rem;color:#999;margin-top:.1rem;}
  .warningDetCont{padding:.16rem 0 .2rem 0;font-size:.15rem;line-height:.26rem;}
</style>
<style>

</style>
<template>
  <!--<transition name="slide">-->
  <div class="warningBox">
    <mt-header title="预警信息">
      <mt-button  slot="left" icon="back" @click="backHome">返回</mt-button>
    </mt-header>
    <div class="warningDet hdBody">
      <div class="warningDetTit"><span v-if="warnInfo.category !== ''">{{warnInfo.category}} | </span>{{warnInfo.title}}</div>
      <div class="warningDetUntit">{{warnInfo.createDate}}</div>
      <div class="warningDetCont">
        <p v-html="warnInfo.fcontent"></p>
      </div>
    </div>
  </div>
  <!--</transition>-->
</template>
<script>
  import {LoginUser} from '@/common/js/common';
  import { Indicator } from 'mint-ui';
  export default {
    name: 'warningList',
    data () {
      return {
        warnInfo:{},
        userId:0
      }
    },
    activated:function(){
      this.userId = LoginUser.getUser().userId;
      this.queryCont();
    },
    methods:{
      queryCont:function () {
        Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        });
        let _this = this;
        let warningInfoId = this.$route.query.warningInfoId;
        this.$axios({
          method: 'post',
          url: '/warningInfo/selectWarningInfo',
          data:this.$qs.stringify({
            userId:this.userId,
            warningInfoId:warningInfoId
          })
        }).then(function (response) {
          Indicator.close();
          if(response.data.msg=='success'){
            _this.warnInfo = response.data.data.list[0];
          }
        }).catch(function(){
          Indicator.close();
          throw new Error('网络异常');
        });
      },
      backHome:function(){
        this.$router.goBack();
      }
    }
  }
</script>




