<template>
    <!--<transition name="slide">-->
        <div class="msgBody">
          <mt-header title="行程提醒">
              <mt-button icon="back" slot="left" @click="backPage">返回</mt-button>
          </mt-header>
          <div class="no-data-pub" v-if="todayList.length<=0&&tommorowList.length<=0">
            <i class="iconfont  icon-zanwuxiaoxi"></i>
            <p class="text">近期暂无行程提醒信息</p>
          </div>
          <div class="notices" v-else>
            <div class="container">
              <div v-if="todayseen" class="title">
                <span class="today">今日</span>
              </div>
              <div  class="box"  v-if="tx.fcityTo!==''"   @click="clickList(tx.fid)" v-for="(tx,idx) in todayList">
                <em v-if="tx.freaded==0"></em>
                <div class="msgTitle border-bottom">
                  {{tx.fname}}
                  <span v-if="sign" class="signStyle" :class="styleArr[tx.ftype]"><i class="iconfont"  :class="iconstyleArr[tx.ftype]"></i>{{tx.ftype==0?"团队行程":"个人行程"}}</span>
                </div>
                <div class="item">
                  <span><i class="iconfont icon-lvxinghover"></i>{{tx.fcityTo}}</span>
                  <span><i class="iconfont icon-bourn2"></i>{{tx.fassemblyAt}}</span>
                </div>
                <div class="item">
                  <span><i class="iconfont"  :class="tx.fvehicle | jtFilter"></i>{{tx.fvehicle}}</span>
                  <span><i class="iconfont icon-jiudian"></i>{{tx.fhotel}}</span>
                </div>
                <div class="item border-top">
                  <div class="timeBox">
                    <p>{{tx.fdesc}}</p>
                  </div>
                  <div class="photoBox">
                    <div class="xcImages">
                      <div class="mitem" @click="lookImg($event,tx.photoList,idx)" v-for="(imageObj,idx) in tx.photoList" v-bind:style="{backgroundImage: 'url('+imageObj.thumbnailURL+')'}"></div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="container">
              <div v-if="tomorrowseen" class="title tomorrow">
                <span>明日</span>
              </div>
              <div  class="box" v-if="tx.fcityTo!==''"  @click="clickList(tx.fid)" v-for="(tx,idx) in tommorowList">
                <em v-if="tx.freaded==0"></em>
                <div class="msgTitle border-bottom">
                  {{tx.fname}}
                  <span v-if="sign" class="signStyle" :class="styleArr[tx.ftype]"><i class="iconfont"  :class="iconstyleArr[tx.ftype]"></i>{{tx.ftype==0?"团队行程":"个人行程"}}</span>
                </div>
                <div class="item">
                  <span><i class="iconfont icon-lvxinghover"></i>{{tx.fcityTo}}</span>
                  <span><i class="iconfont icon-bourn2"></i>{{tx.fassemblyAt}}</span>
                </div>
                <div class="item">
                  <span><i class="iconfont"  :class="tx.fvehicle | jtFilter"></i>{{tx.fvehicle}}</span>
                  <span><i class="iconfont icon-jiudian"></i>{{tx.fhotel}}</span>
                </div>
                <div class="item border-top">
                  <div class="timeBox">
                    <p>{{tx.fdesc}}</p>
                  </div>
                  <div class="photoBox">
                    <div class="xcImages">
                      <div class="mitem" @click="lookImg($event,tx.photoList,idx)" v-for="(imageObj,idx) in tx.photoList" v-bind:style="{backgroundImage: 'url('+imageObj.thumbnailURL+')'}"></div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
       </div>
   <!--</transition>-->
</template>
<script>
  import {LoginUser} from '@/common/js/common';
  import { Indicator } from 'mint-ui';
  let pageNum= 1,pageSize=20;
  export default {
    name: 'xingchengtixing',
    data () {
      return {
        todayList:[],
        tommorowList:[],
        styleArr:["tuandui","geren"],
        iconstyleArr:["icon-tuandui3","icon-geren"],
        sign:true,
        style:"",
        tomorrowseen:false,
        todayseen:false,
        imageList:{
          minImages:[],
          maxImages:[]
        }
      }
    },
    activated(){
      this.todayList=[];
      this.tommorowList=[];
      this.imageList.minImages=[];
      this.imageList.maxImages=[];
      this.queryList();
    },
    methods: {
      backPage:function(){
        this.$router.goBack();
      },
      queryList(){
        Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        });
        let _this = this;
        this.$axios({
          method: 'post',
          url: '/ttravel/selectSub',
          data:this.$qs.stringify({
            userId:LoginUser.getUser().userId,
            pageNum:pageNum,
            pageSize:pageSize
          })
        }).then(function(response){
          Indicator.close();
          if(response.data.msg=='success'){
            var todayDate=new Date().getFullYear()+"-"+(new Date().getMonth()+1)+"-"+new Date().getDate();
            response.data.data.list.forEach(function(item){
              if(item.fcreateAt==todayDate){
                _this.todayList.push(item);
                if(_this.todayList.length>0){
                  _this.todayseen=true
                }else{
                  _this.todayseen=false
                }
              }else{
                _this.tommorowList.push(item);
                if(_this.tommorowList.length>0){
                  _this.tomorrowseen=true
                }else{
                  _this.tomorrowseen=false
                }
              }
            })
          }
        }).catch(function(){
          Indicator.close();
          throw new Error('网络异常');
        });
      },
      lookImg:function(e,photoList,idx){
        e.stopPropagation();

        let maxImages=[];
        photoList.forEach(function(item){
          maxImages.push(item.sourcePhotoURL)
        });
        let data = {
          ImagesUrlArray:maxImages,
          ImageIndex:idx
        };
        window.location.href = 'myapp://parameter?type=ShowImage&imageData='+JSON.stringify(data);
      },
      clickList(idx){
        this.$router.push({path:"/home/xchengtixingDetail",query:{fid:idx}})
      }
    },
    filters:{
      jtFilter(val){
        var jt=""
        switch(val){
          case "火车":
          jt="icon-huoche";
          break;
          case "汽车":
            jt="icon-daba";
            break;
          case "飞机":
            jt="icon-feiji";
            break;
          case "轮船":
            jt="icon-lunchuan";
            break;
        }
        return jt
      }
    }
  }
</script>

<style scoped>
.mt{
  width: 100%;
}
.msgBody{
  height: calc(100vh);
}
  .notices{
    width: 100%;
    height: calc(100vh - 0.6rem);
    text-align: left;
    overflow-y: scroll;
    background-color: #f0f0f0;
    -webkit-overflow-scrolling: touch;
    overflow-x: hidden;
  }
  .title{
    height: .45rem;
    background: #ffffff;
  }
.tomorrow{
  margin-top: .15rem;

}

  .title span{
    display: inline-block;
    float: left;
    margin-top: .1rem;
    margin-left: .18rem;
    width: .45rem;
    height: .21rem;
    border-radius: .1rem;
    color: #ffffff;
    font-size: .16rem;
    text-align: center;
    line-height: .21rem;
    background: #f56153;
  }
.tomorrow span{
  background: #f5ba53;
}
  .notices .box{
    padding: 0 0.15rem;
    margin-top: 0.16rem;
    background-color: #fff;
    position: relative;
  }
  .msgTitle{
    font-size: 1.2em;
    padding-bottom: 10px;
    line-height: .4rem;
    overflow: hidden;
    white-space:nowrap;
    text-overflow:ellipsis;
    overflow:hidden;
    height: .4rem;
    position: relative;
    padding-right: 1.05rem;
  }
.signStyle{
  width: .94rem;
  height: .25rem;
  border-radius: .125rem;
  float: right;
  color: #ffffff;
  font-size: .16rem;
  text-align: center;
  line-height: .25rem;
  margin-top: .1rem;
  padding: 0 .1rem;
  position: absolute;
  right: 0;
  top: -.04rem;

}
  .box .item{
    width: 100%;
    line-height: .3rem;
    padding-top: .1rem;
  }
  .border-top{
    border-top: 1px  solid #d9d9d9;
  }
  .border-bottom{
    border-bottom: 1px  solid #d9d9d9;
  }
  .msgBody .box .item:last-child{
    margin-top: 15px;
  }
  .box .item span{
    display: inline-block;
    vertical-align: middle;
  }
  .box .item span:first-child{
    width: 36%;
    white-space:nowrap;
    text-overflow:ellipsis;
    overflow:hidden;
  }
  .icon-lvxinghover{ color: #8a68d7;}
  .icon-bourn2{color: #33aa59;}
  .icon-tuandui3{color: #f6be61;}
  .tuandui{
    background: #2a64e9;
  }
.geren{
  background: #ff6a01;
}
  .icon-daba{
    color: #edc06e;
  }
  .icon-jiudian{
    color: #7f5ecb;
  }
  .icon-shijian{
    color: #c8c8c8;
  }
  .icon-rili{color: #f66755;}
  .iconfont{
    margin-right: 5px;
  }
.xcImages{
  width: 98%;
  margin: 10px auto;
  text-align: left;
  font-size: 0;
  line-height: 0;
}
.xcImages .mitem{
  height: 70px;
  width: 33.333333%;
  display: inline-block;
  border: 0.03rem solid #ffffff;
  box-sizing: border-box;
  background-size: cover;
  background-position: center center;
}




  .photoBox {
    overflow: hidden;
  }

.box em {
  position: absolute;
  top: 4px;
  left: 4px;
  width: 8px;
  height: 8px;
  border: 1px solid #fff;
  border-radius: 50%;
  background: linear-gradient(to bottom,#ff641f 0%,#ff1306 100%);
  display: block;
}

</style>




