<style scoped>
.tmteamBox{text-align: left;background: #fff;height:100%;}

  .listFbt{color:#999;font-size:12px;}

  .addTeamNews{
    width: 0.7rem;
    height: 0.7rem;
    border-radius: .1rem;
    position: fixed;
    z-index: 1000;
    right: .2rem;
    bottom: .9rem;
    border: none;
    outline: none;
    color: #ffffff;
    font-size: .16rem;
    opacity: 0.7;
    background:-webkit-gradient(linear, 0% 0%, 100% 100%, from(#ffb621), to(#ff821b))
  }
  .pr{position: relative;}

</style>
<style>
.tmteamBox  .mint-loadmore{padding-bottom:50px;}
</style>
<template>
  <transition name="slide">
    <div>
      <div class="no-data-pub" v-if="teamList.length<=0">
        <i class="iconfont  icon-zanwuxiaoxi"></i>
        <p class="text">暂无团队信息</p>
        <p class="sub-text">点击“创建团队”试试吧</p>
      </div>
    <button id="addTeamNews" class="addTeamNews" v-on:click="addTeam">创建<br>团队</button>
    <div class="tmteamBox" v-if="!teamList.length<=0">
      <mt-loadmore :top-method="loadTop" ref="loadmore">
      <div class="teamList">
        <template v-for="(n,index) in teamList">
          <router-link :to="{path: '/teamManage/teamView', query: { groupId:n.id,groupName:n.name,noticeId:n.noticeId }}">
            <mt-cell :title="(index+1) +'、'+ n.name +'('+ n.count  +'人)'" is-link class="pr">
              <span class="red-point" v-if="n.readed !== true"></span>
              <span class="listFbt">创建于{{n.createTime | FormartDate}}</span>
            </mt-cell>
          </router-link>
        </template>
      </div>
      </mt-loadmore>
    </div>
    </div>
  </transition>
</template>
<script>
  import {LoginUser} from '@/common/js/common';
  import { Toast } from 'mint-ui';
  import { MessageBox } from 'mint-ui';
  import { Loadmore } from 'mint-ui';
  import { Indicator } from 'mint-ui';
  export default {
    name: 'teamManage',
    data () {
      return {
        loginUser:{},
        teamList:{}
      }
    },
    created:function(){
      tmSetTab(1);
      this.loginUser=LoginUser.getUser();
      this.queryList(0,100);
    },
    activated:function () {
      tmSetTab(1);
      this.loginUser=LoginUser.getUser();
      this.queryList(0,100);
    },
    methods:{
      loadTop:function() {
        this.queryList(0,100);
        this.$refs.loadmore.onTopLoaded();
      },
      queryList:function (pageNum,pageSize) {
        Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        });
        let it = this;
        //团队列表
        this.$axios({
          method: 'post',
          url: 'group/getGroupInfoList',
          data:this.$qs.stringify({
            userId:it.loginUser.userId,
            pageNum:pageNum,
            pageSize:pageSize
          })
        }).then(function (response) {
          Indicator.close();
          it.teamList = response.data.data.list;
        }).catch(function(){
          Indicator.close();
          throw new Error('网络异常');
        });
      },
      addTeam: function () {
        appMethod = this.finishTeam;
        window.location.href = 'myapp://parameter?type=CreateTeam&method=createTeam';
      },
      finishTeam:function(){
        this.queryList(0,100);
        let instance = Toast('团队创建成功');
        setTimeout(() => {
          instance.close();
        }, 2000);
      }
    },

    components:{Loadmore}
  }
</script>




