<style scoped>
  .helpBody{
    width: 100%;
    height: calc(100vh - 60px);
    text-align: left;
  }
  .helpBody .box.topbox{
     height: 30vh;
     width: 100%;
     background-image: url("./help_bg.jpg");
     background-size: cover;
     border-bottom: solid 0.1rem #f0f0f0;
    position: relative;
  }
  .icon {
    background-image: url("./help_menu.png");
    display: inline-block;
    position: absolute;
    top: 16px;
    left: 10px;
    width: 30px;
    height: 30px;
    border-radius: 15px;
  }
  .pic_1 {background-position: -0px -0px; }
  .pic_2 {background-position: -32px -0px; }
  .pic_3 {background-position: -64px -0px; }
  .address{
     text-align: center;
    color: #fff;
    position: absolute;
    width: 66%;
    top:80px;
    margin-left: 17%;
    opacity: 0.8;
  }
  .address span{
     display: block;
  }
  .address span:first-child{
     line-height: 0.3rem;
     font-size: 0.8em;
  }
  .address span:last-child{
    line-height: 0.18rem;
  }
  .icon-location{
     display: inline-block;
     color: #fbcb3f;
     margin-right: 4px;
  }

</style>
<style>
  .mint-cell:first-child .mint-cell-wrapper{
    background-image: none !important;
  }
  .help .mint-cell-title{
    padding: 14px 0 14px 36px !important;
  }
</style>
<template>
  <!--<transition name="slide">-->
  <div class="helpBody">

    <div class="box topbox">
      <mt-header title="求助服务">
          <mt-button icon="back" slot="left" @click="backPage">返回</mt-button>
      </mt-header>
      <div class="address">
         <span>你当前所在</span>
         <span><i class="iconfont icon-location"></i>
           {{address.LocationName}}
         </span>
      </div>
    </div>
    <div class="box help">
      <div class="box">
        <mt-cell title="救助通信录" label="提供当地救助机构电话黄页查询" is-link to="/home/commonHelp">
          <i slot="icon"  class="icon pic_2"></i>
        </mt-cell>
      </div>
      <div class="box" @click="callPhone()">
        <mt-cell title="领保中心求助" label="一键拨打领保中心救助电话" is-link>
          <i slot="icon"  class="icon pic_3"></i>
        </mt-cell>
      </div>
    </div>
  </div>
  <!--</transition>-->
</template>
<script>
  export default {
    name: 'help',
    data () {
      return {
        address:{LocationName:'定位中...'}
      }
    },
    created:function(){
      appMethod = this.getLocation;
      window.location.href = 'myapp://parameter?type=GetCurrentLocation&method=getAddress';
    },
    methods:{
      backPage:function(){
        this.$router.goBack();
      },
      callPhone:function(){
         window.location.href = 'myapp://parameter?type=PhoneCall&Phone=+861012308';
      },
      getLocation:function(data){
        this.address = data.data;
      }
    }
  }
</script>




