global.appData=null;
global.appMethod=null;
global.longTouchMethodT=null;
global.longTouchMethodG=null;
global.longTouchMethodLT=null;
global.longTouchMethodLG=null;
global.tmSetTab=null;
global.appRouter = null;
global.numMethod = null;
global.wordTimeMethod = null;
global.timeInterval = null;
global.scnaQRcodeMethod = null;
let LoginUser={};
LoginUser.setUser = function(user){
  localStorage.setItem('loginUserInfo',JSON.stringify(user));
};
LoginUser.getUser = function(){
  var user = JSON.parse(localStorage.getItem('loginUserInfo'));
  if(!user) return null;
  if(!user.url||user.url==='') {
    user.url = './static/img/head.png';
  }
  return user;
};
LoginUser.clear = function(){
  localStorage.removeItem('loginUserInfo');
  localStorage.removeItem('loginUser');
};
global.msgType=function (data){
  //alert(JSON.stringify(data));
  data = global.formatData(data);
  if(!JSON.parse(localStorage.getItem('loginUserInfo'))){
    appRouter.push('/login');
    return;
  }
  let url = window.location.href;
  //0:团队通知；1：位置共享；2：团队求助；3：团队邀请；4：形成分享；5：预警提示；
  // 6：系统通知；7：签到；8：系统消息通知；10：行程列表  11:系统通知
  if(data.type==0 || data.type==11 || data.type==8){//团队通知,系统通知
    if(url.indexOf('msg_')!=-1){
      appRouter.push('/home/teamNoticeInfo?isMsg=true&id='+data.messageId+'&ftoGroupId='+data.ftoGroupId+'&type='+data.type);
    }else{
      appRouter.push('/home/msg_teamNoticeInfo?isMsg=true&id='+data.messageId+'&ftoGroupId='+data.ftoGroupId+'&type='+data.type);
    }
  }else if(data.type==1){//位置共享
    window.location.href = 'myapp://parameter?type=SharedLocation';
  }else if(data.type==2 || data.type==6){//团队救助,系统回复
    if(url.indexOf('msg_')!=-1){
      appRouter.push('/home/teamHelpInfo?isMsg=true&id='+data.messageId);
    }else{
      appRouter.push('/home/msg_teamHelpInfo?isMsg=true&id='+data.messageId);
    }
  }else if(data.type==3){//团队邀请
    if(url.indexOf('msg_')!=-1){
      appRouter.push('/teamManage/teamView?isMsg=true&noticeId='+data.messageId+'&groupId='+data.groupId+'&groupName='+data.groupName);
    }else{
      appRouter.push('/teamManage/msg_teamView?isMsg=true&noticeId='+data.messageId+'&groupId='+data.groupId+'&groupName='+data.groupName);
    }
  }else if(data.type==4){//行程分享
    let routerInfo = {name:"tuanduiList",isMsg:true,fid:data.messageId};
    localStorage.setItem("routerInfo",JSON.stringify(routerInfo));
    appRouter.push({name:'xcInfo'});
    //appRouter.push({name: "xcInfo", params:{isMsg:true,name:"tuanduiList",fid:data.messageId}});
  }else if(data.type==5){//预警信息
    if(url.indexOf('msg_')!=-1){
      appRouter.push('/home/warningDet?isMsg=true&warningInfoId='+data.messageId);
    }else{
      appRouter.push('/home/msg_warningDet?isMsg=true&warningInfoId='+data.messageId);
    }
  }else if(data.type==7){//签到
    if(url.indexOf('msg_')!=-1){
      appRouter.push('/home/teamNoticeSignInfo?isMsg=true&noticeId='+data.messageId+'&groupId='+data.groupId);
    }else{
      appRouter.push('/home/msg_teamNoticeSignInfo?isMsg=true&noticeId='+data.messageId+'&groupId='+data.groupId);
    }
  }else if(data.type==10){//行程列表
    appRouter.push('/xcheng');
  }
};
global.formatData=function (data){
  if(typeof data=='string'){
    data = JSON.parse(data);
  }
  return data;
};
global.updateHeadIcon=function (data){
  data = global.formatData(data);
  if(global.appMethod){
    global.appMethod(data);
  }
};
global.callbackQRcode=function (data){//手机回调二维码信息
  data = formatData(data);
  if(global.scnaQRcodeMethod){
    global.scnaQRcodeMethod(data);
  }
};
global.getAddress=function (data){//获取手机当前位置
  data = global.formatData(data);
  if(global.appMethod){
    global.appMethod(data);
  }
};
global.getWordAddress=function (data){//获取手机当前位置
  data = global.formatData(data);
  if(global.wordTimeMethod){
    global.wordTimeMethod(data);
  }
};
global.lookImags=function (idx,data){//调用手机图片浏览器
  let imageData = JSON.stringify({ImagesUrlArray:data, ImageIndex:idx});
  window.location.href = 'myapp://parameter?type=ShowImage&imageData='+imageData;
};

global.fileImg=function (data){
  data = global.formatData(data);
  if(global.appMethod){
    global.appMethod(data);
  }
};
global.createTeam=function (){/*创建团队*/
  if(global.appMethod){
    global.appMethod();
  }
};
global.appCallBack=function (){
  if(global.numMethod){
    global.numMethod();
  }
};
global.rootDom=function (e,classStr){
  let flag = true,dom= e.target;
  while(flag){
    if(dom.getAttribute('class')===classStr){
      flag = false;
      return dom;
    }else{
      dom = dom.parentNode;
    }
  }
};
global.getWeather=function (data){
  data = global.formatData(data);
  if(global.appMethod){
    global.appMethod(data);
  }
};
global.callbackAddPeople=function (data){//团队查看中的添加联系人
  data = global.formatData(data);
  if(global.appMethod){
    global.appMethod(data);
  }
};
global.addContact=function (){/* 添加手机联系人 */
  if(global.appMethod){
    global.appMethod();
  }
};
global.getAppVersion=function (data){/* 获取系统版本号 */
  data = global.formatData(data);
  if(global.appMethod){
    global.appMethod(data);
  }
};

export {LoginUser}



