<style scoped>
  .myBody{
     width: 100%;
     height: 100vh;
     text-align: left;
     margin-top: -1px;
  }
  .box .list:nth-child(2),.box .list:nth-child(6){
    border-top: 0.12rem solid #f0f0f0;
    margin-top: -2px;
    position: relative;
  }
  .mint-cell-value span{
     font-size: 0.9em;
  }
  .list img{
     width: 0.6rem;
     height: 0.6rem;
     border-radius: 0.3rem;
     border: none;
  }
  .mint-cell-value input{
    border: solid 1px transparent;
    width: 70vw;
    padding: 0.04rem 0;
    border-radius: 3px;
    outline: none;
    text-align: right;
    padding-right: 10px;
    color: #333;
    font-size: 0.9em;
    background-color: #fff;
  }
  .list .mint-cell-value img{
     margin: 10px 0;
  }
</style>
<style>
  .list img + span{
    margin-left: 10px;
  }
</style>
<template>
  <!--<transition name="slide">-->
   <div class="myBody personInfo">
      <mt-header title="修改个人信息">
        <mt-button slot="left" icon="back" @click="closePage">返回</mt-button>
        <mt-button slot="right" @click="saveInfo">保存</mt-button>
      </mt-header>
      <div class="box">
        <div class="list" @click="modHeadIcon()">
          <mt-cell title="我的头像" is-link>
            <img :src="userInfo.url|NoImg" width="60" height="60">
          </mt-cell>
        </div>
        <div class="list">
          <mt-cell title="账号">
            <input v-model="userInfo.username" disabled>
          </mt-cell>
        </div>
        <div class="list">
          <mt-cell title="真实姓名">
            <input v-model="userInfo.name">
          </mt-cell>
        </div>
        <div class="list">
          <mt-cell title="公司名称">
            <input v-model="userInfo.companyName" disabled>
          </mt-cell>
        </div>
        <div class="list" @click="modPhoneNo()">
          <mt-cell title="绑定手机" is-link><span id="jjjjId" style="margin-right:0;">{{userInfo.phoneNumber}}</span></mt-cell>
        </div>
        <div class="list" @click="modPwd()">
          <mt-cell title="修改登录密码" is-link></mt-cell>
        </div>
      </div>
   </div>
    <!--</transition>-->
</template>
<script>
  import {LoginUser} from '@/common/js/common';
  import { Toast } from 'mint-ui';
  import { Indicator } from 'mint-ui';
  export default {
    name: 'personInfo',
    data () {
      return {
        loginUser:{},
        isReset : false,
        userInfo:{}
      }
    },
    activated:function(){
      let phoneNum = localStorage.setItem('phoneNum',this.phoneNumber);
      localStorage.removeItem('phoneNum');
      let isUpdate = localStorage.getItem('isUpdate');
      if(phoneNum){
        this.userInfo.phoneNumber = phoneNum;
      }else if(isUpdate){
        this.queryUserInfo();
      }else{
        this.userInfo = JSON.parse(localStorage.getItem('userInfo'));
      }
    },
    methods:{
      closePage:function(){
        this.$router.goBack();
      },
      queryUserInfo:function(){
        let _this = this;
        this.$axios({
          method: 'post',
          url: 'register/getUserInfo',
          data:this.$qs.stringify({userId:LoginUser.getUser().userId})
        }).then(function (response) {
          _this.userInfo = response.data.data;
          localStorage.setItem('userInfo',JSON.stringify(_this.userInfo));
        });
      },
      saveInfo:function(){
        Indicator.open({
          text: '提交中...',
          spinnerType: 'fading-circle'
        });
        let _this = this;
        this.$axios({
          method: 'post',
          url: 'register/updateUserInfo',
          data:this.$qs.stringify({
            userId:LoginUser.getUser().userId,
            name:this.userInfo.name,
            phoneNumber:this.userInfo.phoneNumber
          })
        }).then(function (res) {
           Indicator.close();
           if(res.data.code==1){
              let instance = Toast(res.data.msg);
              setTimeout(() => {
               instance.close();
               _this.closePage();
              }, 1000);
             let userData = JSON.stringify({
               userID:LoginUser.getUser().userId,
               userName:_this.userInfo.name,
               phoneNo:_this.userInfo.phoneNumber,
               headImageUrl:_this.userInfo.url
             });
             window.location.href = 'myapp://parameter?type= UpdateUserInfo&userData='+userData;
           }
        }).catch(function(){
          Indicator.close();
          throw new Error('网络异常');
        });
      },
      modHeadIcon:function(){//修改头像
        this.$router.push('/my/modHeadIcon');
      },
      modPwd:function(){//绑定密码
        this.$router.push('/my/modPassword');
      },
      modPhoneNo:function(){//绑定手机
        this.$router.push('/my/modPhoneNo');
      }
    }
  }
</script>




