<style scoped>
  .teamBox{text-align: left;background: #f0f0f0;height:100%;overflow-y:auto;}
  .headBox{height:60px;}
  .teamAddBox{}
  .teamAddTit{color:#666;font-size:14px;padding:10px 10px 0 10px;line-height:26px;}
  .teamAddTit span{color:#999;font-size:12px;}
  .mr10{margin-right:.1rem;}
  .teamListFbt{font-size:12px;color:#999;}
  .wh100{
    width:100% !important;height:100% !important;
  }
  .teamAddButton{
    padding-top:.5rem;background:#fff;text-align: center;padding-bottom:20px;
  }
  .teamAddButton input{height:.42rem;width:80%;border-radius:.21rem;border:none;font-size:.18rem;color:#fff;box-shadow:0 5px 15px rgba(62,142,215,.43);background:linear-gradient(126deg,#2a64e9,#16a3e4);}
</style>
<style>
  .teamAddName .mint-cell-value .mint-field-core{text-align: right;}

</style>
<template>
  <transition name="slide">
  <div class="teamBox">
    <div class="headBox">
      <mt-header fixed title="创建团队">
        <mt-button  slot="left" icon="back" @click="backTeam">返回</mt-button>
      </mt-header>
    </div>

    <div class="teamAddBox">
      <div class="teamAddName">
        <mt-field label="团队名称" placeholder="请填写" v-model="groupName"></mt-field>
      </div>
      <div class="teamAddTit">团队成员<span>（5人）</span></div>
      <div class="teamAddList">
        <mt-cell title="添加团队成员" is-link @click.native="addPeople"></mt-cell>
        <mt-cell :title=loginUser.username>
          <img slot="icon" class="mr10" v-bind:src="loginUser.url" width="24" height="24">
          <span class="teamListFbt">发起人</span>
        </mt-cell>
        <mt-cell-swipe v-for="item in tAddData"
          :title=item.name
          :right="[
          {
            content: '删除',
            style: { background: '#e5433a', color: '#fff' },
            handler: (index) => messagebox('删除')
          }
        ]">
          <img slot="icon" class="mr10" v-bind:src="item.url" width="24" height="24">
        </mt-cell-swipe>
      </div>
      <div class="teamAddButton"><input type="button" value="立即创建团队" @click="submitTeam"></div>
      <div>

      </div>
    </div>

    <mt-popup class="wh100" v-model="popleftAdd" position="right">
      <peopleAdd ref="peopleAdd" v-on:backTeamAdd="addPeople"></peopleAdd>
    </mt-popup>

  </div>
  </transition>
</template>
<script>
  import peopleAdd from './peopleAdd/peopleAdd';
  import {LoginUser} from '@/common/js/common';
  export default {
    name: 'teamAdd',
    data () {
      return {
        msg: '创建团队',
        popleftAdd:false,
        loginUser:LoginUser.getUser(),
        groupName:'',
        peopleNum:'',
        tAddData:[]
      }
    },
    methods:{
      messagebox:function (txt) {
        alert(txt)
      },
      addPeople:function () {
        this.popleftAdd = !this.popleftAdd;
      },
      backTeam:function(){
        this.$router.push('/teamManage');
//        this.$emit('backTeam');
      },
      submitTeam:function () {
          let phones;
          for(let i=0;i<this.tAddData.length;i++){
              if(i=this.tAddData.length){
                phones += this.tAddData[i].phoneNo;
              }else{
                phones += this.tAddData[i].phoneNo+',';
              }
          }
          let it = this;
          let flag = confirm('确定创建团队?');
          if (flag) {
            this.$axios({
              method: 'post',
              url: '/group/createGroup',
              data: this.$qs.stringify({
                userId:it.loginUser.userId,
                groupName:it.groupName,
                phone:phones
              })
            }).then(function (response) {
              if(it.groupName == ''){
                  alert("请填写团队名称");
                  return
              }
              if (response.data.code >= 0) {
                alert("团队创建成功");
                it.$router.push('/teamManage');
              }
            });
        }
      }
    },
    created:function(){

    },
    components:{
      peopleAdd
    }
  }
</script>




