<template>
  <div class="myBody">
  <div class="apparatusBox">
    <mt-header title="绑定设备信息">
       <mt-button slot="left" icon="back" @click="closePage">返回</mt-button>
    </mt-header>
    <div class="apparatusLogo"><i class="iconfont icon-diannaoshouji"></i></div>
    <div class="apparatusCont">
      <mt-cell title="网络状态">
        <label class="green" v-if="connect">连接正常</label>
        <label class="red" v-if="!connect">未连接</label>
      </mt-cell>
      <mt-cell title="Mac地址">
        <label>{{twocData.mac}}</label>
      </mt-cell>
      <mt-cell title="wifi名称">
        <label>{{twocData.wifiName}}</label>
      </mt-cell>
      <mt-cell title="Wifi秘钥">
        <label>{{twocData.wifipwd}}</label>
      </mt-cell>
    </div>
    <div class="apparatusIcon">
      <input type="button" value="解绑此设备" class="apparatusIcon1" @click="unBundling">
      <input type="button" value="连接此设备的Wifi网络" class="apparatusIcon2" @click="linkWifi">
    </div>
  </div>
  </div>
</template>
<script>
  import {LoginUser} from '@/common/js/common';
  import { Toast } from 'mint-ui';
  export default {
    name: 'device',
    data () {
      return {
        connect:false,
        loginUser:{},
        twocData:{}
      }
    },
    activated:function(){
      this.loginUser = LoginUser.getUser();
      this.getNetwork();
    },
    methods:{
      getNetwork:function(){
        let _this=this;
        this.$axios({
          method: 'get',
          url: '/binding/selectTwocMsgByUserId?userId='+_this.loginUser.userId +'&userName='+_this.loginUser.username
        }).then(function (response) {
          if(response.data.msg=='success'){
            _this.twocData=response.data.data;
            _this.connect = true;
          }
        });
      },
      closePage:function(){
        //this.$router.push('/my');
        this.$router.goBack();
      },
      unBundling:function(){
        if(!(this.twocData&&this.twocData.mac)){
          let instance = Toast('设备未绑定,无法解绑');
          setTimeout(() => {
            instance.close();
          }, 2000);
          return;
        }
        let _this=this;
        this.$axios({
          method: 'post',
          url: '/binding/disBindingTwocPhoneByUserId',
          data:this.$qs.stringify({userId:_this.loginUser.userId,userName:_this.loginUser.username})
        }).then(function (response) {
          if(response.data.code > 0){
            _this.twocData = {};
            _this.connect = false;
            let instance = Toast('设备解绑成功');
            setTimeout(() => {
                instance.close();
            }, 2000);
          }
        });
      },
      linkWifi:function(){
         if(this.twocData&&this.twocData.mac){
           window.location.href = 'myapp://parameter?type=ConnectionWiFi&wifiData='+JSON.stringify(this.twocData)
         }else{
           let instance = Toast('设备未绑定，无法连接WiFi');
           setTimeout(() => {
             instance.close();
           }, 2000);
         }
      }
    }
  }
</script>
<style scoped>
  .myBody{
    width: 100%;
    height: 100vh;
  }
  .apparatusBox{background:url('./apparatusBox.png') no-repeat center top;background-size:100% 1.42rem;position:relative;}
  .mint-header{
    background: none;
  }
  .apparatusLogo{border:1px solid #1081f3;background:#fff;width:1.15rem;height:1.15rem;line-height:1.3rem;border-radius:50%;position: absolute;left:50%;top:1.42rem;margin:-0.71rem auto auto -0.57rem;overflow:hidden;}
  .apparatusLogo i{font-size:.9rem;color:#2a81ee;}
  .apparatusCont{padding-top:1.6rem;text-align: left;}
  .apparatusIcon{margin-top:0.8rem;}
  .apparatusIcon input[type=button]{
     display: block;
  }
  .apparatusIcon1{width:80%;margin:.1rem auto;height:.42rem;line-height:.42rem;font-size:.18rem;border-radius:.21rem;color:#fff;background:linear-gradient(126deg,#0f5bf6 0%,#16a3e4 100%);box-shadow:0 0 9px #16a3e4;border:none; }
  .apparatusIcon2{width:80%;margin:.1rem auto;height:.42rem;line-height:.42rem;font-size:.18rem;border-radius:.21rem;color:#249ee8;background:#fff;border:1px solid #dfd6d6;}

  .green{ color: green !important;}
  .red{color: red !important;}
  .apparatusIcon input{
    text-align: center;
  }
</style>





