<style scoped>
  .msgBody{
    width: 100%;
    height: calc(100vh);
    text-align: left;
    overflow: hidden;
    background-color: #f0f0f0;
  }
  .notices .box{
    padding: 0.2rem 0.15rem;
    margin-top: 0.48rem;
    background-color: #fff;
    position: relative;
  }
  .msgTitle{
    font-weight: bold;
    padding-bottom: 10px;
  }
  .box div:first-child{
    position: absolute;
    text-align: center;
    width: calc(100% - 0.3rem);
    top: -30px;
  }
  .box div:first-child label{
    display: inline-block;
    text-align: center;
    padding: 0 10px;
    border-radius: 3px;
    color: #fff;
    line-height: 20px;
    background-color: #c5c5c5;
  }
  .msgContent{
    color: #666;
    opacity: 0.8;
    position: relative;
    padding-right: 10px;
  }
  .msgContent div{
    padding-left: 0.3rem;
  }
  .notices .icon-xiaoxi{
    position: absolute;
    top: -2px;
    display: block;
    width: 0.2rem;
    height: 0.2rem;
    background-image: url("./xiaoxi.png");
    background-size: 100% 100%;
  }
</style>
<template>
  <transition name="slide">
  <div class="msgBody">
    <mt-header title="系统通知">
      <mt-button slot="left" icon="back" @click="backPage">返回</mt-button>
    </mt-header>
    <div class="notices hdBody">
      <mt-loadmore :bottom-method="loadBottom" :top-method="loadTop" :bottom-all-loaded="allLoaded" :autoFill="false" ref="loadmore">
        <div class="j_loadmore">
            <div class="box" v-for="(notice,idx) in noticeList">
                <div><label>{{notice.date|FormartDate}}</label></div>
                <div class="msgTitle" v-if='notice.title'>
                  {{notice.title}}
                </div>
                <div class="msgContent">
                  <i class="icon-xiaoxi"></i>
                  <div>{{notice.fcontent}}</div>
                </div>
            </div>
        </div>
      </mt-loadmore>
      <!--<div class="box">-->
      <!--<div><label>10:21</label></div>-->
      <!--<div class="msgTitle">-->
      <!--消息标题:XXXXXXXXX-->
      <!--</div>-->
      <!--<div class="msgContent">-->
      <!--<i class="icon-xiaoxi"></i>-->
      <!--<span>大家在希尔顿酒店大门口集合，带好各自行李和随 身物品办理入住。</span>-->
      <!--</div>-->
      <!--</div>-->
    </div>
  </div>
  </transition>
</template>
<script>
  import { Toast } from 'mint-ui';
  import { Loadmore } from 'mint-ui';
  import {LoginUser} from '@/common/js/common';
  export default {
    name: 'systemNotice',
    data () {
      return {
        noticeList: [],
        pageNum:1,
        pageSize:20,
        allLoaded:false
      }
    },
    components:{Loadmore},
    created:function(){
      this.queryList();
    },
    methods:{
      loadTop:function() {
        this.pageNum = 1;
        this.allLoaded = false;
        this.queryList();
        this.$refs.loadmore.onTopLoaded();
      },
      loadBottom:function(){
        this.pageNum++;
        this.queryList();
      },
      postion:function(){
        if(this.pageNum===1){
          this.$nextTick(function() {
            var dom = document.getElementsByClassName('mint-loadmore');
            dom[0].parentNode.scrollTop = 0;
          })
        }
      },
      backPage:function(){
        if(this.$route.query.param){
          this.$router.push('/msgList?storage=true&param='+this.$route.query.param);
        }else if(this.$route.query.isMsg){
          this.$router.push('/msgList?param=home');
        }else{
          this.$router.push('/msgList?storage=true');
        }
      },
      queryList:function(){
        let _this = this;
        this.$axios({
          method: 'post',
          url: 'groupMessage/getGroupLocationShare',
          data:this.$qs.stringify({
            userId:this.userId,
            messageType:6,
            pageNum:this.pageNum,
            pageSize:this.pageSize
          })
        }).then(function (response) {
          if(response.data.msg=='success'){
            if(!response.data.data.list||response.data.data.list.length<=0){
               _this.allLoaded = true;
            }
            if(_this.pageNum===1){
              _this.noticeList = response.data.data.list;
            }else{
              _this.noticeList = _this.noticeList.concat(response.data.data.list);
            }
            if(_this.noticeList.length<=0){
              let instance = Toast('没有系统消息');
              setTimeout(() => {
                 instance.close();
                 _this.backPage();
              }, 1000);
            }
            _this.$refs.loadmore.onBottomLoaded();
            _this.postion();
          }
        });
      }
    }
  }
</script>




