<style scoped>
  .msgBody{
    width: 100%;
    height: calc(100vh);
    text-align: left;
    overflow-y: scroll;
  }
</style>
<template>
    <div class="msgBody">
      <transition :name="slideName">
        <keep-alive>
          <router-view class="child-view"></router-view>
        </keep-alive>
      </transition>
    </div>
</template>
<script>
  export default {
    name: 'messages',
    beforeRouteUpdate (to, from, next) {
      let isBack = this.$router.isBack;
      if (isBack) {
        this.slideName = 'slide-right';
      } else {
        this.slideName = 'slide-left';
      }
      this.$router.isBack = false;
      next();
    }
  }
</script>




