<template>
    <div class="msgBody">
      <div class="no-data-pub" v-if="xcList.length<=0">
        <i class="iconfont  icon-zanwuxiaoxi"></i>
        <p class="text">暂无行程信息</p>
      </div>
      <div class="notices xcBody" v-else>
        <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom"  :autoFill="false"  :bottom-all-loaded="allLoaded" ref="loadmore">
          <div class="container">
            <div class="box" :val="idx" :dType="dType" @click="clickList(xc.fid)"  v-longtouch="timeOutEvent"  v-for="(xc,idx) in xcList">
              <div class="msgTitle border-bottom">
                {{xc.fname}}
              </div>
              <div class="item">
                <span><i class="iconfont icon-lvxinghover"></i>{{xc.fcityFrom}}-{{xc.fcityTo}}</span>
                <span><i class="iconfont icon-bourn2"></i>{{xc.fnum}}个目的地</span>
              </div>
              <div class="item">
                <span><i class="iconfont icon-tuandui3"></i>{{xc.fpersionNum}}人</span>
                <span><i class="iconfont icon-rili"></i>{{xc.fstartAt}}到{{xc.fbackAt}}</span>
              </div>
              <div class="item border-top">
                <span class="caozuo fr"  @click="clickluxian">行程路线</span>
              </div>
            </div>
          </div>

          </mt-loadmore>
      </div>
  </div>


</template>
<script>
  import {LoginUser} from '@/common/js/common';
  import { Toast } from 'mint-ui';
  import { MessageBox } from 'mint-ui';
  import { Indicator } from 'mint-ui';
  export default {
    name: 'lishituanduixingcheng',
    data () {
      return {
        dType:"lstuandui",
        deleIndex:0,
        timeOutEvent:1,
        xcList:[],
        pageNum:1,
        pageSize:10,
        allLoaded:false
      }
    },
    activated(){
      longTouchMethodLT = this.longlstuandui;
      this.pageNum=1;
      this.xcList=[];
      this.queryList();
    },
    created:function(){
      longTouchMethodLT = this.longlstuandui;
      this.pageNum=1;
      this.xcList=[];
      this.queryList();
    },
    methods: {
      loadTop:function() {
        this.pageNum = 1;
        this.allLoaded = false;
        this.queryList();
        this.$refs.loadmore.onTopLoaded();
      },
      loadBottom:function(){
        this.pageNum++;
        this.queryList();
      },
      postion:function(){
        if(this.pageNum===1){
          this.$nextTick(function() {
            var dom = document.getElementsByClassName('mint-loadmore');
            dom[0].parentNode.scrollTop = 0;
          })
        }
      },
      queryList(){
        Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        });
        let _this = this;
        this.$axios({
          method: 'post',
          url: 'ttravel/ListHistoryTtravel',
          data: _this.$qs.stringify({
            userId: LoginUser.getUser().userId,
            pageNum: _this.pageNum,
            pageSize: _this.pageSize
          })
        }).then(function (response) {
          Indicator.close();
          if(response.data.msg=='success'){
            if(!response.data.data.list||response.data.data.list.length<=0){
              _this.allLoaded = true;
            }
            if(_this.pageNum===1){
              _this.xcList = response.data.data.list;
            }else{
              _this.xcList = _this.xcList.concat(response.data.data.list);
            }
          }
          if(!_this.$refs.loadmore) return;
          _this.$refs.loadmore.onBottomLoaded();
          _this.postion();
        }).catch(function(){
          Indicator.close();
          throw new Error('网络异常');
        });
      },
      longlstuandui:function(e){
        console.log("历史团队");
        let flag = true,dom= rootDom(e,'box');
        let obj=this.xcList[dom.getAttribute('val')];
        this.deleIndex=dom.getAttribute('val');       //该行程处于列表中的序号
          MessageBox.confirm('确定删除该行程吗?').then(action => {
            this.delFn(obj.fid)
        });

      },
      delFn(fid){
        Indicator.open({
          text: '删除中...',
          spinnerType: 'fading-circle'
        });
        let _this=this;
        this.$axios({
          method: 'post',
          url: 'ttravel/deleteTravelShi',
          data:this.$qs.stringify({
            userId:LoginUser.getUser().userId,
            fid:fid
          })
        }).then(function(response){
          Indicator.close();
          if(response.data.msg=='success'){
            let instance = Toast('删除成功');
            _this.xcList.splice(_this.deleIndex, 1);
            setTimeout(() => {
              instance.close();
          }, 2000);
          }
        }).catch(function(){
          Indicator.close();
          throw new Error('网络异常');
        });
      },
      clickList:function(idx){
        //this.$router.push({name:"xcInfo",params:{name:"lstuanduiList",fid:idx}})
        let routerInfo = {name:"lstuanduiList",fid:idx};
        localStorage.setItem("routerInfo",JSON.stringify(routerInfo));
        this.$router.push({name:'xcInfo'});
      },
      clickluxian:function(e){
        e.stopPropagation();
        let instance = Toast('暂无行程路线');
        setTimeout(function () {
          instance.close();
        }, 2000);
      },
      fhrecieveMessage(){
        this.xcck=!this.xcck;
      }

    }
  }
</script>

<style scoped>
  .mt{
    width: 100%;
  }
  .msgBody{
    width: 100%;
    text-align: left;
    overflow-y: scroll;
    background-color: #f0f0f0;
    padding-bottom: .1rem;
    -webkit-overflow-scrolling: touch;
    overflow-x: hidden;
  }
  .notices .box{
    padding: 0.16rem 0.15rem 0.3rem 0.15rem ;
    margin-top: 0.16rem;
    background-color: #fff;
  }
  .msgTitle{
    font-size: 1.2em;
    padding-bottom: 10px;
    line-height: .32rem;
    overflow: hidden;
    white-space:nowrap;
    text-overflow:ellipsis;
    overflow:hidden;
    height: .32rem;
  }
  .box .item{
    width: 100%;
    line-height: .3rem;
    padding-top: .1rem;
  }
  .border-top{
    border-top: 1px  solid #d9d9d9;
  }
  .border-bottom{
    border-bottom: 1px  solid #d9d9d9;
  }
  .msgBody .box .item:last-child{
    margin-top: 15px;
  }
  .box .item span{
    display: inline-block;
    vertical-align: middle;
    font-size: 0.16rem;
  }
  .box .item span:first-child{
    width: 36%;
    white-space:nowrap;
    text-overflow:ellipsis;
    overflow:hidden;
  }
  .icon-lvxinghover{ color: #8a68d7;}
  .icon-bourn2{color: #33aa59;}
  .icon-tuandui3{color: #f6be61;}
  .icon-rili{color: #f66755;}
  .iconfont{
    margin-right: 5px;
  }


 .fr{
   float: right;
 }
  .fl{
    float: left;
  }
 .caozuo{
   font-size: .16rem;
   color: #2074e7;
   display: block;
   width: 50%;
   text-align: center;
 }
  .signStyle{
    width: .66rem;
    height: .25rem;
    border-radius: .125rem;
    float: right;
    color: #ffffff;
    font-size: .12rem;
    text-align: center;
    line-height: .25rem;

  }
  .xinjian{
    background: #17a1e4;
  }
  .fengxiang{
background: #14ae41;
  }
  .chexiao{
background: #ff6e01;
  }
  .xcBody{
    height: calc(100vh - 1.7rem);
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    overflow-x: hidden;
  }
  .container{
    min-height: calc(100vh  - 1.7rem);
  }
</style>




