<style scoped>
  .myBody{
     width: 100%;
     height: 100vh;
     text-align: left;
  }
  .box{
    border-top: 0.12rem solid #f0f0f0;
  }
  .mint-cell-value span{
     font-size: 0.9em;
  }
  .mint-cell-value input{
     border: solid 1px transparent;
     width: 70vw;
     padding: 0.04rem 0;
     border-radius: 3px;
     outline: none;
  }
  .box .check{
    text-align: right;
    line-height: .4rem;
  }
  .box input[type='checkbox']{
   float: right;
    margin-right: .3rem;
    margin-top: .12rem;
 }
  .box label{
    float: right;
    margin-right: .05rem;
    vertical-align: middle;
  }
</style>
<template>
  <transition name="slide">
   <div class="myBody">
      <mt-header title="修改密码">
        <mt-button slot="left" icon="back" @click="closePage">返回</mt-button>
        <mt-button slot="right" @click="saveInfo">完成</mt-button>
      </mt-header>
      <div class="box">
        <div class="list">
          <mt-cell title="原始密码">
            <input class="pas" type="password" v-model="user.password" placeholder="请原始密码">
          </mt-cell>
        </div>
        <div class="list">
          <mt-cell title="密码">
            <input class="pas" type="password" v-model="user.passwordNew" placeholder="请设置新密码">
          </mt-cell>
        </div>
        <div class="list">
          <mt-cell title="确认密码">
            <input class="pas" type="password" v-model="user.rePwd" placeholder="请再次输入">
          </mt-cell>
        </div>
        <div class="check">
          <input type="checkbox" v-model="checked" >
          <label>显示密码</label>
        </div>

      </div>
   </div>
   </transition>
</template>
<script>
  import { Toast } from 'mint-ui';
  import {LoginUser} from '@/common/js/common';
  export default {
    name: 'modPwd',
    data () {
      return {
        checked:false,
         user:{}
      }
    },
    activated:function(){
      this.user = {};
    },
    watch:{
      checked:function(val){
        var pas= document.getElementsByClassName("pas");
        for(var i=0;i<pas.length;i++){
          if(val==true){
            pas[i].setAttribute("type","text");
          }else{
            pas[i].setAttribute("type","password");
          }
        }
      }
    },
    methods:{
      closePage:function(){
        //this.$router.push('/personInfo');
        this.$router.goBack();
      },
      saveInfo:function(){
        var patrn=/^(\w){6,20}$/;
        let _this=this;
        if (!patrn.exec(_this.user.passwordNew)){
          let instance = Toast('密码格式不正确');
          setTimeout(() => {
            instance.close();
        }, 2000);
          return
        };
        if(this.user.password==''||this.user.passwordNew==''||this.user.rePwd==''){
          let instance = Toast('请输入完整信息');
          setTimeout(() => {
            instance.close();
          }, 2000);
        }else if(this.user.passwordNew!==this.user.rePwd){
          let instance = Toast('二次输入密码不一样');
          setTimeout(() => {
            instance.close();
          }, 2000);
        }else{
          let _this = this;
          this.$axios({
            method: 'post',
            url: 'register/updatePassword',
            data:this.$qs.stringify({
               userId:LoginUser.getUser().userId,
               password:this.user.password,
               passwordNew:this.user.passwordNew
            })
          }).then(function (response) {
             if(response.data.code==1){
               let instance = Toast('修改成功');
               setTimeout(() => {
                 instance.close();
                 appSocket.websocket.close();
                 window.location.href = "myapp://parameter?type=LoginOut";
                 LoginUser.clear();//清除用户缓存
                 _this.$router.push('/login');
               }, 2000);
             }else{
                Toast(response.data.msg);
             }
          });
        }
      }
    }
  }
</script>




