<template>
    <div class="msgBody">
            <div class="no-data-pub" v-if="xcList.length<=0">
              <i class="iconfont  icon-zanwuxiaoxi"></i>
              <p class="text">暂无行程信息</p>
              <p class="sub-text">点击“创建行程”试试吧</p>
            </div>
            <div class="notices xcBody" v-else>
                <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom"  :autoFill="false"  :bottom-all-loaded="allLoaded" ref="loadmore">
                    <div class="container">
                        <div  class="box" :val="idx" :dType="dType" @click="clickList(xc)" v-longtouch="timeOutEvent"
                              v-for="(xc,idx) in xcList">
                            <em v-if="xc.fread==0"></em>
                            <div class="msgTitle border-bottom">
                                {{xc.fname}}
                                <span v-if="xc.fshared!==''&&xc.fshared==0" class="signStyle" :class="style='xinjian'">新建</span>
                            <span v-if="(xc.fshared!==''&&xc.fshared==1)||(xc.fshared!==''&&xc.fshared==3)" class="signStyle"
                                  :class="style='fengxiang'">已分享</span>
                            <span v-if="xc.fshared!==''&&xc.fshared==2" class="signStyle"
                                  :class="style='chexiao'">已撤销</span>
                            </div>
                            <div class="item">
                                <span><i class="iconfont icon-lvxinghover"></i>{{xc.fcityFrom}}-{{xc.fcityTo}}</span>
                                <span><i class="iconfont icon-bourn2"></i>{{xc.fnum}}个目的地</span>
                            </div>
                            <div class="item">
                                <span><i class="iconfont icon-tuandui3"></i>{{xc.fpersionNum}}人</span>
                                <span><i class="iconfont icon-rili"></i>{{xc.fstartAt}}到{{xc.fbackAt}}</span>
                            </div>
                            <div class="item border-top">
                            <span v-if="xc.fshared!==''&&(xc.fshared==0 || xc.fshared==2 )" class="caozuo fl"
                                  @click="clickfengxiang($event,xc.fid)">分享行程</span>
                                <span v-if="xc.fshared==1" class="caozuo fl" @click="clickchexiao($event,xc.fid)">撤销行程</span>
                                <span class="caozuo fr" @click="clickluxian($event,xc.fid)">行程路线</span>
                            </div>
                        </div>
                    </div>
                </mt-loadmore>

            </div>

            <shared1 v-if="shared1" v-on:fxEvent="fxMessage" v-on:qxEvent="qxMessage"></shared1>

    </div>

</template>
<script>
    import {LoginUser} from '@/common/js/common';
    import { Toast } from 'mint-ui';
    import { MessageBox } from 'mint-ui';
    import shared1 from '@/components/xcheng/shared1/shared1'
    import { Indicator } from 'mint-ui';
    export default {
        name: 'tuanduixingcheng',
        data () {
            return {
                dType: "tuandui",
                timeOutEvent: 1,
                deleIndex: 0,
                xcList: [],
                dataObj: [],
                fid: "",
                shared1: false,
                pageNum:1,
                pageSize:10,
                allLoaded:false
            }
        },
        activated:function(){
            longTouchMethodT = this.tlongA;
            this.xcList=[];
            this.pageNum=1;
            this.queryList();
        },
        created:function(){
            longTouchMethodT = this.tlongA;
            this.xcList=[];
            this.pageNum=1;
            this.queryList();
        },
        methods: {
            loadTop:function() {
                this.pageNum = 1;
                this.allLoaded = false;
                this.queryList();
                this.$refs.loadmore.onTopLoaded();
            },
            loadBottom:function(){
                this.pageNum++;
                this.queryList();
            },
            postion:function(){
                if(this.pageNum===1){
                    this.$nextTick(function() {
                        var dom = document.getElementsByClassName('mint-loadmore');
                        dom[0].parentNode.scrollTop = 0;
                    })
                }
            },
            queryList(){
                Indicator.open({
                  text: '加载中...',
                  spinnerType: 'fading-circle'
                });
                let _this = this;
                this.$axios({
                    method: 'post',
                    url: 'ttravel/ListTtravel',
                    data: _this.$qs.stringify({
                        userId: LoginUser.getUser().userId,
                        pageNum: _this.pageNum,
                        pageSize: _this.pageSize
                    })
                }).then(function (response) {
                    Indicator.close();
                    if(response.data.msg=='success'){
                        if(!response.data.data.list||response.data.data.list.length<=0){
                            _this.allLoaded = true;
                        }
                        if(_this.pageNum===1){
                            _this.xcList = response.data.data.list;
                        }else{
                            _this.xcList = _this.xcList.concat(response.data.data.list);
                        }
                    }
                    if(!_this.$refs.loadmore) return;
                    _this.$refs.loadmore.onBottomLoaded();
                    _this.postion();
                }).catch(function(){
                  Indicator.close();
                  throw new Error('网络异常');
                });
            },
            tlongA: function (e) {
               let _this=this;
                let flag = true, dom = rootDom(e, 'box');

                let obj = this.xcList[dom.getAttribute('val')];
                this.deleIndex = dom.getAttribute('val');

                if ( (obj.fuserId==LoginUser.getUser().userId&&obj.fshared==0)||(obj.fuserId==LoginUser.getUser().userId&&obj.fshared==2)) {
                  MessageBox({
                    message: '确定删除该行程吗?',
                    confirmButtonText: '删除',
                    showCancelButton: true
                  }).then(
                    function (action) {
                      if (action == 'confirm') {
                        _this.delFn(obj.fid);
                      }
                    }
                  );

                } else {

                  let instance = Toast('禁止操作');
                  setTimeout(function () {
                    instance.close();
                  }, 1000)
                }
            },
            delFn(fid){
                Indicator.open({
                  text: '删除中...',
                  spinnerType: 'fading-circle'
                });
                let _this = this;
                this.$axios({
                    method: 'post',
                    url: 'ttravel/deleteTravel',
                    data: _this.$qs.stringify({
                        userId: LoginUser.getUser().userId,
                        fid: fid
                    })
                }).then(function (response) {
                    Indicator.close();
                    if (response.data.msg == 'success') {
                        let instance = Toast('删除成功');
                        _this.xcList.splice(_this.deleIndex, 1);
                        setTimeout(function () {
                            instance.close();
                        }, 1000);
                    }
                }).catch(function(){
                  Indicator.close();
                  throw new Error('网络异常');
                });
            },
            fxMessage(groupObj){
                Indicator.open({
                  text: '分享中...',
                  spinnerType: 'fading-circle'
                });
                this.shared1 = false;
                let _this = this;
                this.$axios({
                    method: 'post',
                    url: 'ttravel/insertTtravelShared',
                    data: this.$qs.stringify({
                        userId: LoginUser.getUser().userId,
                        ftravelId: this.fid,
                        groupId: groupObj.groupId
                    })
                }).then(function (response) {
                    Indicator.close();
                    if (response.data.msg == 'success') {
                        let instance = Toast('分享成功');
                        setTimeout(function () {
                            instance.close();
                        }, 2000);
                        _this.queryList();
                    }
                }).catch(function(){
                  Indicator.close();
                  throw new Error('网络异常');
                });
            },
            qxMessage(){
                this.shared1 = false;
            },
            clickList(xc){
                if (xc.fshared == 2&&LoginUser.getUser().userId!=xc.fuserId) {
                    let instance = Toast('该行程已撤销');
                    setTimeout(function () {
                        instance.close();
                    }, 2000);
                    return
                }
                let routerInfo = {name: "tuanduiList", fid: xc.fid};
                localStorage.setItem("routerInfo",JSON.stringify(routerInfo));
                //this.$router.push({name: "xcInfo", params: {name: "tuanduiList", fid: xc.fid}})
                this.$router.push({name: "xcInfo"});
            },
            clickluxian(e, idx){
                e.stopPropagation();
                let instance = Toast('暂无行程路线');
                setTimeout(function () {
                    instance.close();
                }, 2000);
            },
            clickfengxiang(e, idx){
                this.fid = idx;
                e.stopPropagation();
                this.shared1 = true;

            },
            clickchexiao(e, idx){
                e.stopPropagation();
                let _this = this;
                MessageBox.confirm('确定撤销当前行程?').then(function () {
                    _this.$axios({
                        method: 'post',
                        url: 'ttravel/updatestatus2',
                        data: _this.$qs.stringify({
                            userId: LoginUser.getUser().userId,
                            fid: idx
                        })
                    }).then(function (response) {
                        if (response.data.msg == 'success') {
                            _this.queryList();
                            let instance = Toast('操作成功');
                            setTimeout(function () {
                                instance.close();
                            }, 2000);
                        }
                    });
                });
            }
        },
        components: {
            shared1
        }
    }
</script>

<style scoped>
    .mt {
        width: 100%;
    }

    .msgBody {
        width: 100%;
        text-align: left;
        overflow-y: scroll;
        background-color: #f0f0f0;
        -webkit-overflow-scrolling: touch;
        overflow-x: hidden;
    }

    .notices .box {
        padding: 0.16rem 0.15rem 0.3rem 0.15rem;
        margin-top: 0.16rem;
        background-color: #fff;
        overflow: hidden;
      position: relative;
    }

    .msgTitle {
        font-size: 1.2em;
        padding-bottom: 10px;
        line-height: .32rem;
        overflow: hidden;
      white-space:nowrap;
      text-overflow:ellipsis;
      overflow:hidden;
      height: .32rem;
      position: relative;
      padding-right: .76rem;

    }
    .signStyle {
      width: .66rem;
      height: .25rem;
      border-radius: .125rem;
      position: absolute;
      right: 0;
      top: .04rem;
      color: #ffffff;
      font-size: .12rem;
      text-align: center;
      line-height: .25rem;

    }
    .box .item {
        width: 100%;
        line-height: .3rem;
        padding-top: .1rem;
    }

    .border-top {
        border-top: 1px solid #d9d9d9;
    }

    .border-bottom {
        border-bottom: 1px solid #d9d9d9;
    }

    .msgBody .box .item:last-child {
        margin-top: 15px;
    }

    .box .item span {
        display: inline-block;
        vertical-align: middle;
        font-size: 0.16rem;
    }

    .box .item span:first-child {
        width: 36%;
        white-space:nowrap;
        text-overflow:ellipsis;
        overflow:hidden;
    }

    .icon-lvxinghover {
        color: #8a68d7;
    }

    .icon-bourn2 {
        color: #33aa59;
    }

    .icon-tuandui3 {
        color: #f6be61;
    }

    .icon-rili {
        color: #f66755;
    }

    .iconfont {
        margin-right: 5px;
    }

    .fr {
        float: right;
    }

    .fl {
        float: left;
    }

    .caozuo {
        font-size: .16rem;
        color: #2074e7;
        display: block;
        width: 50%;
        text-align: center;
    }



    .xinjian {
        background: #17a1e4;
    }

    .fengxiang {
        background: #14ae41;
    }

    .chexiao {
        background: #ff6e01;
    }
    .xcBody{
        height: calc(100vh - .6rem - 1.1rem);
        overflow-y: auto;
        -webkit-overflow-scrolling: touch;
        overflow-x: hidden;
    }
    .container{
        min-height: calc(100vh - .6rem - 1.1rem);
    }

    .box em {
      position: absolute;
      top: 4px;
      left: 4px;
      width: 8px;
      height: 8px;
      border: 1px solid #fff;
      border-radius: 50%;
      background: linear-gradient(to bottom,#ff641f 0%,#ff1306 100%);
      display: block;
    }
</style>




