<style scoped>
  .msgBody{
    width: 100%;
    height: calc(100vh);
    text-align: left;
  }
  .pic {
    background-image: url("msgIcon.png");
    display: block;
    position: absolute;
    top: 12px;
    left: 8px;
    width: 0.5rem;
    height: 0.5rem;
    background-size: cover;
  }
  .pic_1 {background-image: url("./img/icon_1.png");}
  .pic_2 {background-image: url("./img/icon_2.png");}
  .pic_3 {background-image: url("./img/icon_3.png");}
  .pic_4 {background-image: url("./img/icon_4.png");}
  .pic_5 {background-image: url("./img/icon_5.png");}
  .pic_6 {background-image: url("./img/icon_6.png");}
  .pic_7 {background-image: url("./img/icon_7.png");}
</style>
<style>
  .mint-cell:first-child .mint-cell-wrapper{
    background-image: none !important;
  }
  .msgBody .help .mint-cell-title{
    padding-left: 18% !important;
    padding-bottom: 5px !important;
    width: 70%;
  }
  .helpBody .mint-header{
    background:transparent;
  }
  .msgBody .mint-cell-label{
    white-space:nowrap;
    overflow:hidden;
    text-overflow:ellipsis;
    width: 94%;
    padding: 2px 0;
  }
  .msgBody .mint-cell-wrapper{
    padding: 4px 0;
  }
  .help .mint-cell:last-child .mint-cell-title{
    padding-bottom: 13px !important
  }
  .time{
    position: absolute;
    display: block;
    top: 20px;
    right: 9%;
    font-size: 14px;
  }
  .loadmore{
    height: calc(100vh - 60px);
  }
  .msgBody .mint-badge{
    top: 0px;
    right: -6px;
    position: absolute;
  }
</style>
<template>
  <div class="msgBody">
    <mt-header title="消息管理">
      <mt-button slot="left" icon="back" @click="historyGo()">返回</mt-button>
    </mt-header>
    <div class="box help">
      <mt-loadmore :top-method="loadTop" ref="loadmore">
       <div class="loadmore">
         <div class="box" @click="clickList('/msgList/teamNotice')">
           <mt-cell title="团队通知" :label="msgList.teamInfo.fcontent|NoData" is-link>
             <i slot="icon"  class="pic pic_1">
               <mt-badge v-if="msgList.teamInfo.count!==0" size="small" type="error">{{msgList.teamInfo.count}}</mt-badge>
             </i>
             <label class="time">{{msgList.teamInfo.date|FormartDate}}</label>
           </mt-cell>
         </div>
         <div class="box" @click="clickList('/msgList/locationShare')">
           <mt-cell  title="位置共享" :label="msgList.address.fcontent|NoData" is-link>
             <i slot="icon"  class="pic pic_2">
               <mt-badge v-if="msgList.address.count!==0" size="small" type="error">{{msgList.address.count}}</mt-badge>
             </i>
             <label class="time">{{msgList.address.date|FormartDate}}</label>
           </mt-cell>
         </div>
         <div class="box" @click="clickList('/msgList/teamHelp')">
           <mt-cell title="团队求助" :label="msgList.teamHelpInfo.fcontent|NoData" is-link>
             <i slot="icon"  class="pic pic_3">
               <mt-badge v-if="msgList.teamHelpInfo.count!==0" size="small" type="error">{{msgList.teamHelpInfo.count}}</mt-badge>
             </i>
             <label class="time">{{msgList.teamHelpInfo.date|FormartDate}}</label>
           </mt-cell>
         </div>
         <div class="box" @click="clickList('/msgList/teamInvite')">
           <mt-cell title="团队邀请" :label="msgList.teamInvite.fcontent|NoData" is-link>
             <i slot="icon"  class="pic pic_4">
               <mt-badge v-if="msgList.teamInvite.count!==0" size="small" type="error">{{msgList.teamInvite.count}}</mt-badge>
             </i>
             <label class="time">{{msgList.teamInvite.date|FormartDate}}</label>
           </mt-cell>
         </div>
         <div class="box" @click="clickList('/msgList/travelList/msgList')">
           <mt-cell title="行程分享" :label="msgList.shareInfo.fcontent|NoData" is-link>
             <i slot="icon"  class="pic pic_5">
               <mt-badge v-if="msgList.shareInfo.count!==0" size="small" type="error">{{msgList.shareInfo.count}}</mt-badge>
             </i>
             <label class="time">{{msgList.shareInfo.date|FormartDate}}</label>
           </mt-cell>
         </div>
         <div class="box" @click="clickList('/warningList?param=true')">
           <mt-cell title="预警提示" :label="msgList.warnInfo.fcontent|NoData" is-link>
             <i slot="icon"  class="pic pic_6">
               <mt-badge v-if="msgList.warnInfo.count&&msgList.warnInfo.count!==0" size="small" type="error">{{msgList.warnInfo.count}}</mt-badge>
             </i>
             <label class="time">{{msgList.warnInfo.date|FormartDate}}</label>
           </mt-cell>
         </div>
         <div class="box" @click="clickList('/msgList/systemNotice')">
           <mt-cell title="系统通知" :label="msgList.sysInfo.fcontent|NoData" is-link>
             <i slot="icon"  class="pic pic_7">
               <mt-badge v-if="msgList.sysInfo.count!==0" size="small" type="error">{{msgList.sysInfo.count}}</mt-badge>
             </i>
             <label class="time">{{msgList.sysInfo.date|FormartDate}}</label>
           </mt-cell>
         </div>
       </div>
      </mt-loadmore>
    </div>
  </div>
</template>
<script>
  import {LoginUser} from '@/common/js/common';
  import { Loadmore } from 'mint-ui';
  export default {
    name: 'msgs',
    data () {
      return {
         msgList:{
            teamInfo:{fcontent:''},//0:团队通知；
            address:{fcontent:''},// 1：位置共享；
            teamHelpInfo:{fcontent:''},// 2：团队求助；
            teamInvite:{fcontent:''},// 3：团队邀请；
            shareInfo:{fcontent:''},// 4：行程分享；
            warnInfo:{fcontent:''},// 5：预警提示；
            sysInfo:{fcontent:''}// 6：系统通知
         }
      }
    },
    components:{Loadmore},
    created:function(){
      if(this.$route.query.storage){
        this.msgList = JSON.parse(localStorage.getItem('msgList'));
      }else{
        this.queryMsgList();
      }
    },
    activated:function(){
      localStorage.removeItem('msgList');
      this.queryMsgList();
    },
    methods:{
      loadTop:function() {
        this.queryMsgList();
        this.$refs.loadmore.onTopLoaded();
      },
      historyGo:function (){
        if(this.$route.query.param=='home'){
          this.$router.push('/home');
        }else{
          this.$router.push('/my');
        }
      },
      queryMsgList:function(){
        let _this = this;
        this.$axios({
          method: 'post',
          url: 'groupMessage/getGroupMessage',
          data:this.$qs.stringify({
             userId:LoginUser.getUser().userId,
             type:'all'
          })
        }).then(function (response) {
           for(let i=0;i<response.data.data.length;i++){
               let msg = response.data.data[i];
               if(msg.ftype===0){//0:团队通知；
                  _this.msgList.teamInfo = msg;
               }else if(msg.ftype==1){//1：位置共享；
                 _this.msgList.address = msg;
               }else if(msg.ftype==2){//2：团队求助
                 _this.msgList.teamHelpInfo = msg;
               }else if(msg.ftype==3){//3：团队邀请；
                 _this.msgList.teamInvite = msg;
               }else if(msg.ftype==4){//4：行程分享
                 _this.msgList.shareInfo = msg;
               }else if(msg.ftype==5){//5：预警提示；
                 _this.msgList.warnInfo = msg;
               }else if(msg.ftype==6){//6：系统通知
                 _this.msgList.sysInfo = msg;
               }
           }
           localStorage.setItem('msgList',JSON.stringify(_this.msgList));
        });
      },
      clickList:function(url){
         if(this.$route.query.param){
            url = url+'?param='+this.$route.query.param;
         }
         this.$router.push(url);
      }
    }
  }
</script>




