<template>

    <div class="xcheng">
        <mt-navbar v-model="selected">
            <mt-tab-item id="1">团队行程</mt-tab-item>
            <mt-tab-item id="2">个人行程</mt-tab-item>
        </mt-navbar>
        <div class="xchengContent">

            <!-- tab-container -->
            <mt-tab-container v-model="selected">
                <mt-tab-container-item id="1">
                    <tuanduixingcheng></tuanduixingcheng>
                </mt-tab-container-item>
                <mt-tab-container-item id="2">
                    <gerenxingcheng></gerenxingcheng>
                </mt-tab-container-item>
            </mt-tab-container>
        </div>
        <button id="btnn" class="addBtn" @click="add">
           创建<br>行程
        </button>
    </div>
</template>
<script>
    import tuanduixingcheng from './tuanduixingcheng/tuanduixingcheng.vue';
    import gerenxingcheng from './gerenxingcheng/gerenxingcheng.vue';
    export default {
        name: 'xcheng',
        data () {
            return {
                xctj:false,
                selected:"1"
            }
        },
        components:{
            tuanduixingcheng,
            gerenxingcheng
        },
        methods:{
            add(){
                this.$router.push({name:"xcAdd",params:{selected:this.selected}});
            }
        }

    }
</script>

<style scoped>

    .addBtn{
        width: 0.7rem;
        height: 0.7rem;
        border-radius: .1rem;
        position: fixed;
        z-index: 1000;
        right: .2rem;
        bottom: .9rem;
        border: none;
        outline: none;
        color: #ffffff;
        font-size: .16rem;
        opacity: 0.7;
        background:-webkit-gradient(linear, 0% 0%, 100% 100%, from(#ffb621), to(#ff821b))
    }
    .icon-tianjia{
        color: #ffffff;
        font-size: .2rem;
    }
    .xchengContent {
        overflow-y: auto;
        height: calc(100vh - 1.9rem);
        background: #f0f0f0;
        padding-bottom: .6rem;
        -webkit-overflow-scrolling: touch;
        overflow-x: hidden;

    }

    .mint-navbar .mint-tab-item.is-selected{
        margin-bottom: 0;

    }

    .xcheng  .mint-tab-item-label{
      font-size: .16rem;
    }
</style>




