<style scoped>
  .teamBox{text-align: left;background: #f0f0f0;height:100%;overflow:hidden;}
  .teamAddBox{}
  .teamAddTit{color:#666;font-size:14px;padding:10px 10px 0 10px;line-height:26px;}
  .teamAddTit span{color:#999;font-size:12px;}
  .mr10{margin-right:.1rem;border-radius: 50%;}
  .teamListFbt{font-size:12px;color:#999;}
  .teamAddList{
    height: calc(100vh - 145px - 41px);
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    overflow-x: hidden;
  }

</style>
<style>
  .teamAddName .mint-cell-value .mint-field-core{text-align: right;}
  .teamAddBox  .mint-cell-value{max-width:70%;}
  .din{display: none;}
  .teamAddBox .mint-loadmore{height:100%;}
  .teamBox .avatar{
    width: 0.28rem;
    height: 0.28rem;
    top:0.16rem;
  }
</style>
<template>
  <transition name="slide">
    <div class="teamBox">
      <mt-header title="团队查看">
        <mt-button  slot="left" icon="back" @click="backTeam" >返回</mt-button>
        <mt-button  slot="right" @click="saveTeam" v-if="isSendMan">保存</mt-button>
      </mt-header>

      <div class="teamAddBox">
        <div v-if="isSendMan">

          <div class="teamAddName">
            <a class="mint-cell mint-field">
              <div class="mint-cell-left"></div>
              <div class="mint-cell-wrapper">
                <div class="mint-cell-title">
                  <span class="mint-cell-text">团队名称</span>
                </div>
                <div class="mint-cell-value">
                  <input :placeholder="teamTit" v-model="teamTit" type="text" class="mint-field-core" maxlength="20">
                  <div class="mint-field-clear" style="">
                    <i class="mintui mintui-field-error"></i>
                  </div>
                  <span class="mint-field-state is-default">
                  <i class="mintui mintui-field-default"></i>
                </span>
                  <div class="mint-field-other"></div>
                </div>
              </div>
              <div class="mint-cell-right"></div>
            </a>
          </div>
          <div class="teamAddTit">团队成员<span>（{{teamNum}}人）</span></div>
          <div class="teamAddList">
            <mt-cell title="添加团队成员" is-link @click.native="addPeople"></mt-cell>
            <mt-cell v-for="(item ,index) in swipeData" v-if="item.memeberJob==='发起人'" :title=item.name>
              <img slot="icon" class="mr10 avatar" v-bind:src="item.url | NoImg" >
              <span class="teamListFbt">{{ item.memeberJob }}</span>
            </mt-cell>
            <mt-cell-swipe v-for="(item ,index) in swipeData" v-if="item.memeberJob!=='发起人'"
                           :title=item.name
                           :right="[
              {
                content: '删除',
                style: { background: '#e5433a', color: '#fff' },
                handler: () => delMemeber(index,item.memeberId)
              }
            ]">
              <img slot="icon" class="mr10 avatar" v-bind:src="item.url | NoImg">
              <span class="teamListFbt">{{ item.memeberJob }}</span>
            </mt-cell-swipe>
          </div>
          <div class="send-btn">
            <mt-button type="primary"  size="large" @click="deleteGroup">解散团队</mt-button>
          </div>
        </div>
        <div v-else>
          <div class="teamAddName">
            <mt-cell title="团队名称" :value="teamTit"></mt-cell>
          </div>
          <div class="teamAddTit">团队成员<span>（{{teamNum}}人）</span></div>
          <div class="teamAddList">
            <mt-cell v-for="(item ,index) in swipeData" :title=item.name>
              <img slot="icon" class="mr10 avatar" v-bind:src="item.url | NoImg">
              <span class="teamListFbt">{{ item.memeberJob }}</span>
            </mt-cell>
          </div>
          <div class="send-btn">
            <mt-button type="primary"  size="large" @click="exitGroup">退出团队</mt-button>
          </div>
        </div>
      </div>

    </div>
  </transition>
</template>
<script>
  import {LoginUser} from '@/common/js/common';
  import { MessageBox } from 'mint-ui';
  import { Toast } from 'mint-ui';
  import { Loadmore } from 'mint-ui';
  import { Indicator } from 'mint-ui';
  export default {
    name: 'teamView',
    data () {
      return {
        msg: '查看团队',
        loginUser:{},
        isSendMan:true,
        teamTit:'',
        teamNum:'',
        swipeData:'',
        titChange:false
      }
    },
    watch:{
      teamTit:function (val,oldval) {
        let groupName = this.$route.query.groupName;
        if(val !== groupName){
          this.titChange = true;
        }else{
          this.titChange = false;
        }
      }
    },
    created:function() {
      this.loginUser = LoginUser.getUser();
    },
    activated:function(){
      this.isSendMan = false;
      this.initAxios();
    },
    components:{Loadmore},
    methods:{
      loadTop:function() {
        this.initAxios();
        this.$refs.loadmore.onTopLoaded();
      },
      addPeople:function () {
        appMethod = this.getAddPeople;
        window.location.href = 'myapp://parameter?type=AddTeamMember&method=callbackAddPeople';
      },
      getAddPeople:function (data) {
        let phones = '',memeberNames='';
        for(let i=0;i<data.data.length;i++){
          phones += data.data[i].phone+',';
          memeberNames += data.data[i].name+',';
        }
        phones = phones.substring(0,phones.length-1);
        memeberNames = memeberNames.substring(0,memeberNames.length-1);
        let it = this;
        let groupId = this.$route.query.groupId;
        let groupName = this.$route.query.groupName;
        Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        });
        this.$axios({
          method: 'post',
          url: '/group/addGroupMember',
          data:this.$qs.stringify({
            userId:it.loginUser.userId,
            userName:it.loginUser.name,
            groupName:groupName,
            groupId:groupId,
            memeberName:memeberNames,
            phone:phones
          })
        }).then(function (response) {
          Indicator.close();
          it.initAxios();
          let instance = Toast('添加完成');
          setTimeout(() => {
            instance.close();
          }, 2000);
        }).catch(function(){
          Indicator.close();
          throw new Error('网络异常');
        });
      },
      initAxios:function () {
        Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        });
        if(!(this.$route.query||this.$route.query.groupId)) {
          return;
        }
        let it = this;
        let groupId = this.$route.query.groupId;
        let groupName = this.$route.query.groupName;
        let noticeId = this.$route.query.noticeId;
        this.$axios({
          method: 'post',
          url: 'group/getGroupDetail',
          data:this.$qs.stringify({
            userId:it.loginUser.userId,
            groupId:groupId,
            groupName:groupName,
            noticeId:noticeId
          })
        }).then(function (response) {
          Indicator.close();
          it.teamTit = response.data.data.groupName;
          it.teamNum = response.data.data.memebers.length ;
          it.swipeData = [{}];
          for(let i=0;i<response.data.data.memebers.length;i++){
            let dt = response.data.data.memebers[i];
            if(dt.memeberJob==='发起人'){
              if(dt.memeberId===it.loginUser.userId){
                it.isSendMan = true;
              }
              it.swipeData[0] = dt;
              continue;
            }
            it.swipeData.push(dt)
          }
        }).catch(function(){
          Indicator.close();
          throw new Error('网络异常');
        });
      },
      delMemeber:function (idx,memeberId) {
        //团员删除
        let it = this;
        let groupId = this.$route.query.groupId;
        MessageBox.confirm('确定删除联系人?').then(action => {
          Indicator.open({
            text: '数据删除中...',
            spinnerType: 'fading-circle'
          });
          this.$axios({
            method: 'post',
            url: '/group/deleteMemeber',
            data: this.$qs.stringify({
              userId: it.loginUser.userId,
              memeberId: memeberId,
              groupId:groupId
            })
          }).then(function (response) {
            Indicator.close();
            if (response.data.code >= 0) {
              it.initAxios();
            }
          }).catch(function(){
            Indicator.close();
            throw new Error('网络异常');
          });
        })
      },
      deleteGroup:function () {
        let it = this;
        let groupId = this.$route.query.groupId;
        MessageBox.confirm('确定解散团队?').then(action => {
          Indicator.open({
            text: '团队解散中...',
            spinnerType: 'fading-circle'
          });
          this.$axios({
            method: 'post',
            url: '/group/deleteGroup',
            data: this.$qs.stringify({
              userId: it.loginUser.userId,
              groupId:groupId
            })
          }).then(function (response) {
            Indicator.close();
            if (response.data.code >= 0) {
              it.$router.push('/teamManage/');
            }
          }).catch(function(){
            Indicator.close();
            throw new Error('网络异常');
          });
        });
      },
      exitGroup:function () {
        let it = this;
        let groupId = this.$route.query.groupId;
        MessageBox.confirm('确定退出团队?').then(action => {
          Indicator.open({
            text: '退出团队中...',
            spinnerType: 'fading-circle'
          });
          this.$axios({
            method: 'post',
            url: '/group/exitGroup',
            data: this.$qs.stringify({
              userId: it.loginUser.userId,
              groupId:groupId
            })
          }).then(function (response) {
            Indicator.close();
            if (response.data.code >= 0) {
              it.$router.push('/teamManage/');
            }
          }).catch(function(){
            Indicator.close();
            throw new Error('网络异常');
          });
        });

      },
      backTeam:function(){
//        this.$router.push('/teamManage/');
        this.$router.goBack();
      },
      saveTeam:function () {
        if(this.teamTit === ''){
          this.initAxios();
          let instance = Toast('请填写团队名称');
          setTimeout(() => {
            instance.close();
          }, 2000);
          return;
        }
        Indicator.open({
          text: '团队修改中...',
          spinnerType: 'fading-circle'
        });
        let it = this;
        let groupId = this.$route.query.groupId;
        this.$axios({
          method: 'post',
          url: '/group/updateGroup',
          data:this.$qs.stringify({
            groupId:groupId,
            groupName:it.teamTit
          })
        }).then(function (response) {
          Indicator.close();
          if(response.data.code > 0){
            it.$router.push('/teamManage/');
          }else{
            let instance = Toast('系统错误');
            setTimeout(() => {
              instance.close();
            }, 2000);
          }
        }).catch(function(){
          Indicator.close();
          throw new Error('网络异常');
        });
      }
    }
  }
</script>




