<style scoped>
  .rg_body{
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    color: #fff;
    background-color: #f0f0f0 !important;
    margin:-1px 0 0 0;
    padding: 0;
  }
  .rgfooter, .rg_input, .mint-button--primary{
    width: 76%;
    margin: 0 auto;
  }
  .rg_input{
    margin: 0 auto 0 auto;
    position: relative;
  }
  .rg_input input{
    width: calc(100% - 0.24rem);
    margin-top: 0.12rem;
    height: 0.36rem;
    border-radius: 0.18rem;
    padding: 0 0.12rem;
    outline: none;
    border: solid 1px #bababa;
  }
  .rg_input input[name='check']{
    padding-right: 45%;
    width: calc(100% - 0.12rem - 45%);
  }
  .rg_input span{
    display: block;
    position: absolute;
    top: 0.19rem;
    right: 0.14rem;
    height: 0.2rem;
    border-radius: 0.15rem;
    line-height: 0.2rem;
    padding: 0.02rem 0.1rem;
    background:-webkit-gradient(linear, 0 0, right 0, from(rgba(255,183,10,0.8)), to(#ff6a01));
  }
  .rgfooter{
    margin: 0.2rem auto 0 auto;
    padding-left: 0.2rem;
    text-align: left;
    color: #bababa;
    font-size: 0.8em;
  }
  .rgfooter span:nth-child(2) a,.footer span:nth-child(2) a{
    color: #1d8ee6;
  }
  .mint-button--primary{
    margin-top: 0.3rem;
    background:-webkit-gradient(linear, 0 0, right 0, from(rgba(22,163,228,0.8)), to(#0f5bf5));
    height: 0.5rem;
    border-radius: 0.25rem;
  }
  .footer{
    margin: 0.2rem auto 0 auto;
    text-align: center;
    color: #bababa;
  }
  .companys{
    width: 101%;
    height: 30vh;
    position: absolute;
    top:0.52rem;
    left: 0;
    z-index: 2;
    border-radius: 5px;
    display: none;
    color: #000;
    overflow-y: scroll;
    text-align: left;
    padding-right: 10px;
  }
  .companys .mint-radiolist{
    margin-top: -7px;
  }
  .registerBody{
    height: calc(100vh);
    padding-top: 60px;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    overflow-x: hidden;
  }
</style>
<style>
  .register .companys .mint-radio{
    margin-right: -10px;
  }
  .getTime {
    background: #ccc !important;
  }
</style>
<template>
  <transition name="slide">
    <div class="rg_body register">
      <mt-header title="注册" fixed>
        <mt-button slot="left" icon="back" @click="closePage">登录</mt-button>
      </mt-header>
      <div class="registerBody">
        <div class="rg_input" style="margin-top: 8%;">
          <input autocomplete="off" v-model="user.name" placeholder="姓名">
        </div>
        <div class="rg_input">
          <input autocomplete="off" v-model="user.companyId" v-show="false">
          <input autocomplete="off" @keyup="keyUpInput($event)" @focus="onfocusInput($event)" v-model="user.companyName" placeholder="所在公司名称" id="companyName" @click="clickCompany">
          <input autocomplete="off" v-model="user.phoneNumber" type="number" placeholder="手机号">
          <div class="companys" id="companyId" @click="closeCompany">
            <mt-radio autocomplete="off"
              align="right"
              v-model="value"
              :options="options">
            </mt-radio>
          </div>
        </div>
        <div class="rg_input">
          <!--<span @click="getVerificationCode" v-show="getVCode">获取验证码</span>-->
          <!--<span class="getTime" v-show="!getVCode">重新获取({{getTime}}s)</span>-->
          <!--<input v-model="user.smsCode" placeholder="验证码" name="check">-->
          <input autocomplete="off" v-model="user.password" type="password" placeholder="密码">
          <input autocomplete="off" v-model="user.rePwd" type="password" placeholder="请再次输入密码">
        </div>
        <div class="rgfooter">
          <span>注册既代表你已同意</span>
          <span><a @click="serviceAgreement">服务协议</a></span>
        </div>
        <mt-button size="large" type="primary" @click="register">注&nbsp;&nbsp;&nbsp;&nbsp;册</mt-button>
        <div class="footer">
          <span>已有账号</span>
          <span><a @click="closePage">快速登录</a></span>
        </div>
      </div>
    </div>
  </transition>
</template>
<script>
  import { Toast } from 'mint-ui';
  import { Indicator } from 'mint-ui';
  export default {
    name: 'register',
    data () {
      return {
        user: {},
        value:"0",
        getVCode:true,
        getTime:60,
        timer:0,
        options:[],
        companyList:[]
      }
    },
    activated:function(){
      this.timer = 0;
      this.user.name = '';
      this.user.phoneNumber = '';
      this.user.password = '';
      this.user.rePwd = '';
      this.user.smsCode = '';
      this.queryCompanys();
    },
    watch:{
      value:function(val){
        if(val==='') return;
        this.user.companyName = this.options[val].label;
        this.user.companyId = this.options[val].companyId;
        document.getElementById('companyId').style.display = 'none';
      }
    },
    methods:{
      closePage:function(){
        this.$router.push('/login');
      },
      clickCompany:function(){
        this.options = [];
        document.getElementById('companyId').style.display = 'block';
      },
      closeCompany:function(){
        document.getElementById('companyId').style.display = 'none';
      },
      serviceAgreement:function(){
        this.$router.push('/my/serviceAgreement');
      },
      queryCompanys:function(){
        Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        });
        let _this = this;
        this.$axios({
          method: 'post',
          url: 'register/selectCompany'
        }).then(function (response) {
           Indicator.close();
           let opts = [];
           for(let i=0;i<response.data.data.length;i++){
             opts.push({
                value:i.toString(),
                label:response.data.data[i].companyName,
                companyId:response.data.data[i].companyId
             });
           }
           _this.companyList = opts;
        }).catch(function(){
          Indicator.close();
          throw new Error('网络异常');
        });
      },
      onfocusInput:function(e){
        this.user.companyId = '';
        document.getElementById('companyId').style.display = 'block';
        this.keyUpInput(e);
      },
      keyUpInput:function(e){
         let _this = this;
         _this.options = [];
         _this.user.companyId = '';
         if(_this.timer!==0) return;
         if(!e.target.value){
           _this.value = '';
           _this.user.companyName = '';
           return;
         }
         _this.timer = setTimeout(function(){
            clearTimeout(_this.timer);
            _this.timer = 0;
            _this.options = [];
            let idx = 0;
            _this.companyList.forEach(function(item){
                if(e.target.value&&item.label.indexOf(e.target.value)!=-1){
                  item.value = (idx++).toString();
                  _this.options.push(item);
                }
            });
            _this.$nextTick(function(){
               _this.value = '';
               setTimeout(function(){
                 var radios = document.getElementsByClassName('mint-radio-input');
                 if(radios.length==1){
                   radios[0].checked = 'checked';
                   e.target.value = _this.options[0].label;
                   _this.user.companyName = _this.options[0].label;
                   return;
                 }
                 for(let i=0;i<radios.length;i++){
                   if(radios[i].checked){
                     radios[i].checked = '';
                     return;
                   }
                 }
               },50);
            });
         },1000);
      },
      phoneNumValidate:function (num) {
        let tel =  /^[0-9]\d{5,10}$/;
        return tel.test(num);
      },
      getVerificationCode:function () {
        let _this = this;
        if (!_this.getVCode){ return }
        let phoneNumber = this.user.phoneNumber;

        if (!this.phoneNumValidate(phoneNumber)){
          let instance = Toast("请输入正确的手机号码");
          setTimeout(() => {
            instance.close();
          }, 1000);
          return false;
        }
        Indicator.open({
          text: '获取中...',
          spinnerType: 'fading-circle'
        });
        this.$axios({
          method: 'post',
          url: 'register/smsSend',
          data:{phoneNumber:phoneNumber}
        }).then(function (response) {
          Indicator.close();
          if(response.data.msg=='success'){
            console.log(response.data);
            let instance = Toast("短信已发送,请注意查收");
            _this.getVCode = false;
            let countDown = setInterval(() => {
                _this.getTime--;
            if(_this.getTime<1){
              clearInterval(countDown);
              _this.getVCode = true;
              _this.getTime = 60;
            }
          },1000);
            setTimeout(() => {
              instance.close();
            }, 1000);
          }else{
            let instance = Toast(response.data.msg);
            setTimeout(() => {
              instance.close();
            }, 1000);
          }
        }).catch(function(){
          Indicator.close();
          throw new Error('网络异常');
        });
      },
      register:function(){
        let tpThis = this;
        if (!tpThis.user.name){
          let instance = Toast("请输入用户名");
          setTimeout(() => {
            instance.close();
          }, 1000);
          return false;
        }
        if (!tpThis.user.companyId){
          let instance = Toast("请选择正确的公司");
          setTimeout(() => {
            instance.close();
          }, 1000);
          return false;
        }
        if (!this.phoneNumValidate(tpThis.user.phoneNumber)){
          let instance = Toast("请输入正确的手机号码");
          setTimeout(() => {
            instance.close();
        }, 1000);
          return false;
        }
//        if (!tpThis.user.smsCode){
//          let instance = Toast("请输入验证码");
//          setTimeout(() => {
//            instance.close();
//          }, 1000);
//          return false;
//        }
        if (!tpThis.user.password){
          let instance = Toast("请输入密码");
          setTimeout(() => {
            instance.close();
        }, 1000);
          return false;
        }
        if (!tpThis.user.rePwd){
          let instance = Toast("请再次输入密码");
          setTimeout(() => {
            instance.close();
        }, 1000);
          return false;
        }
        if (tpThis.user.password != tpThis.user.rePwd){
          let instance = Toast("俩次输入的密码不一致");
          setTimeout(() => {
            instance.close();
        }, 1000);
          return false;
        }
        Indicator.open({
          text: '注册中...',
          spinnerType: 'fading-circle'
        });
        this.$axios({
          method: 'post',
          url: 'register/insertRegister',
          data:this.$qs.stringify(this.user)
        }).then(function (response) {
          Indicator.close();
          if(response.data.msg=='success'){
            let instance = Toast("注册成功");
            setTimeout(() => {
              instance.close();
            tpThis.closePage();
          }, 1000);
          }else{
            let instance = Toast(response.data.msg);
            setTimeout(() => {
              instance.close();
          }, 2000);
          }
        }).catch(function(){
          Indicator.close();
          throw new Error('网络异常');
        });
      }
    }
  }
</script>




