<style>
  .tdxc .mint-cell-title {
    text-align: left
  }

  .tdxc .mint-field-core {
    text-align: right
  }
  .tdxc .mint-cell-value span{
    margin-right:0;
  }
  .tdxc .mint-cell-value{
    max-width:80%;
  }
  .tdxc .is-default{
    display: none;
  }
  .tdxc input[type='date']{
    background: #ffffff;
  }
</style>
<template>
  <!--<transition name="slide">-->
      <div class="tdxc">
        <mt-header title="行程查看">
          <mt-button icon="back" slot="left" @click="backPage">返回</mt-button>
          <mt-button slot="right" v-if="(xcInfo.fuserId==userId&&xcInfo.fshared==0)||(xcInfo.fuserId==userId&&xcInfo.fshared==2)" @click="delXc">删除</mt-button>
        </mt-header>

        <div class="tdxcContent">
          <mt-field label="行程名称" disabled="disabled" v-model="xcInfo.fname"></mt-field>
          <mt-field v-if="reshushow" label="出行人数" type="number" disabled="disabled"
                    v-model="xcInfo.fpersionNum"></mt-field>
          <div class="mint-cell" ><span class="mint-cell-mask"></span>
            <div class="mint-cell-left"></div>
            <div class="mint-cell-wrapper">
              <div class="mint-cell-title"><!---->
                <div><i class="indexicon icon-indicator"></i> <span>出发城市</span></div>
              </div>
              <div class="mint-cell-value is-link">{{xcInfo.fcityFrom}}</div>
            </div>
            <div class="mint-cell-right"></div>
          </div>
          <div class="mint-cell" ><span class="mint-cell-mask"></span>
            <div class="mint-cell-left"></div>
            <div class="mint-cell-wrapper">
              <div class="mint-cell-title"><!---->
                <div><i class="indexicon icon-indicator"></i> <span>终点城市</span></div>
              </div>
              <div class="mint-cell-value is-link">{{xcInfo.fcityTo}}</div>
            </div>
            <div class="mint-cell-right"></div>
          </div>
          <div class="date cfDate">
            <span>出发日期</span><input disabled="disabled" v-model="xcInfo.fstartAt" type="text">
          </div>
          <div class="date fhDate">
            <span>返程日期</span><input disabled="disabled" v-model="xcInfo.fbackAt" type="text">
          </div>
          <div class="xcap-module">
            <div class="xingchenganpaiTitle">行程安排</div>
            <div class="jiluxc">
              <div class="jiluxc-title">记录每日行程安排</div>
              <div class="jiluxc-content">
                <ul class="jiluxc-contentC">
                  <!--<li @click="xcDetailFn(item.index)" v-for="item in xcapArr">-->
                    <!--<div class="djt">-->
                      <!--第{{item.index}}天-->
                    <!--</div>-->
                    <!--<div class="riqi">-->
                      <!--{{item.day}} {{item.week | fileterweek}}-->
                    <!--</div>-->
                  <!--</li>-->
                  <li @click="xcDetailFn(idx)" v-for="(itemDay,idx) in days">
                    <div class="djt">
                      第{{idx+1}}天
                    </div>
                    <div class="riqi">
                      {{itemDay.day}} - {{itemDay.week|fileterweek}}
                    </div>
                  </li>
                </ul>
              </div>
            </div>
            <div class="scxc">
              <div class="scxc-title">行程表单照片</div>
              <div class="scxc-content">
                <div class="xcImages">
                  <div class="item" @click="lookImg(idx)" v-for="(image,idx) in imageList.minImages" v-bind:style="{backgroundImage: 'url('+image+')'}"></div>
                </div>
              </div>

            </div>
          </div>
        </div>
        <div v-if="tuandui" class="scxc-btn">
          <button @click="xcchexiao" class="bt1" v-if="xcInfo.fuserId==userId&&xcInfo.fshared==1">行程撤销</button>
          <button @click="xcfxFn" class="bt1" v-if="(xcInfo.fuserId==userId&&xcInfo.fshared==0)||(xcInfo.fuserId==userId&&xcInfo.fshared==2)">行程分享</button>
          <button @click="xcxgFn" class="bt2" :class="(xcInfo.fshared!=0&&xcInfo.fshared!=1&&xcInfo.fshared!=2)?'width-all':false" v-if="xcInfo.fuserId==userId">行程修改</button>
        </div>
        <div v-if="geren" class="scxc-btn">
          <button @click="xcxgFn" class="bt3">行程修改</button>
        </div>
        <div v-if="lishi" class="scxc-btn">
          <button @click="xclxFn" class="bt4">行程路线</button>
        </div>
        <shared1 v-if="shared1"  v-on:fxEvent="fxMessage" v-on:qxEvent="qxMessage"></shared1>
      </div>
    <!--</transition>-->
</template>
<script>
  import {LoginUser} from '@/common/js/common';
  import shared1 from '../shared1/shared1'
  import { Toast } from 'mint-ui';
  import { MessageBox } from 'mint-ui';
  import { Indicator } from 'mint-ui';
  export default {
    name: 'xingchengchakan',
    data () {
      return {
        shared1:false,
        userId:LoginUser.getUser().userId,
        xcInfo:{},
        days:[],
        reshushow:false,
        tuandui:false,
        geren:false,
        lishi:false,
        Trast:false,//消息-行程
        xctx:false,
        fid:'',
        isMsg:false,
        imageList:{
          minImages:[],
          maxImages:[]
        }
      }
    },
    components:{
      shared1
    },
    activated: function () {
      let routerInfo = JSON.parse(localStorage.getItem("routerInfo"));
      if(!routerInfo) return;
      if(routerInfo.name=="dayDetailchakan" ||
        routerInfo.name=="xingchengxiugaiback"){
        return;
      }
      let url = '';
      if(routerInfo.name=='xchengtixingDetail'){  // 从行程提醒详情页面跳转过来
        this.clearAll();
        this.xctx=true;
        let ftype=routerInfo.ftype;
        if(ftype=="团队行程"){
          url="ttravel/selectTtravel";
        }else if(ftype=="个人行程"){
          url="ttravel/selectTtravelGe";
        }
        this.fid=routerInfo.ftravelId;
      }
      if(routerInfo.name=="xingchengxiugaibaocun"){     // 从修改页面点击保存过来
           this.clearAll();
           if(this.tuandui==true){
             this.reshushow=true;
             url="ttravel/selectTtravel";
             this.tuandui=true;
             this.geren=false;
             this.lishi=false;
           }else{
             url="ttravel/selectTtravelGe";
             this.reshushow=false;
             this.tuandui=false;
             this.geren=true;
             this.lishi=false;
           }
      }else if(routerInfo.name=="tuanduiList"){   // 从团队行程列表页跳转过来
        this.fid=routerInfo.fid;
        url="ttravel/selectTtravel";
        this.tuandui=true;
        this.geren=false;
        this.lishi=false;
        this.reshushow=true;
        this.clearAll();
      }else if(routerInfo.name=="gerenList"){    // 从个人行程列表页跳转过来
        url="ttravel/selectTtravelGe";
        this.fid=routerInfo.fid;
        this.tuandui=false;
        this.geren=true;
        this.lishi=false;
        this.reshushow=false;
        this.clearAll();
      }else if(routerInfo.name=="lsgerenList"){  // 从历史个人行程列表页跳转过来
        url="ttravel/selectTtravelGe";
        this.fid=routerInfo.fid;
        this.tuandui=false;
        this.geren=false;
        this.lishi=true;
        this.reshushow=false;
        this.clearAll();
      }else if(routerInfo.name=="lstuanduiList"){  // 从历史团队行程列表页跳转过来
        url="ttravel/selectTtravel";
        this.fid=routerInfo.fid;
        this.tuandui=false;
        this.geren=false;
        this.lishi=true;
        this.reshushow=true;
        this.clearAll();
      }
      this.queryInfo(url);
    },
    methods: {
      backPage: function () {
        this.$router.goBack();
      },
      xcchexiao(){
        let _this = this;
        MessageBox.confirm('确定撤销当前行程?').then(function () {
          Indicator.open({
            text: '撤销中...',
            spinnerType: 'fading-circle'
          });
          _this.$axios({
            method: 'post',
            url: 'ttravel/updatestatus2',
            data: _this.$qs.stringify({
              userId: LoginUser.getUser().userId,
              fid: _this.fid
            })
          }).then(function (response) {
            Indicator.close();
            if (response.data.msg == 'success') {
              _this.xcInfo.fshared=2;
              let instance = Toast('撤销成功');
              setTimeout(function () {
                instance.close();
              }, 2000);
            }
          }).catch(function(){
            Indicator.close();
            throw new Error('网络异常');
          });
        });
      },
      queryInfo: function (url) {
        Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        });
        let _this = this;
        this.$axios({
            method: 'post',
            url: url,
            data:this.$qs.stringify({
              fid:this.fid,
              userId:this.userId
            })
        }).then(function (res) {
            Indicator.close();
            if(res.data.msg=='success'||res.data.data){
               _this.xcInfo = res.data.data;
              if(_this.xcInfo.listPhoto.length){
                  _this.xcInfo.listPhoto.forEach(function(item){
                  _this.imageList.maxImages.push(item.sourcePhotoURL);
                  _this.imageList.minImages.push(item.thumbnailURL);
                });
              }
               _this.initDate();
            }
        }).catch(function(error){
          Indicator.close();
          throw new Error('网络异常');
        })
      },
      lookImg:function(idx){
        let data = {
          ImagesUrlArray:this.imageList.maxImages,
          ImageIndex:idx
        };
        window.location.href = 'myapp://parameter?type=ShowImage&imageData='+JSON.stringify(data);
      },
      deXcFn:function(){
        Indicator.open({
          text: '删除中...',
          spinnerType: 'fading-circle'
        });
        let _this=this;
        this.$axios({
          method: 'post',
          url: 'ttravel/deleteTravel',
          data:this.$qs.stringify({
            userId:_this.userId,
            fid:_this.fid
          })
        }).then(function (res) {
          Indicator.close();
          let instance = Toast('删除成功');
          setTimeout(() => {
            instance.close();
            _this.backPage();
          }, 2000);
        }).catch(function(error){
          Indicator.close();
          throw new Error('网络异常');
        })
      },
      delXc:function(){
        MessageBox.confirm('确定删除当前行程?').then(action => {
          this.deXcFn();
        });
      },
      clearAll:function(){
        this.imageList.maxImages=[];
        this.imageList.minImages=[];
        this.xcInfo.fname="";
        this.xcInfo.fpersionNum="";
        this.xcInfo.fcityFrom="";
        this.xcInfo.fcityTo="";
        this.xcInfo.fstartAt="";
        this.xcInfo.fbackAt="";
        this.xcInfo.listTravelSub=[];
        this.xcInfo.listPhoto=[];
      },
      xcDetailFn: function (index) {
        let dayInfo=this.xcInfo.listTravelSub[index];
        this.$router.push({name:"dayDetailchakan",params:{dayInfo:dayInfo,index:index}});
      },
      xcxgFn:function(){
        if(this.tuandui==true){
          this.$router.push({name:"xcInfoMod",params:{name:"tuandui",fid:this.fid}});
        }
        if(this.geren==true){
          this.$router.push({name:"xcInfoMod",params:{name:"geren",fid:this.fid}});
        }
      },
      initDate: function () {
         this.days = []
        var _this = this;
        if (!_this.xcInfo.fstartAt || !_this.xcInfo.fbackAt) {
          this.days = []
        }
        let stime = new Date(_this.xcInfo.fbackAt).getTime() - new Date(_this.xcInfo.fstartAt).getTime();
        let dayNums = stime / (24 * 60 * 60 * 1000);
        for (var i = 0; i <= dayNums; i++) {
          var itemDate = new Date(_this.xcInfo.fstartAt);
          itemDate.setDate(itemDate.getDate(_this.xcInfo.fstartAt) + i);
          var objDate = {
            day: (itemDate.getMonth() + 1) + "/" + itemDate.getDate(),
            week: itemDate.getDay()
          };
          this.days.push(objDate);
        }
      },
      xcfxFn:function(){
        this.shared1=true;
      },
      xclxFn(){
        let instance = Toast('暂无行程路线');
        setTimeout(() => {
           instance.close();
        }, 2000);
      },
      fxMessage(groupObj){
        this.shared1=false;
        Indicator.open({
          text: '分享中...',
          spinnerType: 'fading-circle'
        });
        let _this = this;
        this.$axios({
          method: 'post',
          url: 'ttravel/insertTtravelShared',
          data:this.$qs.stringify({
            userId:LoginUser.getUser().userId,
            ftravelId:this.fid,
            groupId:groupObj.groupId
          })
        }).then(function(response){
          Indicator.close();
          if(response.data.msg=='success'){
            _this.xcInfo.fshared=1;
            let instance = Toast('分享成功');
            setTimeout(() => {
              instance.close();
            }, 2000);
          }
        }).catch(function(){
          Indicator.close();
          throw new Error('网络异常');
        })
      },
      qxMessage(){
        this.shared1=false;
      }
    },
    filters: {
      fileterweek: function (value) {
        var newweek;
        switch (value) {
          case 1:
            newweek = "周一";
            break;
          case 2:
            newweek = "周二";
            break;
          case 3:
            newweek = "周三";
            break;
          case 4:
            newweek = "周四";
            break;
          case 5:
            newweek = "周五";
            break;
          case 6:
            newweek = "周六";
            break;
          case 0:
            newweek = "周日";
            break;
        }
        return newweek;
      }
    }
  }
</script>

<style scoped>
  .mt {
    width: 100%;
  }

  .xingchenganpaiTitle {
    padding-top: .07rem;
    line-height: .26rem;
    font-size: .14rem;
    color: #666666;
    text-align: left;
    padding-left: .1rem;
    background: #f0f0f0;
  }

  .tdxc {
    height: 100%;
  }

  .tdxcContent {
    overflow-y: auto;
    height: calc(100vh - .62rem);
    background: #FFFFFF;
    -webkit-overflow-scrolling: touch;
    overflow-x: hidden;

  }
  .tdxcContent   .mint-cell-mask:active::after{
    opacity: 0;
  }
  .jiluxc-content {
    overflow: hidden;
    padding-bottom: .15rem;
  }

  .jiluxc-title {
    line-height: .42rem;
    font-size: .16rem;
    color: #666666;
    text-align: left;
  }

  .jiluxc {
    margin: 0 .1rem;
    border-bottom: .01rem solid #d9d9d9;
  }

  .jiluxc-content li {
    width: 30%;
    height: .6rem;
    float: left;
    box-sizing: border-box;
    border: .01rem solid #c4c4c4;
    margin-right: 5%;
    margin-bottom: .18rem;
    border-radius: .03rem;
    text-align: center;
  }

  .jiluxc-content li:nth-child(3n+0) {
    margin-right: 0;
  }

  .jiluxc-contentC .djt {
    line-height: .24rem;
    color: #5a5a5a;
    font-size: .14rem;
    margin-top: .06rem;

  }

  .jiluxc-contentC .riqi {
    line-height: .24rem;
    color: #2074e7;
    font-size: .14rem;
  }

  .scxc {
    padding: 0 .1rem .3rem .1rem;
    background: #FFFFFF;
  }

  .scxc-title {
    line-height: .55rem;
    text-align: left;
    color: #666666;
    font-size: .16rem;
  }

  .scxc-content {
    margin-bottom: .2rem;
    overflow: hidden;
  }

  .scxc-content li {
    width: 30%;
    height: .68rem;
    float: left;
    margin-right: 5%;
    margin-bottom: .18rem;
  }

  .scxc-content li:nth-child(3n+0) {
    margin-right: 0;
  }

  .scxc-content li img {
    width: 100%;
    height: 100%;
  }

  .btnStyle {
    width: 60%;
    height: .42rem;
    border-radius: .21rem;
    -moz-box-shadow: .01rem .13rem .21rem #c2e9ff;
    -webkit-box-shadow: .01rem .13rem .21rem #c2e9ff;
    box-shadow: .01rem .13rem .21rem #c2e9ff;
  }

  .scxc-btn {
    position: fixed;
    height: .5rem;
    width: 100%;
    bottom: 0;
    left: 0;
  }

  .scxc-btn button {
    display: inline-block;
    width: 50%;
    float: left;
    margin: 0;
    padding: 0;
    height: 100%;
    border: none;
    outline: none;
    color: #FFFFFF;
    font-size: .16rem;
    text-align: center;
    line-height: .5rem;
  }

  .scxc-btn button.bt1 {
    background: #169bd5;
  }

  .scxc-btn button.bt2 {
    background: #ff9966;

  }
  .scxc-btn button.bt3{
    width: 100%;
    background: #ff9966;

  }
  .scxc-btn button.bt4{
    width: 100%;
    background: #15a3e3;

  }
  .date {
    height: .44rem;
    line-height: .44rem;
    margin: 0 .1rem;
    background-image: linear-gradient(180deg, #d9d9d9, #d9d9d9 50%, transparent 50%);
    background-size: 120% 1px;
    background-repeat: no-repeat;
    background-position: top left;
    background-origin: content-box;

  }
  .date span {
    float: left;
    font-size: .16rem;
  }

  .date input {
    float: right;
    height: 100%;
    text-align: right;
    background: none;
    font-size: 0.16rem;
    /*color: #888;*/
  }

  input:focus {
    border-color: #66afe9;
    outline: 0;
    -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 8px rgba(102, 175, 233, .6);
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 8px rgba(102, 175, 233, .6);
  }
 .mint-cell-wrapper{
   padding:0 .1rem;
 }
  .xcImages{
    width: 98%;
    margin: 10px auto;
    text-align: left;
    font-size: 0;
  }
  .xcImages .item{
    height: 70px;
    width: 33.333333%;
    display: inline-block;
    border: 0.03rem solid #ffffff;
    box-sizing: border-box;
    background-size: cover;
    background-position:center center;
  }
  .scxc-btn button.width-all{
   width: 100%;
 }
</style>




