<style scoped>
  .msgBody{
    width: 100%;
    height: calc(100vh);
    text-align: left;
    background-color: #f0f0f0;
  }
  .mint-cell-value span{
    font-size: 0.9em;
  }
  .primaryBtn{
    width: 76%;
    position: absolute;
    bottom: 10%;
    left: 50%;
    margin-left: -38%;
  }
  .primaryBtn .mint-button{
    background-color: #1168f2;
    background:-webkit-gradient(linear, 0 0, right 0, from(#16a2e3), to(#0f5bf5));
    height: 0.5rem;
    border-radius: 0.25rem;
  }
  .info{
     padding: 0.2rem 0.15rem;
     line-height: 0.2rem;
  }
  .msgBody .box{
     padding: 0 0.15rem 0.2rem 0.15rem;
  }
  .btns{
     margin-bottom: 0.2rem;
     height: 30vh;
  }
  .btns .active{
     background-color: #48c475;
     color: #fff;
     border: transparent;
     background:-webkit-gradient(linear, 0 50%, right 0, from(rgba(107,213,118,0.8)), to(#0da873));
  }
  .msgInfo span{
     display: block;
     line-height: 0.3rem;
  }
  .msgInfo textarea{
     width: 100%;
     height: 30vh;
     box-sizing: border-box;
     border-radius: 0.03rem;
     outline: none;
     border-color: #ccc;
     padding: 10px;
     color: #666;
     font-size: 0.16rem;
     opacity: 0.8;
  }
  .msgInfo span{
     font-size: 0.18rem;
     line-height: 0.3rem;
     font-weight: 600;
  }
  .noSubmit {
    background: #ccc !important;
    background-color: #ccc !important;
  }
  .noSubmit:active{
    background: #ccc !important;
    background-color: #ccc !important;
  }
</style>
<template>
  <!--<transition name="slide">-->
  <div class="msgBody">
    <mt-header title="建议反馈">
      <mt-button slot="left" icon="back" @click="closePage">返回</mt-button>
    </mt-header>
    <div class="info">亲爱的用户，感谢您的反馈，您的每次反馈都是我们前进的动力。</div>
    <div class="box">
        <div class="msgInfo">
           <span>详细问题描述</span>
           <textarea  placeholder="请输入您的反馈意见（300字以内）" @input="btnBg" v-model="content"></textarea>
        </div>
    </div>
    <div class="primaryBtn">
      <mt-button type="primary" :class="{noSubmit:noSubmit}" size="large" @click="subVeri()">提交</mt-button>
    </div>
  </div>
    <!--</transition>-->
</template>
<script>
  import {LoginUser} from '@/common/js/common';
  import { Toast } from 'mint-ui';
  import { Indicator } from 'mint-ui';
  export default {
    name: 'suggestion',
    data () {
      return {
        content:'',
        noSubmit:true
      }
    },
    activated:function(){
      this.content = '';
      this.noSubmit = true;
    },
    methods:{
      closePage:function(){
        this.$router.goBack();
      },
      btnBg:function(){
        if(this.content.length > 0 && this.content.length <= 300){
          this.noSubmit = false;
        }else {
          this.noSubmit = true;
        }
      },
      subSuggest:function(content){
        Indicator.open({
          text: '提交中...',
          spinnerType: 'fading-circle'
        });
        let thisTemp = this;
        this.$axios({
           method: 'post',
           url: 'feeback/insertFeedback',
           data:this.$qs.stringify({userId:LoginUser.getUser().userId,questionDetails:content})
        }).then(function (response) {
           Indicator.close();
           if(response.data.msg=='success'){
             thisTemp.content = '';
             let instance = Toast('提交成功');
             setTimeout(() => {
               instance.close();
               thisTemp.closePage();
             }, 1000);
           }
        }).catch(function(){
          Indicator.close();
          let instance = Toast('提交失败');
          setTimeout(() => {
            instance.close();
          }, 1000);
        });
      },
      subVeri:function(){

        if (this.noSubmit){
          return false;
        }

        let content = this.content.replace(/(^\s*)|(\s*$)/g,"");
        if (!content){
          let instance = Toast('内容不能为空');
          setTimeout(() => {
            instance.close();
        }, 1000);
          return false;
        }
        if (content.length>300){
          let instance = Toast('超出字数限制');
          setTimeout(() => {
            instance.close();
        }, 1000);
          return false;
        }
        this.subSuggest(content);
      },
      selectBtn:function(evt){
         let ele  = evt.target;
         let classStr = ele.getAttribute('class');
         if(classStr.indexOf('active')!=-1){
           classStr = classStr.replace('active','');
         }else{
           classStr = classStr + ' active';
         }
         ele.setAttribute('class',classStr);
      }
    }
  }
</script>




