<style scoped>
  .teamBox{text-align: left;background: #f0f0f0;height:100%;overflow:hidden;}
  .tabTit{background:#fff;}
  .tabCont{margin-top:10px;background:#fff;height:calc(100vh - 116px - 0.6rem);overflow-y: auto;-webkit-overflow-scrolling: touch;overflow-x: hidden;}


</style>
<style>
  .mint-header{
    background:linear-gradient(to right,#16a2e3 0%,#0f5bf5 100%);
  }
  .teamBox .mint-button-text i{font-size:.24rem;margin:0 .05rem;}
  .teamList .mint-cell-title .mint-cell-text{font-weight: bold;}
  .contactList .mint-cell-title{padding: .14rem 0 .14rem .36rem!important;position: relative;}
  .contactList .mint-cell-title .avatar{top:.22rem;left:0;position: absolute;font-size:.3rem;border-radius:50%;}
  .contactList .mint-cell-swipe-button{line-height:62px;}
</style>
<template>
  <div class="teamBox">
    <mt-header title="团队管理">
      <!--<router-link slot="left">-->
      <!--&lt;!&ndash;<mt-button icon="back">返回</mt-button>&ndash;&gt;-->
      <!--</router-link>-->
      <!--<mt-button slot="right">
        <i class="iconfont icon-sousuo"></i>
      </mt-button>-->
    </mt-header>

    <div class="tabTit">
      <mt-navbar v-model="selected">
        <mt-tab-item id="1" @click.native="clickRoute('/teamManage/',1)">团队</mt-tab-item>
        <mt-tab-item id="2" @click.native="clickRoute('/teamManage/tmContact',2)">通讯录</mt-tab-item>
      </mt-navbar>
    </div>
    <div class="tabCont">
      <router-view></router-view>
      <!-- tab-container -->
      <mt-tab-container v-model="selected" style="display: none;">
        <mt-tab-container-item id="1"></mt-tab-container-item>
        <mt-tab-container-item id="2"></mt-tab-container-item>
      </mt-tab-container>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'teamManage',
    data () {
      return {
        msg: '团队',
        selected:'1'
      }
    },
    created:function () {
      tmSetTab = this.tmSetTab;
    },
    methods:{
      tmSetTab:function (idx) {
        this.selected = idx.toString();
      },
      clickRoute:function(url,idx){
        this.$router.push(url+'?idx='+idx);
      }
    }
  }
</script>




