<style scoped>
  .teamBox{text-align: left;background: #f0f0f0;height:100%;overflow-y:auto;}
  .headBox{height:40px;}
  .peopleAddBox{}
.peopleAddSeach{
  height:30px;padding:5px 10px 10px 10px;background: #fff;
}
.peopleAddSeach input{
  height:28px;border:1px solid #dfdfdf;line-height:28px;width:99%;text-align: center;background:url("./seachBg.png") no-repeat center;background-size:37px auto;
}
  .peopleAddSeach input.bgNone{background: none;}
  .peopleAddList{margin-top:10px;}
.mr10{margin-right:.1rem;}
</style>
<style>
  .peopleAddList .mint-cell-title{padding: .14rem 0 .14rem .36rem!important;position: relative;}
  .peopleAddList .mint-cell-title .avatar{top:.22rem;left:0;position: absolute;font-size:.3rem;}
  .peopleAddList .mint-cell-value{padding-right:25px;display:none;}
  .peopleAddList .mint-cell-value .iconfont{font-size:.3rem;color:#0e82e8;}
  .peopleAddList .active .mint-cell-value{display:block;}
</style>
<template>
  <div class="teamBox">
    <div class="headBox">
      <mt-header fixed title="添加成员">
        <mt-button  slot="left" icon="back" @click="backTeamAdd">返回</mt-button>
        <mt-button slot="right">确定（5人）</mt-button>
      </mt-header>
    </div>

    <div class="peopleAddBox">
      <div class="peopleAddSeach">
        <input type="search" onfocus="this.className='bgNone'" onblur="if (value ==''){this.className=''}">
      </div>
      <div class="peopleAddList">
        <mt-index-list :height=peopleAddListH>
          <mt-index-section index="A">

              <mt-cell v-for="i in item.A" v-bind:class="{'active':i.isActive}" @click.native="isActive(i)" :title=i.title :label=i.label>
                <img slot="icon" src="./img_03.png" class="avatar" width="24" height="24">
                <i class="iconfont icon-duihao"></i>
              </mt-cell>

          </mt-index-section>
          <mt-index-section index="B">
            <div v-for="i in item.B" v-bind:class="{'active':i.isActive}" @click="isActive(i)">
              <mt-cell :title=i.title :label=i.label>
                <img slot="icon" src="./img_03.png" class="avatar" width="24" height="24">
                <i class="iconfont icon-duihao"></i>
              </mt-cell>
            </div>
          </mt-index-section>
          <mt-index-section index="C">
            <div v-for="i in item.C" v-bind:class="{'active':i.isActive}" @click="isActive(i)">
              <mt-cell :title=i.title :label=i.label>
                <img slot="icon" src="./img_03.png" class="avatar" width="24" height="24">
                <i class="iconfont icon-duihao"></i>
              </mt-cell>
            </div>
          </mt-index-section>


        </mt-index-list>
      </div>
    </div>

  </div>

</template>
<script>
  export default {
    name: 'peopleAdd',
    data () {
      return {
        msg: '添加成员',
        peopleAddListH:window.screen.height - 95,
        item:{
          A:[
            {
              title:'阿大',
              label:'13123456789'
            },
            {
              title:'阿二',
              label:'13333333333'
            }
          ],
          B:[
            {
              title:'宝宝',
              label:'13098765432'
            },
            {
              title:'宝贝',
              label:'13111111111'
            }
          ],
          C:[]
        }
      }
    },
    mounted(){

    },
    methods:{
      isActive:function (i) {
        if(typeof i.isActive == 'undefined'){
            this.$set(i,"isActive",true);
        }else{
          i.isActive = !i.isActive
        }
      },
      backTeamAdd:function(){
        this.$emit('backTeamAdd');
      }
    }
  }
</script>




