<style>
.tdxc .mint-cell-title {
    text-align: left;
  font-size: .16rem;
  }

.tdxc .mint-field-core {
    text-align: right;
  font-size: .16rem;
  }
.tdxc .mint-cell-value span{
    margin-right: 20px;
  }
.tdxc .mint-cell-value{
    max-width:80%;
  font-size: .16rem;
  }
.tdxc .is-default{
  display: none;
}
  input[type='date']{
    background: #ffffff;
  }
  .tdxc .add-xc-box{
     height: 100%;
     margin-bottom: 0.5rem;
  }
.mint-cell-wrapper {
  background-size: 100% 1px;
}
  .tdxc-input input{
     margin-right: 0.08rem;
     color: #888;
  }
</style>
<template>
      <div class="tdxc">
        <mt-header title="添加行程">
          <mt-button icon="back" slot="left" @click="backPage()">返回</mt-button>
          <mt-button slot="right" @click="finshXc()">
            保存
          </mt-button>
        </mt-header>
        <div class="tdxcContent">
          <div class="add-xc-box">
            <mt-field label="行程名称" class="tdxc-input" placeholder="填写" v-model="xcInfo.fname"></mt-field>
            <mt-field v-if="addType=='tuandui'" class="tdxc-input" label="出行人数"  placeholder="填写"  v-model="xcInfo.fpersionNum"></mt-field>
            <div class="mint-cell" @click="selectCity(0)"><span class="mint-cell-mask"></span>
              <div class="mint-cell-left"></div>
              <div class="mint-cell-wrapper">
                <div class="mint-cell-title"><!---->
                  <div><i class="indexicon icon-indicator"></i> <span>出发城市</span></div>
                </div>
                <div class="mint-cell-value is-link"><span>{{xcInfo.fcityFrom}}</span></div>
              </div>
              <div class="mint-cell-right"></div>
              <i  class="mint-cell-allow-right"></i>
            </div>
            <div class="mint-cell" @click="selectCity(1)"><span class="mint-cell-mask"></span>
              <div class="mint-cell-left"></div>
              <div class="mint-cell-wrapper">
                <div class="mint-cell-title"><!---->
                  <div><i class="indexicon icon-indicator"></i> <span>终点城市</span></div>
                </div>
                <div class="mint-cell-value is-link"><span>{{xcInfo.fcityTo}}</span></div>
              </div>
              <div class="mint-cell-right"></div>
              <i  class="mint-cell-allow-right"></i>
            </div>

            <div class="date cfDate mint-cell">
              <span>出发日期</span><input  v-model="cfDate" :min="todayDate"  type="date">
              <i  class="mint-cell-allow-right"></i>
              <div class="dateBox">{{cfDate}}</div>
            </div>
            <div class="date fhDate mint-cell">
              <span>返程日期</span><input  v-model="zdDate" :min="cfDate" type="date">
              <i  class="mint-cell-allow-right"></i>
              <div class="dateBox">{{zdDate}}</div>
            </div>
            <div class="xcap-module">
              <div class="xingchenganpaiTitle">行程安排</div>
              <div class="jiluxc">
                <div class="jiluxc-title">每日行程安排<span v-if="days.length>0" class="detailText">点击编辑详情</span></div>
                <div class="jiluxc-content">
                  <ul class="jiluxc-contentC">
                    <li @click="xcDetailFn(idx)" v-for="(itemDay,idx) in days" :class="(xcInfo.ttravelSub[idx].fcityTo=='')?false:'DaysActive'">
                      <div class="djt">
                        第{{idx+1}}天
                      </div>
                      <div class="riqi">
                        {{itemDay.day}} - {{itemDay.week|fileterweek}}
                      </div>
                    </li>
                  </ul>
                </div>
              </div>
              <div class="scxc">
                <div class="scxc-title">上传行程表单照片</div>
                <div class="scxc-content">
                  <div class="xcImages">
                    <div class="item" @click="lookImg(idx)" v-for="(image,idx) in imageList.minImages" v-bind:style="{backgroundImage: 'url('+image+')'}">
                      <i @click="deleImage($event,idx)" class="shanchu iconfont icon-shanchu"></i>
                    </div>
                    <div class="item up-img" v-if="imageList.minImages.length<9?true:false">
                      <i class="iconfont icon-camera" @click="openSheet"></i>
                      <span class="text">上传照片</span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="scxc-btn" v-if="this.addType=='tuandui'">
          <button @click="clickfengxiang">保存并分享</button>
        </div>
        <!--分行程添加-->
        <mt-actionsheet :actions="[
           {name:'拍照',method:() => selectCamera()},
           {name:'从相册中选择',method:() => selectPicture()}
         ]" v-model="sheetVisible">
        </mt-actionsheet>
        <shared1 v-if="shared1" v-on:fxEvent="fxMessage" v-on:qxEvent="qxMessage"></shared1>
      </div>
</template>
<script>
  import { Toast } from 'mint-ui';
  import { MessageBox } from 'mint-ui';
  import {LoginUser} from '@/common/js/common';
  import { Indicator } from 'mint-ui';
  import shared1 from '@/components/xcheng/shared1/shared1'
  export default {
    name: 'xingchengtianjia',
    data () {
      return {
        sheetVisible: false,
        days:[],
        shared1:false,
        imageList:{
          minImages:[],
          maxImages:[]
        },
        isDisabled:false,
        todayDate:'',
        dayIndex:0,
        cfDate:'',
        zdDate:'',
        addType:"",
        fid:'',
        groupObj:'',
        xcInfo:{
          userId:LoginUser.getUser().userId,
          fname:"",
          fpersionNum:"",
          fcityFrom:"",
          fcityTo:"",
          fstartAt:"",
          fbackAt:"",
          ttravelSub:[],
          photoNames:[]
        }
      }
    },
    activated:function(){
      this.isDisabled=false;
      this.dateValite();
      let dayIndex=this.dayIndex;
      if(this.$route.params.selected=="1"){      //选择团队列表跳转过来
        this.addType="tuandui";
        this.clearAll();
      }else if(this.$route.params.selected=="2"){        //选择个人列表跳转过来
        this.addType="geren";
        this.clearAll();
      }else if(this.$route.params.name=="Detailday"){     // 从子行程页面按保存按钮后跳转过来
        this.xcInfo.ttravelSub[dayIndex]=this.$route.params.dayAddObj;
      }else if(this.$route.params.name=="DetaildayBack"){    // 从子行程页面按返回按钮后跳转过来
        return;
      }
      this.dealWithCity();
    },
    components:{
      shared1
    },
    methods: {
      baocunFn2(){
        if(this.isDisabled==true){return}  //防止重复保存
        this.isDisabled=true;
        Indicator.open({
          text: '提交中...',
          spinnerType: 'fading-circle'
        });
        let _this=this;
        this.$axios({
          method: 'post',
          url:'ttravel/insertTtravel',
          data:_this.xcInfo
        }).then(function (response) {
          Indicator.close();
          if(response.data.msg=='success'){
            _this.fid=response.data.data;
            _this.fengxiangFn();
          }else{
            let instance = Toast('操作失败');
            _this.isDisabled=false;
            setTimeout(() => {
              instance.close();
          }, 2000);
          }
        }).catch(function(){
          Indicator.close();
          throw new Error('网络异常');
        });
      },
      fengxiangFn(){
        Indicator.open({
          text: '分享中...',
          spinnerType: 'fading-circle'
        });
        let _this = this;
        this.$axios({
          method: 'post',
          url: 'ttravel/insertTtravelShared',
          data: _this.$qs.stringify({
            userId: LoginUser.getUser().userId,
            ftravelId: _this.fid,
            groupId: _this.groupObj.groupId
          })
        }).then(function (response) {
          Indicator.close();
          if (response.data.msg == 'success') {
            let instance = Toast('分享成功');
            _this.shared1 = false;
            setTimeout(function () {
              instance.close();
              _this.$router.push('/xcheng');
            }, 2000);

          }else{
            let instance = Toast('操作失败');
            _this.isDisabled=false;
            setTimeout(() => {
              instance.close();
          }, 2000);
          }
        }).catch(function(){
          Indicator.close();
          throw new Error('网络异常');
        });
      },
      fxMessage(groupObj){
        this.groupObj=groupObj;
        this.baocunFn2();
      },
      qxMessage(){
        this.shared1 = false;
      },
      clickfengxiang:function(){
        let isValidity=this.verificationFn();
        if(isValidity==false){
          return;
        }
        this.shared1 = true;
      },
      backPage: function () {
        localStorage.removeItem('citys');//清除缓存数据
        MessageBox.confirm('确定放弃当前行程编辑?').then(action => {
          //this.$router.push('xcheng');
          this.$router.goBack();
        });
      },
      dateValite:function(){
        let _this=this;
        let newDate=new Date();
        let month=(newDate.getMonth() + 1)>=10?(newDate.getMonth() + 1):("0"+(newDate.getMonth() + 1));
        let date=newDate.getDate()>=10?newDate.getDate():("0"+newDate.getDate());
        setTimeout(function(){
          _this.todayDate=newDate.getFullYear() + "-" +month + "-" + date;
        },500)
      },
      toastFn:function(text){
        let instance = Toast(text);
        setTimeout(function () {
          instance.close();
        }, 1000);
      },
      selectCity:function(type){
        this.$router.push({name:'citySelect',query:{name:"tianjiaCity",cityType:type}});
      },
      dealWithCity:function(){//处理起始城市
         let xcCityInfo = JSON.parse(localStorage.getItem('xcCityInfo'));
         localStorage.removeItem('xcCityInfo');
         if(!xcCityInfo) return;
         if(xcCityInfo.cityType==0){//出发城市
           let citys = JSON.parse(localStorage.getItem('citys'));
           if(!citys){return}
           if((citys[citys.length-1].cityId)&&(citys[citys.length-1].cityId!="")){
             this.xcInfo.fcityFrom = citys[citys.length-1].name;
           }else if((citys[citys.length-1].cityId=="")&&(citys[citys.length-2].cityId=="")){
             this.xcInfo.fcityFrom = citys[citys.length-3].name;
           }else if((citys[citys.length-1].cityId=="")){
             this.xcInfo.fcityFrom = citys[citys.length-2].name;
           }
        }else if(xcCityInfo.cityType==1){//终点城市
           let citys = JSON.parse(localStorage.getItem('citys'));
           if(!citys){return}
           if((citys[citys.length-1].cityId)&&(citys[citys.length-1].cityId!="")){
             this.xcInfo.fcityTo = citys[citys.length-1].name;
           }else if((citys[citys.length-1].cityId=="")&&(citys[citys.length-2].cityId=="")){
             this.xcInfo.fcityTo = citys[citys.length-3].name;
           }else if((citys[citys.length-1].cityId=="")){
             this.xcInfo.fcityTo = citys[citys.length-2].name;
           }
        }
      },
      xcDetailFn:function(index){     //打开子行程
        let _this=this;
        this.dayIndex=index;
        let dayNum=index+1;
        this.$router.push({name:'xcDayAdd',params:{name:"tianjia",dayObj:_this.xcInfo.ttravelSub[index],dayNum:dayNum}});
      },
      initDate: function () {
        this.days = []
        this.xcInfo.ttravelSub=[]
        var _this = this;
        if (!_this.xcInfo.fstartAt || !_this.xcInfo.fbackAt) {
          this.days = [];
          _this.xcInfo.ttravelSub=[];
        }
        let stime = new Date(_this.xcInfo.fbackAt).getTime() - new Date(_this.xcInfo.fstartAt).getTime();
        let dayNums = stime / (24 * 60 * 60 * 1000);
        for (var i = 0; i <= dayNums; i++) {
          var itemDate = new Date(_this.xcInfo.fstartAt);
          itemDate.setDate(itemDate.getDate(_this.xcInfo.fstartAt) + i);
          var objDate = {
            day: (itemDate.getMonth() + 1) + "/" + itemDate.getDate(),
            week: itemDate.getDay()
          };
          _this.days.push(objDate);
          var newDate=itemDate.getFullYear()+'-'+(itemDate.getMonth()+1)+"-"+itemDate.getDate();
          var newObj={
            fcityTo:"",
            fassemblyAt:"",
            fhotel:"",
            fvehicle:"",
            fdesc:"",
            fcreateAt:newDate
          };
          _this.xcInfo.ttravelSub.push(newObj);
        }
      },
      openSheet: function () {
        this.sheetVisible = true;
      },
      selectPicture:function(){
        appMethod = this.insertImg;
        this.sheetVisible = false;
        let limitNum=9-this.imageList.minImages.length;
        window.location.href = "myapp://parameter?type=TakeAlbum&method=fileImg&imageNum="+limitNum;
      },
      selectCamera:function(){
        appMethod = this.insertImg;
        this.sheetVisible = false;
        let limitNum=9-this.imageList.minImages.length;
        window.location.href = "myapp://parameter?type=TakePhoto&method=fileImg&imageNum="+limitNum;
      },
      lookImg:function(idx){
        let data = {
          ImagesUrlArray:this.imageList.maxImages,
          ImageIndex:idx
        };
        window.location.href = 'myapp://parameter?type=ShowImage&imageData='+JSON.stringify(data);
      },
      insertImg:function(data){
        for(let i=0;i<data.data.length;i++){
          this.xcInfo.photoNames.push(data.data[i].photoName);
          this.imageList.minImages.push(data.data[i].thumbnailURL);
          this.imageList.maxImages.push(data.data[i].sourcePhotoURL);
        }
        appMethod = null;
      },
      verificationFn:function(){
        var xcInfo=this.xcInfo;
        if(xcInfo.fname==""){
           this.toastFn("请填写行程名称");
          return false;
        }
        if(xcInfo.fname.length>20){
          this.toastFn("行程名称不能超过20个字符");
          return false;
        }
        if(this.addType=='tuandui'&&xcInfo.fpersionNum==""){
          this.toastFn("请填写行程人数");
          return false;
        }
        if(xcInfo.fcityFrom==""){
          this.toastFn("请选择出发城市");
          return false;
        }
        if(xcInfo.fcityTo==""){
          this.toastFn("请选择终点城市");
          return false;
        }
        if(xcInfo.fstartAt==""){
          this.toastFn("请选择出发时间");
          return false;
        }
        if(xcInfo.fbackAt==""){
          this.toastFn("请选择返程时间");
          return false;
        }
//        let isEvery=this.checkEveryDayFn();
//        if(isEvery==false){
//          this.toastFn("请完善每日日程安排");
//          return false;
//        };

      },
      checkEveryDayFn:function(){
        let isWc;
        this.xcInfo.ttravelSub.forEach(function(item){
          if(item.fcityTo==""||item.fdesc==""){
            isWc=false;
            return;
          }
        });
        return isWc;
      },
      finshXc:function(){
        let isValidity=this.verificationFn();
        if(isValidity==false){return}
        if(this.isDisabled==true){return;}  //防止重复保存
        Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        });
        this.isDisabled=true;
          let thisTemp = this;
          let url = '';
          if(thisTemp.addType=="geren"){
            url = 'ttravel/insertTtravelGe';
          }else if(thisTemp.addType=="tuandui"){
             url = 'ttravel/insertTtravel';
          }
          this.$axios({
               method: 'post',
                 url: url,
                 data:this.xcInfo
            }).then(function (response) {
               Indicator.close();
               if(response.data.msg=='success'){
                 let instance = Toast('创建成功');
                 setTimeout(() => {
                    instance.close();
                    thisTemp.$router.push('/xcheng/plan');
                 }, 2000);
               }else{
                 let instance = Toast('操作失败');
                 thisTemp.isDisabled=false;
                 setTimeout(() => {
                   instance.close();
                  }, 2000);
               }
            }).catch(function(){
              Indicator.close();
              throw new Error('网络异常');
            });
          },
      clearAll:function(){
        this.days=[];
        this.cfDate="";
        this.zdDate="";
        this.imageList.maxImages=[];
        this.imageList.minImages=[];
        this.xcInfo.fname="";
        this.xcInfo.fpersionNum="";
        this.xcInfo.fcityFrom="";
        this.xcInfo.fcityTo="";
        this.xcInfo.fstartAt="";
        this.xcInfo.fbackAt="";
        this.xcInfo.ttravelSub=[];
        this.xcInfo.photoNames=[];
      },
      deleImage:function(e,index){
        e.stopPropagation();
        MessageBox.confirm('确定删除吗?').then(action => {
          this.imageList.minImages.splice(index,1);
        this.imageList.maxImages.splice(index,1);
        this.xcInfo.photoNames.splice(index,1);
      });


      }
    },
    watch: {
      cfDate:function(value) {
        this.xcInfo.fstartAt = value;
        this.xcInfo.ttravelSub=[];
        this.initDate();
      },
      zdDate:function(value) {
        this.xcInfo.fbackAt = value;
        this.xcInfo.ttravelSub=[];
        this.initDate();
      }
    },
    filters: {
      fileterweek: function (value) {
        var newweek;
        switch (value) {
          case 1:
            newweek= "周一";
            break;
          case 2:
            newweek=  "周二";
            break;
          case 3:
            newweek=  "周三";
            break;
          case 4:
            newweek=  "周四";
            break;
          case 5:
            newweek=  "周五";
            break;
          case 6:
            newweek= "周六";
            break;
          case 0:
            newweek= "周日";
            break;
        }
        return newweek;
      }
    }
  }
</script>

