    position: absolute;
    text-align: right;
    right: 0;
    top: 0;
    color: #888;
    padding-right: .2rem;
    box-sizing: border-box;
    z-index: 0;
    font-size: .16rem;
  }
  .date .mint-cell-allow-right::after{
    right: 10px;
  }
  .scxc-btn {
    position: fixed;
    height: .5rem;
    width: 100%;
    bottom: 0px;
    left: 0;
  }

  .scxc-btn button {
    display: inline-block;
    width: 100%;
    margin: 0;
    padding: 0;
    height: 100%;
    border: none;
    outline: none;
    color: #FFFFFF;
    background: #26a2ff;
    font-size: .16rem;
    text-align: center;
    line-height: .5rem;
  }
</style>




