<style scoped>
  .body{
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    color: #fff;
    background-color: #172b57;
    background-image: url("./login_bg.jpg");
    background-size: cover;
  }
  .wh100{
    width:100% !important;height:100% !important;
  }
  .logo{
    display: block;
    margin: 20% auto 10% auto;
    width: 24%;
  }
  .lgfooter{
    width: 80%;
    margin: 0.3rem auto 0 auto;
  }
  .lgfooter span{
    line-height: 0.2rem;
  }
  .lgfooter span:nth-child(1){
    float: right;
    margin-left: 0.14rem;
    color: #0c88e9;
    margin-top: -10px;
  }
  .mint-button{
    background-color: #1168f2;
    background:-webkit-gradient(linear, 0 0, right 0, from(#16a2e3), to(#0f5bf5));
  }
  .lgfooter .mint-button{
    width: 100% !important;
    margin-top: 6%;
    height: 0.5rem;
    border-radius: 0.25rem;
  }
  .red{
    width: 80%;
    color:red;
    opacity: 0.8;
    text-align: left;
    padding: 6px 0 10px 14px;
    display: block;
  }
  .lgfooter img{
    width: 80%;
    display: block;
    margin: 34% auto 0 auto;
  }
  .lgfooter .quickRegister a{
    display: block;
    text-align: center;
    color: #fff;
    margin-top: 0.2rem;
    opacity: 0.8;
  }
  .appName{
    display: block;
    margin-top: -6%;
    font-size: 0.2rem;
    color: #0f61f6;
    background-image: -webkit-gradient(linear, left 0, right 0, from(rgba(12, 137, 233, 0.9)), to(#0f61ff));
    -webkit-background-clip: text;
    /*-webkit-text-fill-color: transparent;*/
  }
  .input-box{
    width: 80%;
    margin: 0.3rem auto 0 auto;
    position: relative;
  }
  .input-box input{
    width: calc(100% - 0.6rem);
    display: inline-block;
    padding: 0.15rem 0.3rem;
    font-size: 0.16rem;
    background-color:rgba(73,86,120,0.5);
    border-radius: 0.28rem;
    color: #fff;
  }
  .input-box .iconfont{
    position: absolute;
    top: 0.17rem;
    right: 0.12rem;
  }
  .input-box input:focus + .iconfont{
    opacity: 1;
    display: inline-block;
  }
  .input-box input:focus{
    outline: none;
  }
</style>
<template>
  <div class="body">
    <img class="logo" src="./login_logo.png">
    <span class="appName">安易行</span>
    <div class="input-box">
      <input type="text" id="username" v-model="username" placeholder="请输入账号或手机号">
      <i class="iconfont icon-shanchu" @click="clear()"></i>
    </div>
    <div class="input-box">
      <input type="password" id="passwordId" v-model="password" placeholder="请输入密码">
      <i class="iconfont icon-biyan" id="eyeId0" @click="pwdType()"></i>
      <i class="iconfont icon-zhengyan1" id="eyeId1" @click="pwdType()"></i>
    </div>
    <div class="lgfooter">
      <router-link to='/forgetPwd'>
        <span>忘记密码?</span>
      </router-link>
      <mt-button type="primary" @click="sendLogin">登&nbsp;&nbsp;&nbsp;&nbsp;录</mt-button>
      <span class="quickRegister"><a @click="register">快速注册</a></span>
      <img src="./login_bottom.png">
    </div>
    <!--<mt-popup class="wh100" v-model="popup.BB" position="right">-->
    <!--<ForgetPwd ref="ForgetPwd" v-on:close="changePop"></ForgetPwd>-->
    <!--</mt-popup>-->
  </div>
</template>
<script>
  import ForgetPwd from '@/components/login/forgetPwd';
  import {LoginUser} from '@/common/js/common';
  import { Toast } from 'mint-ui';
  import { Indicator } from 'mint-ui';
  export default {
    name: 'login',
    data () {
      return {
        password:'',
        username:'',
        user: {username:'',password:''},
        error:{userInfo:'',pwdInfo:''},
        popup:{AA:false,BB:false}
      }
    },
    components:{ForgetPwd,Indicator},
    activated:function(){
      let userName = localStorage.getItem('userName');
      this.password = '';
      this.username = userName?userName:'';
      localStorage.clear();
      localStorage.setItem('userName',this.username);
    },
    methods:{
      clear:function(){
        document.getElementById('username').focus();
        this.username = '';
        localStorage.removeItem('userName');
      },
      pwdType:function(){
        let ipt = document.getElementById('passwordId');
        if(ipt.getAttribute('type')=='password'){
          ipt.setAttribute('type','text');
          document.getElementById('eyeId0').style.display = 'none';
          document.getElementById('eyeId1').style.display = 'block';
        }else if(ipt.getAttribute('type')=='text'){
          ipt.setAttribute('type','password');
          document.getElementById('eyeId0').style.display = 'block';
          document.getElementById('eyeId1').style.display = 'none';
        }
      },
      sendLogin:function(){
        if(this.username==''){
          let instance = Toast('用户名不能为空');
          setTimeout(() => {
            instance.close();
          }, 1000);
          return;
        }
        if(this.password==''){
          let instance = Toast('密码不能为空');
          setTimeout(() => {
            instance.close();
          }, 1000);
          return;
        }
        Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        });
        let stime = setTimeout(() => {
          let ist = Toast('当前网络不佳');
          Indicator.close();
        }, 10000);
        this.user = { username:this.username, password:this.password };
        let thisTemp = this;
        this.$axios({
          method: 'post',
          url: 'register/loginUser',
          data:this.$qs.stringify(this.user)
        }).then(function (response) {
          localStorage.setItem('loginUser',JSON.stringify(thisTemp.user));
          LoginUser.setUser(response.data.data);
          Indicator.close();
          clearTimeout(stime);
          if(response.data.msg!=="success"){
            let instance = Toast(response.data.msg);
            setTimeout(() => {
              instance.close();
            }, 1000);
          }else{
            /* 和服务端建立socket连接 */
            appSocket.init({userId:response.data.data.userId});
            //发送环信账号
            let userData = JSON.stringify({
              userID:response.data.data.userId,
              userName:response.data.data.name,
              phoneNo:response.data.data.phoneNumber,
              headImageUrl:response.data.data.url,
              shareId:response.data.data.h_username,
              password:response.data.data.h_password
            });
            window.location.href = 'myapp://parameter?type=GetInformation&userData='+userData;
            localStorage.setItem('userName',thisTemp.user.username);
            let instance = Toast('登录成功');
            setTimeout(() => {
              instance.close();
              thisTemp.$router.push('/home');
            }, 500);
          }
        }).catch(function(){
          Indicator.close();
          clearTimeout(stime);
          throw new Error('网络异常');
        });
      },
      changePop: function (val) {
        this.popup[val] = !this.popup[val];
      },
      forgetPwd: function(){

      },
      register:function(){
        this.$router.push('/register');
      }
    }
  }
</script>




