// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';
import router from './router/routerConfig';
import MintUI from 'mint-ui';
import axios from 'axios';
import qs from 'qs';
import webSocket from './common/js/websocket';
// const baseURL = 'http://202.106.10.250:8988/';
// webSocket.wsUrl = 'ws://202.106.10.250:8988/ws';
const baseURL = 'http://202.106.10.250:18899/';
webSocket.wsUrl = 'ws://202.106.10.250:18899/ws';
Vue.use(MintUI);
Vue.config.productionTip = false;
axios.defaults.baseURL = baseURL;

axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=utf-8';
axios.defaults.timeout = 30000;
Vue.prototype.$axios = axios;
Vue.prototype.$qs = qs;
Vue.prototype.baseURL = baseURL;
Vue.prototype.slideName = 'slide-left';
global.appSocket = webSocket;
//document.documentElement.style.fontSize = document.documentElement.clientWidth * 100 * window.devicePixelRatio / 750 + 'px';
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
});
appRouter = router;
// 添加响应拦截器
global.homeNeworkCount = 0;
let timerOut = null;
axios.interceptors.response.use(function (response) {
  return response;
}, function (error) {
  if(window.location.href.indexOf('#/home')!=-1&&window.location.href.indexOf('#/home/')==-1){
    //当前处于home页
    if(++homeNeworkCount>=2){
      return;
    }
  }
  if(timerOut) return;
  let netWork = document.getElementById('newWorkId');
  netWork.style.opacity = '1';
  netWork.style.display = 'block';
  timerOut = setTimeout(function(){
    clearTimeout(timerOut);
    timerOut = null;
    netWork.style.opacity = '0.1';
    let noneTime = setTimeout(function () {
      clearTimeout(noneTime);
      noneTime = null;
      netWork.style.display = 'none';
    },300);
  },2000);
  return Promise.reject(error);
});
Vue.filter('NoImg', function(value) {
  if(!value||value===''){//头像站位图片
    return 'data:image/png;base64,';
  }
  return value;
});
Vue.filter('NoData', function(value) {
  if(!value||value===''){
    return '无';
  }
  return value;
});
Vue.filter('FormartDate', function(value) {
  if(!value||value===''){
    return '';
  }
  if(value.length==10){
    return value;
  }
  var time = new Date(Date.parse(value.replace(/-/g, "/")));
  var newTime = new Date();
  if(time.getDay()==newTime.getDay()){
    return time.Format('hh:mm');
  }else if(time.getDay()+1==newTime.getDay()){
    return '昨日 '+time.Format('hh:mm');
  }else if(time.getDay()+2==newTime.getDay()){
    return '前天 '+time.Format('hh:mm');
  }
  return time.Format('yyyy-MM-dd');
});
Date.prototype.Format = function (fmt) { //author: meizz
  var o = {
    "M+": this.getMonth() + 1, //月份
    "d+": this.getDate(), //日
    "h+": this.getHours(), //小时
    "m+": this.getMinutes(), //分
    "s+": this.getSeconds(), //秒
    "q+": Math.floor((this.getMonth() + 3) / 3), //季度
    "S": this.getMilliseconds() //毫秒
  };
  if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
  for (var k in o)
    if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
  return fmt;
};
let timerSS = null;
Vue.directive("longtouch", function(el, binding) {
  var oDiv = el,startY=0,disTimeout=false;
  let dType=oDiv.getAttribute('dType');
  oDiv.addEventListener("touchstart",function(e) {

    if(e.touches.length > 1) {
      return false;
    }
    disTimeout = false;
    timerSS = setTimeout(function(){
      if(disTimeout){
        clearTimeout(timerSS);
        timerSS = null;
        return;
      }
      if(dType=="tuandui"){
        longTouchMethodT(e);
      }else if(dType=="geren"){
        longTouchMethodG(e);
      }else if(dType=="lstuandui"){
        longTouchMethodLT(e);
      }else if(dType=="lsgeren"){
        longTouchMethodLG(e);
      }
    },1800);
    startY = e.touches[0].clientY;
    // e.preventDefault();
  }, false);
  oDiv.addEventListener("touchmove", function(e) {
    disTimeout = Math.abs(startY-e.touches[0].clientY)>20;
  }, false);
  document.addEventListener("touchend", function(ev) {
    disTimeout=true;
    window.clearTimeout(timerSS);
    timerSS = null;
  }, false);

});
/*免登陆处理*/
let loginUser = JSON.parse(localStorage.getItem('loginUser'));
if(loginUser){
  axios({
    method: 'post',
    url: 'register/loginUser',
    data:qs.stringify(loginUser)
  }).then(function (response) {
    if(response.data.msg==='success'){
      localStorage.setItem('loginUserInfo',JSON.stringify(response.data.data));
      //发送环信账号
      let userData = JSON.stringify({
        userID:response.data.data.userId,
        userName:response.data.data.name,
        phoneNo:response.data.data.phoneNumber,
        headImageUrl:response.data.data.url,
        shareId:response.data.data.h_username,
        password:response.data.data.h_password
      });
      window.location.href = 'myapp://parameter?type=GetInformation&userData='+userData;
      /* 和服务端建立socket连接 */
      appSocket.init({userId:response.data.data.userId});
      router.push('/home');
    }else{
      router.push('/login');
    }
  }).catch(function(){
    router.push('/login');
  });

}else{
  router.push('/login');
}



