<style scoped>
  .rg_body{
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    color: #000;
    background-color: #fff;
    margin:-1px 0 0 0;
  }
  .content{
    padding: 0 0.2rem;
    margin-bottom: 0.2rem;
  }
  .content h1{
    font-size: 0.18rem;
    text-align: left;
    font-weight: 500;
  }
  .content .item{
    margin-top: 0.3rem;
  }
  .content .item div{
    margin-top: 0.1rem;
    text-align: left;
    line-height: 0.22rem;
    text-indent: 0.2rem;
    color: #888;
  }
</style>
<template>
  <div class="rg_body">
    <mt-header :title="tipInfo.title">
      <mt-button slot="left" icon="back" @click="backPage">返回</mt-button>
    </mt-header>
    <div class="hdBody">
      <div class="content">
        <div class="item" v-for="tip in tipInfo.list">
          <h1>{{tip.title}}</h1>
          <div v-html="tip.content"></div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'tipInfo',
    data () {
      return {
        tipInfo:{}
      }
    },
    activated:function(){
       this.tipInfo = JSON.parse(localStorage.getItem('tipInfo'));
    },
    methods:{
      backPage:function(){
        this.$router.goBack();
      }
    }
  }
</script>




