<style scoped>
  .msgBody{
    width: 100%;
    height: calc(100vh);
    text-align: left;
    background-color: #f0f0f0;
    overflow: hidden;
  }
  .msg .box{
    padding: 0.2rem 0.15rem;
    margin-top: 0.48rem;
    background-color: #fff;
    position: relative;
  }
  .box div:first-child{
    position: absolute;
    text-align: center;
    width: calc(100% - 0.3rem);
    top: -30px;
  }
  .box div:first-child label{
    display: inline-block;
    text-align: center;
    padding: 0 10px;
    border-radius: 3px;
    color: #fff;
    line-height: 20px;
    background-color: #c5c5c5;
  }
  .icon-dingwei{
     color: #8a68d7;
     margin-right: 0.01rem;
  }

</style>
<template>
  <transition name="slide">
  <div class="msgBody">
    <mt-header title="位置共享">
      <mt-button slot="left" icon="back" @click="backPage">返回</mt-button>
    </mt-header>
    <div class="msg hdBody">
      <mt-loadmore :bottom-method="loadBottom" :top-method="loadTop" :bottom-all-loaded="allLoaded" :autoFill="false" ref="loadmore">
        <div class="j_loadmore">
          <div class="box" @click="locationShare(idx)" v-for="(item,idx) in locationList">
            <div><label>{{item.date|FormartDate}}</label></div>
            <i class="icon iconfont icon-dingwei"></i>
            <span>{{item.fcontent}}</span>
          </div>
        </div>
      </mt-loadmore>
      <!--<div class="box" @click="locationShare">-->
        <!--<div><label>2017-05-30</label></div>-->
        <!--<i class="icon iconfont icon-dingwei"></i>-->
        <!--<span>张大雷发起位置共享</span>-->
      <!--</div>-->
    </div>
  </div>
  </transition>
</template>
<script>
  import { MessageBox } from 'mint-ui';
  import { Toast } from 'mint-ui';
  import { Loadmore } from 'mint-ui';
  import {LoginUser} from '@/common/js/common';
  export default {
    name: 'locationShare',
    data () {
      return {
         pageNum:1,
         pageSize:20,
         allLoaded:false,
         userId:LoginUser.getUser().userId,
         locationList:[]
      }
    },
    components:{Loadmore},
    activated:function(){
      this.queryList();
    },
    created:function(){
       this.queryList();
    },
    methods:{
      loadTop:function() {
        this.pageNum = 1;
        this.allLoaded = false;
        this.queryList();
        this.$refs.loadmore.onTopLoaded();
      },
      loadBottom:function(){
        this.pageNum++;
        this.queryList();
      },
      postion:function(){
        if(this.pageNum===1){
          this.$nextTick(function() {
            var dom = document.getElementsByClassName('mint-loadmore');
            dom[0].parentNode.scrollTop = 0;
          })
        }
      },
      backPage:function(){
        if(this.$route.query.param == 'teamManage'){
          this.$router.push('/teamManage');
        }else if(this.$route.query.param){
          this.$router.push('/msgList?storage=true&param='+this.$route.query.param);
        }else if(this.$route.query.isMsg){
          this.$router.push('/msgList?param=home');
        }else{
          this.$router.push('/msgList?storage=true');
        }
      },
      queryList:function(pageNum,pageSize){
        let _this = this;
        this.$axios({
          method: 'post',
          url: 'groupMessage/getGroupLocationShare',
          data:this.$qs.stringify({
            userId:this.userId,
            messageType:1,
            pageNum:_this.pageNum,
            pageSize:_this.pageSize
          })
        }).then(function (response) {
           if(response.data.msg=='success'){
             if(!response.data.data.list||response.data.data.list.length<=0){
               _this.allLoaded = true;
             }
             if(_this.pageNum===1){
               _this.locationList = response.data.data.list;
             }else{
               _this.locationList = _this.locationList.concat(response.data.data.list);
             }
           }
           _this.$refs.loadmore.onBottomLoaded();
           _this.postion();
        });
      },
      locationShare:function(idx){
        let _this = this;
        MessageBox.confirm( '是否加入团队位置共享?').then(function(){
           window.location.href = 'myapp://parameter?type=JoinLocation&userID='+ _this.userId;
        });
      }
    }
  }
</script>




