<style scoped>
  .teamBody{
    width: 100%;
    height: calc(100vh);
    text-align: left;
    overflow: hidden;
    background-color: #f0f0f0;
  }
  .teamBody .mint-cell-text{
    color: #666;
  }
  .teamBody .mint-cell-value{
    color: #000;
  }
  .teamBody .noticesInfo{
    background-color: #fff;
  }
  .teamBody .info{
    border-top: solid 11px #f0f0f0;
  }
  .imgs{
    width: 96%;
    margin: -10px auto 0 auto;
    padding-bottom: 10px;
  }
  .imgs .item{
    display: inline-block;
    width: 33.33333333%;
    height: 0.9rem;
    margin-top: 10px;
  }
  .imgs .item div{
    display: block;
    width: 96%;
    height: 100%;
    margin: 0 auto;
    background-size: cover;
    background-position: center center;
  }
  .location{
    display: block;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 20px;
    line-height: 0.3rem;
  }
  .location .icon-location{
    color: #367cc9;
  }
  .reply {
    height:35px;
    line-height:35px;
    padding: 0 14px;
    font-size: 16px;
  }
  .replyContent {
    padding: 20px 14px;
  }
  .replyContent .replyCell {
    margin-top: 20px;
  }
  .replyContent .replyCell:first-child{
    margin-top: 0;color: #4ea25c;
  }
  .replyCell p {
    line-height: 24px;
  }
  .replyCell .replyTime {
    font-size: 14px;
  }
  .replyCell .content {
    font-size: 16px;
  }
</style>
<style>
  .noticesInfo .mint-cell-wrapper{
    padding: 0 14px !important;
  }
  .teamBody .content .mint-cell-text {
    display: block;
    padding: 14px 0 !important;
    line-height: 0.22rem;
  }
  .team-help-box{
    min-height: 101%;
  }
</style>
<template>
  <div class="teamBody">
    <mt-header title="求助消息查看">
      <mt-button slot="left" icon="back" @click="backPage">返回</mt-button>
    </mt-header>
    <div class="hdBody" v-cloak>
      <div class="team-help-box">
        <div class="noticesInfo info">
          <mt-cell title="求助人">{{teamInfo.fuserName}}</mt-cell>
          <mt-cell title="联系方式">{{teamInfo.fphone}}</mt-cell>
          <mt-cell title="状况描述"></mt-cell>
          <mt-cell :title="teamInfo.fdesc" class="content"></mt-cell>
          <div class="imgs">
            <div class="item" v-for="(img,idx) in teamInfo.nailImagUrl" @click="clkImage(idx)">
              <div :style="{backgroundImage: 'url('+img+')'}"></div>
            </div>
          </div>
          <span class="location"><i class="iconfont icon-location"></i>所在位置&#12288;{{teamInfo.faddress}}</span>
        </div>
        <p class="reply" v-if="teamInfo.reply&&teamInfo.reply.length>0">回复内容</p>
        <div class="noticesInfo replyContent" v-if="teamInfo.reply&&teamInfo.reply.length>0">
          <div class="replyCell" v-for="(rep,idx) in teamInfo.reply">
            <p class="replyTime">{{rep.replyDate}}</p>
            <p class="content">{{rep.fcontent}}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import {LoginUser} from '@/common/js/common';
  import { Indicator } from 'mint-ui';
  export default {
    name: 'teamHelpInfo',
    data () {
      return {
        teamInfo: {}
      }
    },
    activated:function(){
      this.queryInfo();
    },
    methods:{
      backPage:function(){
        if(this.$route.query.isMsg){
          this.$router.push('/home');
        }else{
          //this.$router.push('/msgList/teamHelp');
          this.$router.goBack();
        }
      },
      clkImage:function(idx){
        lookImags(idx,this.teamInfo.sourceImagUrl);
      },
      queryInfo:function(){
        Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        });
        let _this = this;
        this.$axios({
          method: 'post',
          url: '/tgroupHelp/getDetailOfApp',
          data:this.$qs.stringify({
            userId:LoginUser.getUser().userId,
            id:this.$route.query.id
          })
        }).then(function (response) {
          Indicator.close();
          if(response.data.msg=='success'){
            _this.teamInfo = response.data.data;
          }
        }).catch(function(){
          Indicator.close();
          throw new Error('网络异常');
        });
      }
    }
  }
</script>




