<style scoped>
  .teamHelpBox{
    text-align: left;
    background: #f0f0f0;
    height:100vh;
    overflow:hidden;
  }
  .headBox{height:60px;}
  .teamHelpList{background:#fff;}
  .thSel{
    position: absolute;
    top: 8px;
    right: 0;
    opacity: 0;outline: none;width:70vw;height:.32rem;border:none;background: none;text-align: right;font-size:16px;color:#888;text-align-last: right;}
  .thImg{overflow:hidden;}
  .thImg img{
    height: 0.8rem;
    width: 31.333333%;
    display: inline-block;
    margin: 0.1rem 1% 0 1%;
    background-size: cover;
  }
  .thBottom{margin:.2rem 0;background:#fff;}
  .thWz{font-size:.14rem;line-height:.22rem;overflow:hidden;padding:0.3rem .3rem;}
  .thWz1{float:left;width:35%;color:#666;}
  .thWz1 i{color:#2f8ad5;font-size:.2rem;}
  .thWz2{float:right;width:65%;color:#999;}
  .thIconBox{text-align: center;margin-bottom: 20px;}
  .thIcon{width:80%;margin:0 auto;height:.42rem;line-height:.42rem;font-size:.18rem;border-radius:.21rem;color:#fff;background:linear-gradient(126deg,#0f5bf6 0%,#16a3e4 100%);box-shadow:0 0 9px #16a3e4;border:none;}
  .teamHelpList select{
    text-align-last: right !important;text-align: right !important;
  }
  .teamHelpBox{
     height: calc(100vh);
      overflow:hidden;
     background-color: #fff;
  }
  .th-box{
     height: 101%;
  }
  .xcImages{
    width: 98%;
    margin: 0 auto;
    padding:10px 0;
    text-align: left;
    font-size: 0;
  }
  .xcImages .item{
    height: 70px;
    width: 31.333333%;
    background-color: #f8f8f8;
    display: inline-block;
    margin: 0.1rem 1% 0 1%;
    background-size: cover;
    background-position:center center;
    position: relative;
    text-align: center;
  }
  .xcImages .item .icon-camera {
    font-size: 45px;color: #cfcfcf;
  }
  .xcImages .item .text {
    display: block; font-size: 14px; color: #8f8f8f;    line-height: 20px;
  }
  .xcImages .item .shanchu{
    display: block;
    position: absolute;
    right: -.04rem;
    top: -.04rem;
    color: red;
  }
</style>
<style>
.teamHelpList textarea.mint-field-core{text-align: left;}
.teamHelpBox  .mint-field-core{
  text-align: right;
}
.noSubmit {
  background: #ccc !important;
  background-color: #ccc !important;
  box-shadow: 0 0 9px #ccc !important;
}
.noSubmit:active{
  background: #ccc !important;
  background-color: #ccc !important;
}
</style>
<template>
  <!--<transition name="slide">-->
  <div class="teamHelpBox">
    <mt-header title="求助消息编辑">
        <mt-button  slot="left" icon="back" @click="backHome">返回</mt-button>
    </mt-header>
    <div class="hdBody">
      <div class="th-box">
        <div class="teamHelpList">
          <mt-cell title="团队名称">
            <select class="thSel" v-model="groupId">
              <template v-for="team in teamList">
                <option :value="team.id" >
                  {{team.name}}
                </option>
              </template>
            </select>
            <label class="select-show">{{helpInfo.groupName}}</label><i class="arrow-down"></i>
          </mt-cell>
          <mt-field label="求助人" v-model="helpInfo.userName" disabled></mt-field>
          <mt-field label="联系方式" @input="btnBg" v-model="helpInfo.phone"></mt-field>
          <mt-field label="状况描述" placeholder="尽可能详细描述团队遇到的问题以及希望获得的求助方式" type="textarea" rows="4" v-model="helpInfo.desc"></mt-field>
          <div class="xcImages">
            <div class="item" @click="lookImg(idx)" v-for="(image,idx) in imageList.minImages" v-bind:style="{backgroundImage: 'url('+image+')'}">
              <i @click="deleImage($event,idx)" class="shanchu iconfont icon-shanchu"></i>
            </div>
            <div class="item up-img" v-if="imageList.minImages.length<9?true:false">
              <i class="iconfont icon-camera" @click="openSheet"></i>
              <span class="text">上传照片</span>
            </div>
          </div>
        </div>
        <div class="thBottom">
          <div class="thWz">
            <div class="thWz1"><i class="iconfont icon-location"></i>所在位置</div>
            <div class="thWz2">{{helpInfo.address}}</div>
          </div>
          <div class="thIconBox"><input type="button" value="提交" class="thIcon" :class="{noSubmit:noSubmit}" @click="ifSubmit"></div>
        </div>
      </div>
    </div>
    <mt-actionsheet :actions="[
       {name:'拍照',method:() => selectCamera()},
       {name:'从相册中选择',method:() => selectPicture()}
     ]" v-model="sheetVisible">
    </mt-actionsheet>
  </div>
  <!--</transition>-->
</template>
<script>
  import {LoginUser} from '@/common/js/common';
  import { Toast } from 'mint-ui';
  import { MessageBox } from 'mint-ui';
  import { Indicator } from 'mint-ui';
  export default {
    name: 'teamHelp',
    data () {
      return {
        teamList:[],
        imageList:{
           minImages:[],
           maxImages:[]
        },
        noSubmit:true,
        groupId:'',
        helpInfo:{
          phone:'',
          latitude:'',
          longitude:'',
          address:'',
          groupId:'',
          groupName:'',
          userId:'',
          userName:'',
          images:[],
          desc:''
        },
        sheetVisible:false
      }
    },
    watch:{
      groupId:function(newVal,oldVal){
        this.groupId = newVal;
        for(let i=0;i<this.teamList.length;i++){
          if(this.groupId==this.teamList[i].id){
            this.helpInfo.groupId = this.teamList[i].id;
            this.helpInfo.groupName = this.teamList[i].name;
            return;
          }
        }
      }
    },
    activated:function(){
      this.initData();
      this.queryTeam();
    },
    methods:{
      btnBg:function () {
        if (this.helpInfo.phone){
          this.noSubmit = false;
        } else {
          this.noSubmit = true;
        }
      },
      ifSubmit:function(){

        if (this.noSubmit){
          return false;
        }

        MessageBox({title:'提示', message: '确定发送该求助信息？',showCancelButton:true}).then(action => {
          if(action=='confirm'){
            this.submitHelp();
          }
        });
      },
      initData:function(){
         this.helpInfo={
           phone:'',
           latitude:'',
           longitude:'',
           address:'',
           groupId:'',
           groupName:'',
           userId:'',
           userName:'',
           images:[],
           desc:''
         };
         this.imageList.minImages=[];
         this.imageList.maxImages=[];
         this.helpInfo.userId = LoginUser.getUser().userId;
         this.helpInfo.userName = LoginUser.getUser().name;
         this.helpInfo.phone=LoginUser.getUser().phoneNumber;
         appMethod = this.getLocation;
         window.location.href = 'myapp://parameter?type=GetCurrentLocation&method=getAddress';
      },
      queryTeam:function(){
        Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        });
        let _this = this;
        this.$axios({
          method: 'post',
          url: 'group/getGroupInfoList',
          data:this.$qs.stringify({
             userId:LoginUser.getUser().userId,
             pageNum:0,
             pageSize:1000
          })
        }).then(function (response) {
            Indicator.close();
            if(response.data.msg=='success'){
               if(response.data.data.list.length>0){
                 _this.teamList = response.data.data.list;
                 _this.groupId = _this.teamList[0].id;
                 _this.helpInfo.groupId = _this.teamList[0].id;
                 _this.helpInfo.groupName = _this.teamList[0].name;
                 _this.teamList.push({id:'',name:'无'});
               }else{
                 _this.teamList = [{id:'',name:'无'}];
                 _this.helpInfo.groupName = '无';
               }
            }
        }).catch(function(){
          Indicator.close();
          throw new Error('网络异常');
        });
      },
      backHome:function(){
        this.$router.goBack();
      },
      getLocation:function(data){
         this.helpInfo.address = data.data.LocationName;
         this.helpInfo.latitude = data.data.Latitude;
         this.helpInfo.longitude = data.data.Longitude;
      },
      openSheet:function(){
        this.sheetVisible = true;
      },
      selectPicture:function(){
        //appData = this.helpInfo.images;
        appMethod = this.insertImg;
        this.sheetVisible = false;
        window.location.href = "myapp://parameter?type=TakeAlbum&method=fileImg&imageNum="+(9-this.imageList.maxImages.length);
      },
      selectCamera:function(){
        appMethod = this.insertImg;
        this.sheetVisible = false;
        window.location.href = "myapp://parameter?type=TakePhoto&method=fileImg&imageNum="+(9-this.imageList.maxImages.length);
      },
      lookImg:function(idx){
         let data = {
           ImagesUrlArray:this.imageList.maxImages,
           ImageIndex:idx
         };
         window.location.href = 'myapp://parameter?type=ShowImage&imageData='+JSON.stringify(data);
      },
      deleImage:function(e,index){
        e.stopPropagation();
        MessageBox.confirm('确定删除吗?').then(action => {
          this.imageList.minImages.splice(index,1);
          this.imageList.maxImages.splice(index,1);
          //this.imageList.photoNames.splice(index,1);
          this.helpInfo.images.splice(index,1);
        });
      },
      insertImg:function(data){
        for(let i=0;i<data.data.length;i++){
          this.helpInfo.images.push(data.data[i].photoName);
          this.imageList.minImages.push(data.data[i].thumbnailURL);
          this.imageList.maxImages.push(data.data[i].sourcePhotoURL);
        }
        appMethod = null;
      },
      queryTeamName:function(groupId){
         for(let i=0;i<this.teamList.length;i++){
            if(this.teamList[i].id==groupId){
               return this.teamList[i].name;
            }
         }
      },
      submitHelp:function () {
        if(this.helpInfo.phone===''){
          let instance = Toast('手机号码不能为空');
          setTimeout(() => {
            instance.close();
          }, 2000);
          return;
        }
        Indicator.open({
          text: '提交中...',
          spinnerType: 'fading-circle'
        });
        this.helpInfo.groupName = this.queryTeamName(this.helpInfo.groupId);
        let _this = this;
        this.$axios({
          method: 'post',
          url: 'tgroupHelp/addGroupHelp',
          data:this.helpInfo
        }).then(function (response) {
          Indicator.close();
          if(response.data.msg == 'success'){
            let instance = Toast('求助消息已送达！');
            setTimeout(() => {
               instance.close();
               _this.initData();
               _this.queryTeam();
            }, 2000);
            /* 通知手机，求助已经成功发送 */
            //window.location.href='myapp://parameter?type=TeamHelp&helpState=1';
          }else {
            let instance = Toast('求助消息未送达，请重试！');
            setTimeout(() => {
              instance.close();
            }, 1000);
            /* 通知手机，求助发送失败 */
            //window.location.href='myapp://parameter?type=TeamHelp&helpState=0';
          }
        }).catch(function(){
          Indicator.close();
          /* 通知手机，求助发送失败 */
          let instance = Toast('求助消息未送达，请重试！');
          setTimeout(() => {
            instance.close();
          }, 1000);
        });
      }
    }
  }
</script>




