<style scoped>
  .msgBody{
    width: 100%;
    height: calc(100vh);
    overflow: hidden;
    background-color: #f0f0f0;
  }
  .notices .box{
    padding: 0.2rem 0.15rem;
    margin-top: 0.48rem;
    background-color: #fff;
    position: relative;
  }
  .msgTitle{
    font-size: 1.2em;
    padding-bottom: 10px;
  }
  .box div:first-child{
    position: absolute;
    text-align: center;
    width: calc(100% - 0.3rem);
    top: -30px;
  }
  .box div:first-child label{
    display: inline-block;
    text-align: center;
    padding: 0 10px;
    border-radius: 3px;
    color: #fff;
    line-height: 20px;
    background-color: #c5c5c5;
  }
  .box .item{
    width: 100%;
    margin-top: 10px;
  }
  .msgBody .box .item:last-child{
    margin-top: 15px;
  }
  .box .item span{
    display: inline-block;
  }
  .box .item span:first-child{
    width: 36%;
  }
  .icon-lvxinghover{ color: #8a68d7;}
  .icon-bourn2{color: #33aa59;}
  .icon-tuandui3{color: #f6be61;}
  .icon-rili{color: #f66755;}
  .iconfont{
     margin-right: 5px;
  }
</style>
<template>
  <transition name="slide">
  <div class="msgBody">
    <mt-header title="行程分享">
      <mt-button slot="left" icon="back" @click="backPage">返回</mt-button>
    </mt-header>
    <div class="notices hdBody">
      <mt-loadmore :bottom-method="loadBottom" :top-method="loadTop" :bottom-all-loaded="allLoaded" :autoFill="false" ref="loadmore">
        <div class="j_loadmore">
          <div class="box" @click="clickList(idx)" v-for="(item,idx) in travelList">
            <div><label>{{item.createAt|FormartDate}}</label></div>
            <div class="msgTitle">
              {{item.fname}}
            </div>
            <div class="item">
              <span><i class="iconfont icon-lvxinghover"></i>{{item.fcityFrom}}-{{item.fcityTo}}</span>
              <span><i class="iconfont icon-bourn2"></i>{{item.fnum}}个目的地</span>
            </div>
            <div class="item">
              <span><i class="iconfont icon-tuandui3"></i>{{item.fpersionNum}}人</span>
              <span><i class="iconfont icon-rili"></i>{{item.fstartAt}}到{{item.fbackAt}}</span>
            </div>
          </div>
        </div>
      </mt-loadmore>
      <!--<div class="box" @click="clickList">-->
        <!--<div><label>10:21</label></div>-->
        <!--<div class="msgTitle">-->
          <!--中缅高速道路建设考察-->
        <!--</div>-->
        <!--<div class="item">-->
          <!--<span><i class="iconfont icon-lvxinghover"></i>中国-缅甸</span>-->
          <!--<span><i class="iconfont icon-bourn2"></i>3个目的地</span>-->
        <!--</div>-->
        <!--<div class="item">-->
          <!--<span><i class="iconfont icon-tuandui3"></i>8人</span>-->
          <!--<span><i class="iconfont icon-rili"></i>2017-05-17到2017-05-30</span>-->
        <!--</div>-->
      <!--</div>-->
      <!--<div class="box" @click="clickList">-->
        <!--<div><label>10:21</label></div>-->
        <!--<div class="msgTitle">-->
          <!--中缅高速道路建设考察-->
        <!--</div>-->
        <!--<div class="item">-->
          <!--<span><i class="iconfont icon-lvxinghover"></i>中国-缅甸</span>-->
          <!--<span><i class="iconfont icon-bourn2"></i>3个目的地</span>-->
        <!--</div>-->
        <!--<div class="item">-->
          <!--<span><i class="iconfont icon-tuandui3"></i>8人</span>-->
          <!--<span><i class="iconfont icon-rili"></i>2017-05-17到2017-05-30</span>-->
        <!--</div>-->
      <!--</div>-->
    </div>
  </div>
  </transition>
</template>
<script>
  import {LoginUser} from '@/common/js/common';
  import { Loadmore } from 'mint-ui';
  import { Toast } from 'mint-ui';
  export default {
    name: 'teamNotice',
    data () {
      return {
        pageNum:1,
        pageSize:20,
        allLoaded:false,
        travelList:[]
      }
    },
    components:{Loadmore},
    created:function(){
      this.queryList();
    },
    methods:{
      loadTop:function() {
        this.pageNum = 1;
        this.allLoaded = false;
        this.queryList();
        this.$refs.loadmore.onTopLoaded();
      },
      loadBottom:function(){
        this.pageNum++;
        this.queryList();
      },
      postion:function(){
        if(this.pageNum===1){
          this.$nextTick(function() {
            var dom = document.getElementsByClassName('mint-loadmore');
            dom[0].parentNode.scrollTop = 0;
          })
        }
      },
      backPage:function(){
        if(this.$route.query.param){
           this.$router.push('/'+this.$route.params.backURL+'?storage=true&param='+this.$route.query.param);
        }else if(this.$route.query.isMsg){
          this.$router.push('/msgList?param=home');
        }else{
           this.$router.push('/'+this.$route.params.backURL+'?storage=true');
        }
      },
      clickList:function(idx){
         //this.$router.push('/xcInfo?name=tuandui&fid='+this.travelList[idx].fid+'&isHistory=true');
        this.$router.push({
           name:'xcInfo',
           params:{
             name:'travelList',
             fid:this.travelList[idx].fid
           }
        });
      },
      queryList:function(){
        let _this = this;
        this.$axios({
          method: 'post',
          url: 'tmessage/selectTravelInfo',
          data:this.$qs.stringify({
            userId:LoginUser.getUser().userId,
            pageNum:_this.pageNum,
            pageSize:_this.pageSize
          })
        }).then(function (response) {
          if(response.data.msg=='success'){
            if(!response.data.data.list||response.data.data.list.length<=0){
              _this.allLoaded = true;
            }
            if(_this.pageNum===1){
              _this.travelList = response.data.data.list;
            }else{
              _this.travelList = _this.travelList.concat(response.data.data.list);
            }
          }
          _this.$refs.loadmore.onBottomLoaded();
          _this.postion();
        });
      }
    }
  }
</script>




