import help from '@/components/help/help';
import teamManage from '@/components/teamManage/teamManage';
import tmTeam from '@/components/teamManage/children/tmTeam';
import tmContact from '@/components/teamManage/children/tmContact';
import teamAdd from '@/components/teamManage/teamAdd/teamAdd';
import teamView from '@/components/teamManage/teamView/teamView';
import msg_teamView from '@/components/teamManage/teamView/msg_teamView';
import newsRelease from '@/components/teamManage/newsRelease/newsRelease';
import messages from '@/components/my/pages/messages';
import modHeadIcon from '@/components/my/pages/modHeadIcon';
import modPhone from '@/components/my/pages/modPhone';
import modPwd from '@/components/my/pages/modPwd';
import msgsList from '@/components/my/pages/msgsList';
import teamNotice from '@/components/my/pages/msgList/teamNotice';
import teamCircular from '@/components/my/pages/msgList/teamCircular';
import locationShare from '@/components/my/pages/msgList/locationShare';
