import teamNoticeInfo from '@/components/my/pages/msgList/teamNoticeInfo';
import msg_teamNoticeInfo from '@/components/my/pages/msgList/msgTeamNoticeInfo';
import signInfo from '@/components/my/pages/msgList/signInfo';
import msg_signInfo from '@/components/my/pages/msgList/msg_SignInfo';
import teamHelpInfo from '@/components/my/pages/msgList/teamHelpInfo';
import msg_teamHelpInfo from '@/components/my/pages/msgList/msgTeamHelpInfo';
import travelList from '@/components/my/pages/msgList/travelList';
import apparatus from '@/components/home/apparatus/apparatus';
import indexteamHelp from '@/components/home/teamHelp/teamHelp';
import warningList from '@/components/home/warning/warningList';
import warningDet from '@/components/home/warning/warningDet';
import msg_warningDet from '@/components/home/warning/msg_warningDet';
import tipList from '@/components/home/tips/tipList';
import tipListInfo from '@/components/home/tips/tipListInfo';
import commonHelp from '@/components/help/commonHelp';
import teamSet1 from '@/components/teamManage/teamView/teamSet/teamSet';
import teamSet2 from '@/components/teamManage/teamView/teamSet/teamSet2';
import updateContact from '@/components/teamManage/updateContact/updateContact';
import serviceAgreement from '@/components/comPage/serviceAgreement';
import device from '@/components/my/pages/device';
import Settings from '@/components/my/pages/settings';
import myTrip from '@/components/my/pages/myTrip';
import Register from '@/components/login/register';
import personInfo from '@/components/my/pages/personInfo';
import Suggestion from '@/components/my/pages/suggestion';
import AboutMe from '@/components/my/pages/aboutMe';
import ForgetPwd from '@/components/login/forgetPwd';

import myInfo from '@/components/my/myInfo';
import teamManageInfo from '@/components/teamManage/teamManageInfo';
import homeInfo from '@/components/home/homeInfo';
import xchengInfo from '@/components/xcheng/xchengInfo';

Vue.use(Router);
Router.prototype.goBack = function () {
  this.isBack = true;
  window.history.go(-1);
};
export default new Router({
  routes: [
    {
      path: '/login',
      name: 'login',
      components: {
        body: login
      }
    },
    {
      path: '/register',
      name: 'register',
      components: {
        body: Register
      }
    },
    {
      path: '/home',
      name: 'home',
      components: {
        body: home
      },
      children: [
        {
          path: '',
          components: {
            body: homeInfo,
            footer: footerTab
          }
        },
        {
          path: 'teamHelp',
          components: {body: teamHelp}
        },
        {
          path: 'teamHelpInfo',
          components: {body: teamHelpInfo}
        },
        {
          path: 'msg_teamHelpInfo',
          components: {body: msg_teamHelpInfo}
        },
        {
          path: 'indexteamHelp',
          components: {body: indexteamHelp}
        },
        {
          path: 'help',
          components: {body: help}
        },
        {
          path: 'commonHelp',
          components: {body: commonHelp}
        },
        {
          path: 'teamNotice',
          components: {body: teamNotice}
        },
        {
          path: 'teamNoticeSignInfo',
          components: {body: signInfo}
        },
        {
          path: 'msg_teamNoticeSignInfo',
          components: {body: msg_signInfo}
        },
        {
          path: 'teamCircular',
          components: {body: teamCircular}
        },
        {
          path: 'teamNoticeInfo',
          components: {body: teamNoticeInfo}
        },
        {
          path: 'msg_teamNoticeInfo',
          components: {body: msg_teamNoticeInfo}
        },
        {
          path: 'sendNewsRelease',
          components: {body: newsRelease}
        },
        {
          path: 'xchengtixing',
          components: {body: xchengtixing}
        },
        {
          path: 'xchengtixingDetail',
          components: {body: xchengtixingDetail}
        },
        {
          path: 'warnList',
          components: {body: warningList}
        },
        {
          path: 'warningDet',
          components: {body: warningDet}
        },
        {
          path: 'msg_warningDet',
          components: {body: msg_warningDet}
        },
        {
          path: 'tipList',
          components: {body: tipList}
        },
        {
          path: 'tipListInfo',
          components: {body: tipListInfo}
        }
      ],
      mate: {keepAlive: true}
    },
    {
      path: '/my',
      name: 'my',
      components: {
        body: my
      },
      children: [
        {
          path: '',
          components: {
            body: myInfo,
            footer: footerTab
          }
        },
        {
          path: 'personInfo',
          components: {body: personInfo}
        },
        {
          path: 'device',
          components: {body: device}
        },
        {
          path: 'modHeadIcon',
          components: {body: modHeadIcon}
        },
        {
          path: 'modPhoneNo',
          components: {body: modPhone}
        },
        {
          path: 'modPassword',
          components: {body: modPwd}
        },
        {
          path: 'myTrip',
          components: {body: myTrip}
        },
        {
          path: 'setting',
          components: {body: Settings}
        },
        {
          path: 'suggestion',
          components: {body: Suggestion}
        },
        {
          path: 'aboutMe',
          components: {body: AboutMe}
        },
        {
          path: 'serviceAgreement',
          components: {body: serviceAgreement}
        }
      ],
      mate: {keepAlive: true}
    },
    {
      path: '/xcheng',
      name: 'xcheng',
      components: {
        body: xcheng
      },
      children: [
        {
          path: '',
          components:{
            body: xchengInfo,
            footer: footerTab
          },
          children: [
            {
              path: 'plan',
              components: {xcMain:planTrip}
            },
            {
              path: 'his',
              components: {xcMain:historyTrip}
            },
            {
              path: 'tripNote',
              components: {xcMain:tripNotes}
            }
          ]
        },
        {
          path: 'xcInfo',
          name: 'xcInfo',
          components:{
            body: xcInfo
          }
        },
        {
          path: 'msg_xcInfo',
          name: 'msg_xcInfo',
          components:{
            body: msg_xcInfo
          }
        },
        {
          path: 'xcAdd',
          name: 'xcAdd',
          components:{
            body: xcAdd
          }
        },
        {
          path: 'fabusuiji',
          name: 'fabusuiji',
          components: {
            body: fabusuiji
          }
        },
        {
          path: 'pushDiary',
          name: 'pushDiary',
          components: {
            body: kjfw
          }
        },
        {
          path: 'citySelect',
          name: 'citySelect',
          components: {
            body: citySelect
          }
        },
        {
          path: 'xcDayAdd',
          name: 'xcDayAdd',
          components: {
            body: xcDayAdd
          }
        },
        {
          path: 'xcInfoMod',
          name: 'xcInfoMod',
          components: {
            body: xingchengxiugai
          }
        },
        {
          path: 'dayDetailchakan',
          name: 'dayDetailchakan',
          components: {
            body: dayDetailchakan
          }
        },
        {
          path: 'dayDetailxiugai',
          name: 'dayDetailxiugai',
          components: {
            body: dayDetailxiugai
          }
        }

      ],
      mate: {keepAlive: true}
    },
    {
      path: '/teamManage',
      name: 'teamManage',
      components: {
        body: teamManage
      },
      children: [
        {
          path: '',
          components: {
            body: teamManageInfo,
            footer: footerTab
          },
          children: [
            {path: '', component: tmTeam},
            {path: 'tmContact', component: tmContact}
          ]
        },
        {
          path: 'teamView',
          components: {body: teamView}
        },
        {
          path: 'msg_teamView',
          components: {body: msg_teamView}
        }
      ]
    },
    {
      path: '/teamAdd',
      name: 'teamAdd',
      components: {
        body: teamAdd
      },
      mate: {keepAlive: true}
    },
    //{
    //  path: '/msgList',
    //  name: 'msgList',
    //  components: {
    //    body: messages
    //  },
    //  children: [
    //    {path: '', component: msgsList},
    //    {path: 'locationShare', component: locationShare},
    //    {path: 'teamInvite', component: teamInvite},
    //    {path: 'systemNotice', component: systemNotice},
    //    {path: 'msg_teamHelpInfo', component: msg_teamHelpInfo},
    //    {path: 'travelList/:backURL', component: travelList}
    //  ],
    //  mate: {keepAlive: true}
    //},
    {
      path: '/apparatus',
      name: 'apparatus',
      components: {
        body: apparatus
      },
      mate: {keepAlive: true}
    },
    {
      path: '/msg_warningDet',
      name: 'msg_warningDet',
      components: {
        body: msg_warningDet
      }
    },
    {
      path: '/teamSet1',
      name: 'teamSet1',
      components: {
        body: teamSet1
      }
    },
    {
      path: '/teamSet2',
      name: 'teamSet2',
      components: {
        body: teamSet2
      }
    },
    {
      path: '/updateContact',
      name: 'updateContact',
      components: {
        body: updateContact
      }
    },
    {
      path: '/myTrip',
      name: 'myTrip',
      components: {
        body: myTrip
      }
    },
    {
      path: '/forgetPwd',
      name: 'forgetPwd',
      components: {
        body: ForgetPwd
      }
    }
  ]
});



