<style scoped>
  .myBody{
     width: 100%;
     height: 100vh;
     text-align: left;
    overflow:hidden;
  }
  .box{
    border-top: 0.12rem solid #f0f0f0;
  }
  .mint-cell-value span{
     font-size: 0.9em;
  }
  .mint-cell-value input{
     border: solid 1px transparent;
     border-radius: 3px;
     outline: none;
    text-align: right;
  }
  .list{
     position: relative;
  }
  .list span{
    color: #fff;
    display: block;
    position: absolute;
    top: 0.19rem;
    right: 0.14rem;
    height: 0.2rem;
    border-radius: 0.15rem;
    line-height: 0.2rem;
    padding: 0.02rem 0.1rem;
    background:-webkit-gradient(linear, 0 0, right 0, from(rgba(255,183,10,0.8)), to(#ff6a01));
  }
</style>
<template>
   <div class="myBody">
      <mt-header title="修改联系人">
        <mt-button slot="left" icon="back" @click="closePage">返回</mt-button>
        <mt-button slot="right" @click="saveInfo">完成</mt-button>
      </mt-header>
      <div class="box hdBody">
        <div class="list">
          <mt-field label="新名称" :placeholder=contactName v-model="contactName"></mt-field>
        </div>
        <div class="list">
          <mt-cell title="手机号" :value=phone></mt-cell>

        </div>
      </div>
   </div>
</template>
<script>
  import { MessageBox } from 'mint-ui';
  export default {
    name: 'modPhone',
    data () {
      return {
        contactName:'',
        phone:''
      }
    },
    activated:function(){
      let phone = this.$route.query.phone;
      let contactName = this.$route.query.contactName;
      this.phone = phone;
      this.contactName = contactName;
    },
    methods:{
      closePage:function(){
        this.$router.push('/teamManage/tmContact');
      },
      saveInfo:function(){
        let it = this;
        MessageBox.confirm('确定执行此操作?').then(action => {
          this.$axios({
            method: 'post',
            url: '/contact/updateContact',
            data:this.$qs.stringify({
              contactName:it.contactName,
              phone:it.phone
            })
          }).then(function (response) {
            if(response.data.code >= 0){
              it.$router.push('/teamManage/tmContact');
            }
          });
        });
      }
    }
  }
</script>




