<style scoped>
  .modHeadBody{
     width: 100%;
     height: 100vh;
     text-align: left;
     background-color: rgba(0,0,0,0.3);
     overflow: hidden;
  }
  .mint-cell-value span{
     font-size: 0.9em;
  }
  .mint-cell-value input{
     border: solid 1px transparent;
     width: 70vw;
     padding: 0.04rem 0;
     border-radius: 3px;
     outline: none;
  }
</style>
<style>
  .modHeadBody .box img{
    width: 100vw;
    margin-top: 14%;
  }
  .modHeadBody .box #imageBox{
     width: 100vw;
     height: 100vw;
     margin-top: 10vh;
     background-size: cover;
  }
</style>
<template>
  <!--<transition name="slide">-->
   <div class="modHeadBody">
      <mt-header title="个人头像">
        <mt-button slot="left" icon="back"  @click="closePage">个人信息</mt-button>
        <mt-button icon="more" slot="right" @click="openSheet"></mt-button>
      </mt-header>
      <div class="box" id="boxImg" @click="lookImg">
          <!--<img id="headIconId" :src="user.url">-->
          <div id="imageBox" v-bind:style="{backgroundImage: 'url('+user.url+')'}"></div>
      </div>
     <mt-actionsheet :actions="[
       {name:'拍照',method:() => selectCamera()},
       {name:'从相册中选择',method:() => selectPicture()}
     ]" v-model="sheetVisible" closeOnClickModal="false">
     </mt-actionsheet>
   </div>
   <!--</transition>-->
</template>
<script>
  import {LoginUser} from '@/common/js/common';
  import { Toast } from 'mint-ui';
  export default {
    name: 'modHeadIcon',
    data () {
      return {
        user:{},
        headImg:{ImagesUrlArray:[],ImageIndex:0},
        sheetVisible:false
      }
    },
    activated:function(){
       this.user = LoginUser.getUser();
    },
    methods:{
      closePage:function(){
        //this.$router.push('/personInfo?param=true');
        localStorage.setItem('isUpdate',true);
        this.$router.goBack();
      },
      saveInfo:function(){
        this.closePage();
      },
      openSheet:function(){
         this.sheetVisible = true;
      },
      lookImg:function(){
      },
      selectPicture:function(){
        appMethod = this.updateHead;
        this.sheetVisible = false;
        window.location.href = "myapp://parameter?type=TakeAlbumHead&imageNum=1&method=updateHeadIcon";
      },
      selectCamera:function(){
        appMethod = this.updateHead;
        this.sheetVisible = false;
        window.location.href = "myapp://parameter?type=TakePhotoHead&imageNum=1&method=updateHeadIcon";
      },
      updateHead:function(data){
        document.getElementById('imageBox').style.backgroundImage = 'url('+data.data[0].thumbnailURL+')';
        this.headImg.ImagesUrlArray.push(data.data[0].sourcePhotoURL);
        let user = LoginUser.getUser();
        user.url = data.data[0].thumbnailURL;
        LoginUser.setUser(user);
        let _this = this;
        this.$axios({
           method: 'post',
           url: 'register/updateUserInfo',
           data:this.$qs.stringify({
              userId:LoginUser.getUser().userId,
              url:user.url
           })
        }).then(function (res) {
           if(res.data.msg=='success'){
             let instance = Toast('上传头像成功');
             setTimeout(() => {
               instance.close();
             }, 1000);
           }
           let userData = JSON.stringify({
            userID:user.userId,
            userName:user.name,
            phoneNo:user.phoneNumber,
            headImageUrl:user.url
           });
           window.location.href = 'myapp://parameter?type= UpdateUserInfo&userData='+userData;
        });
      }
    }
  }
</script>




