<style scoped>
  .myBody{
     width: 100%;
     height: calc(100vh);
     text-align: left;
     background-color: #f0f0f0;
     margin-top: -1px;
  }
  .box{
     border-top: 0.12rem solid #f0f0f0;
     background-color: #fff;
  }
  .mint-cell-value span{
     font-size: 0.9em;
  }
  .primaryBtn{
     width: 80%;
     margin: 20% auto 0 auto;
  }
  .primaryBtn .mint-button{
    background-color: #1168f2;
    background:-webkit-gradient(linear, 0 0, right 0, from(#16a2e3), to(#0f5bf5));
  }
  .exitLogin{
     border-top: solid 20px #f0f0f0;
     position: relative;
     margin-top: -2px;
  }
</style>
<style>
  .settings .mint-popup-right{
    left: 0;
  }
  .settings .v-modal{
     display: none;
  }
  .settings .exitLogin .mint-cell-title{
    text-align: center;
    color: blue;
  }
</style>
<template>
  <!--<transition name="slide">-->
   <div class="myBody settings">
      <mt-header title="设置">
        <mt-button slot="left" icon="back" @click="closePage">返回</mt-button>
      </mt-header>
      <div class="box">
        <div class="list" @click="changeLink('/my/suggestion')">
          <mt-cell title="意见反馈" is-link></mt-cell>
        </div>
        <div class="list" @click="updataSys">
          <mt-cell title="检查更新" is-link>
            <label></label>
          </mt-cell>
        </div>
        <div class="list" @click="changeLink('/my/aboutMe')">
          <mt-cell title="关于我们" is-link></mt-cell>
        </div>
        <div class="list exitLogin" @click="exitSys()">
          <mt-cell title="退出登录"></mt-cell>
        </div>
      </div>
   </div>
  <!--</transition>-->
</template>
<script>
  import {LoginUser} from '@/common/js/common';
  export default {
    name: 'settings',
    data () {
      return {
          versionName:'1.0.1'
      }
    },
    created:function(){
       //this.queryVersion();
    },
    methods:{
      closePage:function(){
        //this.$router.push('/my');
        this.$router.goBack();
      },
      changeLink: function (url) {
        this.$router.push(url);
      },
      queryVersion:function(){
        let _this = this;
        this.$axios({
          method: 'post',
          url: 'tmessage/selectNewVersion',
          data:this.$qs.stringify({
            versionCode:1
          })
        }).then(function (response) {
          if(response.data.msg=='success'){
            _this.versionName = response.data.data.c_version_name;
          }
        });
      },
      updataSys:function(){
        window.location.href = 'myapp://parameter?type=UpdateSys';
      },
      exitSys:function(){
        LoginUser.clear();//清除用户缓存
        this.$router.push('/login');
        appSocket.websocket.close();
        window.location.href = "myapp://parameter?type=LoginOut";
      }
    }
  }
</script>




