     color: #666;
     opacity: 0.8;
     position: relative;
  }
  .msgContent span{
     display: inline-block;
     padding-left: 0.3rem;
  }
  .notices .icon-xiaoxi{
     position: absolute;
     display: block;
     width: 0.2rem;
     height: 0.2rem;
     background-image: url("./xiaoxi.png");
     background-size: 100% 100%;
  }
  .msg_btn{
    padding: 0px 20px;
    height: 26px;
    line-height: 26px;
