  }
  .sign{
    background:-webkit-gradient(linear, 0 0, right 0, from(rgba(255,182,10,0.8)), to(#ff6a01));
    color: #fff;
    border:none;
  }
  .msg_sign{
    padding-top: 10px;overflow:hidden;
  }
  .msg_sign span label{
    color: #666;
  }
  .msg_sign span i{
     display: inline-block;
     width: 0.2rem;
     height:0.2rem;
     background-image: url("./sign.png");
     background-size: 100% 100%;
     position: relative;
     top:3px;
     margin-right: 0.05rem;
  }
  .message{
    position: fixed;
    top: 0;
    height: 100vh;
    width: 100%;
    background-color: rgba(0,0,0,.6);
  }
  .message-box{
    position: absolute;
    top: 30%;
    left: 10%;
    z-index: 9999999999999999;
    width: 80%;
    box-sizing: border-box;
    padding: 0 10px;
    height: 40vh;
    border-radius: 0.06rem;
    background-color: #fff;
    overflow: hidden;
  }
  .radio{
    display: block;
    position: relative;
