    width: 0.09rem;
    height: 0.04rem;
    border: 0.024rem solid #fff;
    border-top-color: transparent;
    border-right-color: transparent;
    -ms-transform: rotate(-60deg);
    -moz-transform: rotate(-60deg);
    -webkit-transform: rotate(-60deg);
    transform: rotate(-45deg);
  }
  @media only screen and (-webkit-min-device-pixel-ratio: 3), only screen and (min-device-pixel-ratio: 3) {
    .radio input+i:after{
      top: 0.15rem;
    }
  }
  @media only screen and (-webkit-min-device-pixel-ratio: 3.5), only screen and (min-device-pixel-ratio: 3.5) {
    .radio input+i:after{
      top: 0.16rem;
    }
  }
  .radio input:checked+i{
    background-color: #47c560;
    background:-webkit-gradient(linear, 0 0, right bottom, from(rgba(86,203,104,.8)), to(#28b74d));
  }
  .message-box .title{
    line-height: 0.4rem;
    color: #000;
    text-align: center;
    font-size: 0.16rem;
    background-image:linear-gradient(180deg, #f0f0f0, #f0f0f0 50%, transparent 50%);
    background-size: 100% 1px;
    background-repeat: no-repeat;
    background-position:bottom;
  }
  .message-box .radio-box{
     width: 90%;
     margin: 0 auto;
     height: calc(100% - 0.6rem - 0.4rem);
     overflow-y: auto;
     -webkit-overflow-scrolling: touch;
  }
  .message-box .buttons{
    padding-top: 0.16rem;
    height: 0.5rem;
  }
  .message-box .buttons button{
    border-radius: 0.24rem;
    width: 36%;
    box-sizing: border-box;
  }
  .message-box .buttons button:nth-child(2){
    float: right;
    margin-right: 10%;
  }
  .message-box .buttons button:nth-child(1){
    margin-left: 10%;
  }
  .hdBody{height: calc(100vh - 60px - 41px);}
</style>
<style>
  .msgBody .buttons button.mint-button{
    height: 0.3rem !important;
  }
  .teamnotice .buttons .mint-button{
     font-size: 0.16rem !important;
  }
</style>
<template>
  <!--<transition name="slide">-->
  <div class="msgBody teamnotice">
    <mt-header title="团队签到">
      <mt-button slot="left" icon="back" @click="backPage">返回</mt-button>
    </mt-header>
    <div class="no-data-pub" v-if="noticeList.length<=0">
      <i class="iconfont  icon-zanwuxiaoxi"></i>
      <p class="text">暂无团队签到信息</p>
      <p class="sub-text">发起签到时请点下面按钮</p>
    </div>
    <div class="notices hdBody" v-else>
      <mt-loadmore :bottom-method="loadBottom" :top-method="loadTop" :bottom-all-loaded="allLoaded" :autoFill="false" ref="loadmore">
        <div class="j_loadmore">
          <div class="box" @click="clickList(idx)" v-for="(notice,idx) in noticeList">
             <div><label>{{notice.createTime|FormartDate}}</label></div>
             <div class="msgTitle">
               {{notice.content}}
             </div>
             <div class="msg_sign">
                <span>发起人：{{notice.createName}}</span>
                <mt-button size="small" type="primary" class="msg_btn sign" v-if="!notice.sign" @click.stop="signClick(idx)">签到</mt-button>
                <mt-button size="small" type="primary" class="msg_btn" v-else @click="signToDetail(idx)">已签</mt-button>
             </div>
             <span class="red-point" v-if="notice.readed !== true"></span>
          </div>
        </div>
        </mt-loadmore>
    </div>
    <div class="send-btn">
        <mt-button type="primary"  size="large" @click="sendNotice">发送团队签到</mt-button>
    </div>
    <div class="message" id="messageId"  style="display: none;">
      <div class="message-box">
        <div class="title">选择团队</div>
        <div class="radio-box">
          <span class="radio" @click.stop="selectTeam(idx)" v-for="(team,idx) in teamList">
            <input type="radio" name="radio" :id="'radio'+idx">
            <i></i>
            <label :for="'radio'+idx">{{team.name}}</label>
          </span>
        </div>
        <div class="buttons">
          <mt-button type="primary" @click="clickOk">确 定</mt-button>
          <mt-button type="default" @click="clickCancel">取 消</mt-button>
        </div>
      </div>
    </div>
  </div>
  <!--</transition>-->
</template>
<script>
  import {LoginUser} from '@/common/js/common';
  import { Loadmore } from 'mint-ui';
  import { MessageBox } from 'mint-ui';
  import { Toast } from 'mint-ui';
  import { Indicator } from 'mint-ui';
  export default {
    name: 'teamNotice',
    data () {
      return {
         pageNum:1,
         pageSize:20,
         allLoaded:false,
         noticeList:[],
         teamList:[],
         formData:{
           groupId:'',
           groupName:'',
           userId:LoginUser.getUser().userId,
           userName:LoginUser.getUser().name,
           latitude:'',
           longitude:'',
           address:''
         }
      }
    },
    components:{Loadmore},
    activated:function(){
      this.pageNum = 1;
      this.queryList();
      this.queryTeam();
    },
    created:function(){
       appMethod = this.getLocation;
       window.location.href = 'myapp://parameter?type=GetCurrentLocation&method=getAddress';
    },
    methods:{
      loadTop:function() {
        this.pageNum = 1;
        this.allLoaded = false;
        this.queryList();
        this.$refs.loadmore.onTopLoaded();
      },
      loadBottom:function(){
        this.pageNum++;
        this.queryList();
      },
      postion:function(){
        if(this.pageNum===1){
          this.$nextTick(function() {
            var dom = document.getElementsByClassName('mint-loadmore');
            dom[0].parentNode.scrollTop = 0;
          })
        }
      },
      backPage:function(){
        //this.$router.push('/home');
        this.$router.goBack();
      },
      clickList:function(idx){
        let sign = this.noticeList[idx];
        this.$router.push('/home/teamNoticeSignInfo?noticeId='+sign.id+'&groupId='+sign.toGroupId);
      },
      queryList:function(){//查询团队通知列
         Indicator.open({
           text: '加载中...',
           spinnerType: 'fading-circle'
         });
         let _this = this;
         this.$axios({
            method: 'post',
            url: '/sign/PageListOfApp',
            data:this.$qs.stringify({
               userId:LoginUser.getUser().userId,
               pageNum:this.pageNum,
               pageSize:this.pageSize
            })
         }).then(function (response) {
            Indicator.close();
            if(response.data.msg=='success'){
              if(!response.data.data.list||response.data.data.list.length<=0){
                _this.allLoaded = true;
              }
              if(_this.pageNum===1){
                _this.noticeList = response.data.data.list;
              }else{
                _this.noticeList = _this.noticeList.concat(response.data.data.list);
              }
            }
            if(!_this.$refs.loadmore) return;
            _this.$refs.loadmore.onBottomLoaded();
            _this.postion();
         }).catch(function(){
           Indicator.close();
           throw new Error('网络异常');
         });
      },
      queryTeam:function(){
        Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        });
        let _this = this;
        this.$axios({
          method: 'post',
          url: 'group/getGroupInfoList',
          data:this.$qs.stringify({
            userId:LoginUser.getUser().userId,
            pageNum:0,
            pageSize:1000
          })
        }).then(function (response) {
          Indicator.close();
          if(response.data.msg=='success'){
            _this.teamList = response.data.data.list;
          }
        }).catch(function(){
          Indicator.close();
          throw new Error('网络异常');
        });
      },
      getLocation:function(data){
        this.formData.address = data.data.LocationName;
        this.formData.latitude = data.data.Latitude;
        this.formData.longitude = data.data.Longitude;
      },
      selectTeam:function(idx){
        this.formData.groupId = this.teamList[idx].id;
        this.formData.groupName = this.teamList[idx].name;
      },
      sendNotice:function(){
        document.getElementById('messageId').style.display = 'block';
      },
      clickCancel:function(){
        document.getElementById('messageId').style.display = 'none';
      },
      clickOk:function(){
        if(this.formData.groupId === ''){
          let instance = Toast("请选择团队");
          setTimeout(() => {
              instance.close();
          }, 1000);
          return;
        }
        Indicator.open({
          text: '提交中...',
          spinnerType: 'fading-circle'
        });
        let _this = this;
        this.$axios({
          method: 'post',
          url: 'sign/insertGroupSign',
          data:this.$qs.stringify(this.formData)
        }).then(function (response) {
            Indicator.close();
            if(response.data.code > 0){
              document.getElementById('messageId').style.display = 'none';
              let instance = Toast("发布团队签到成功");
              setTimeout(() => {
                instance.close();
              }, 2000);
              _this.queryList();
            }
        }).catch(function(){
          Indicator.close();
          let instance = Toast("发布团队签到失败");
          setTimeout(() => {
            instance.close();
          }, 1000);
          throw new Error('网络异常');
        });
      },
      signToDetail:function(idx){
        //解决点击签到之后点击已签不能进入详情页的bug，不能删除此方法
      },
      signClick:function(idx){//签到
        this.formData.groupId = this.noticeList[idx].toGroupId;
        this.formData.groupName = this.noticeList[idx].groupName;
        this.formData.noticeId = this.noticeList[idx].id;
        let _this = this;
        this.$axios({
          method: 'post',
          url: '/sign/sign',
          data:this.$qs.stringify(this.formData)
        }).then(function (response) {
          if(response.data.msg=='success'){
            let instance = Toast('签到成功');
            _this.noticeList[idx].sign = true;
            setTimeout(() => {
              instance.close();
            }, 2000);
          }
        });

      }
    }
  }
</script>




