<style>
  .tdxc .mint-cell-title {
    text-align: left
  }

  .tdxc .mint-field-core {
    text-align: right
  }

  .tdxc .cityName .city  .mint-field-core{
    text-align: left;
  }
  .picker{
    position: fixed;
    width: 100vw;
    height: 100vh;
    top:0;
    background-color: rgba(0,0,0,0.4);
  }
  .picker .picker-items{
     background-color: #fff;
     position: absolute;
     width: 100vw;
     bottom: 0;
  }
  .selectCity .mint-field:nth-child(2){
     margin-top: -1px;
  }
  .thSel{
    position: absolute;
    top: 8px;
    right: 0;
    opacity: 0;
    outline: none;width:70vw;height:.32rem;border:none;background: none;text-align: right;font-size:16px;color:#888;text-align-last: right;}
  .city-info{
    position: relative;
  }
  .city-info .noData{
    width: 100%;
    height: 0.5rem;
    opacity: 0;
    background-color: red;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 99999999999;
  }
  .city-info .arrow-down{
     position: absolute;
     top: 20px;
     right: 0;
  }
  .selectCity .mint-cell-wrapper{
    background-image: none !important;
  }
</style>
<template>
  <div class="tdxc selectCity">
    <mt-header title="选择城市">
      <mt-button icon="back" slot="left" @click="backPage">返回</mt-button>
      <mt-button slot="right" @click="saveCity()">完成</mt-button>
    </mt-header>
    <div class="tdxcContent">
      <div class="city-info">
        <mt-cell title="地区">
          <select  class="thSel" v-model="cityOneId"  id="ssId0" @click="selectOneCity(0)">
            <template v-for="city in cityList.cityOne">
              <option :value="city.id" >
                {{city.name}}
              </option>
            </template>
          </select>
          <label class="select-show">{{citys[0].name}}</label><i class="arrow-down"></i>
        </mt-cell>
      </div>
      <div class="city-info">
        <div class="noData" v-if="this.citys[0].cityId===''" @click="clickArea(1)"></div>
        <mt-cell title="国家">
          <select class="thSel" :disabled="this.citys[0].cityId===''" v-model="cityTwoId" id="ssId1" @click="selectOneCity(1)">
            <template v-for="city in cityList.cityTwo">
              <option :value="city.id" >
                {{city.name}}
              </option>
            </template>
          </select>
          <label class="select-show">{{citys[1].name}}</label><i class="arrow-down"></i>
        </mt-cell>
      </div>
      <div class="city-info" >
        <div class="noData" v-if="this.cityList.cityThree.length<=0||this.citys[1].cityId===''"  @click="clickArea(2)"></div>
        <mt-cell title="省州">
          <div data-tap-disabled="true">
            <select class="thSel"  v-model="cityThreeId" id="ssId2" @click="selectOneCity(2)">
              <template v-for="city in cityList.cityThree">
                <option :value="city.id" :selected = "false">
                  {{city.name}}
                </option>
              </template>
            </select>
          </div>

          <label class="select-show">{{citys[2].name}}</label><i class="arrow-down"></i>
        </mt-cell>
      </div>
      <div class="city-info">
        <div class="noData" v-if="this.cityList.cityFour.length<=0||this.citys[2].cityId===''"  @click="clickArea(3)"></div>
        <mt-cell title="城市">
          <select class="thSel" v-model="cityFourId" id="ssId3" @click="selectOneCity(3)">
            <template v-for="city in cityList.cityFour">
              <option :value="city.id" :selected = "false">
                {{city.name}}
              </option>
            </template>
          </select>
          <label class="select-show">{{citys[3].name}}</label><i class="arrow-down"></i>
        </mt-cell>
      </div>
      </div>
    </div>
</template>
<script>
  import {LoginUser} from '@/common/js/common';
  import { Toast } from 'mint-ui';
  export default {
    name: 'citySelect',
    data () {
      return {
        citys:[{name:'',cityId:''},{name:'',cityId:''},{name:'',cityId:''},{name:'',cityId:''}],
        cityIdx:0,
        cityOneId:'',
        cityTwoId:'',
        cityThreeId:'',
        cityFourId:'',
        timeout:0,
        isLoading:false,
        cityList:{
          cityOne:[],
          cityTwo:[],
          cityThree:[],
          cityFour:[]
        }
      }
    },
    watch:{
      cityOneId:function(newVal,oldVal){
         if(!newVal) return;
         this.dealWithCity(0,newVal);
         this.cityList.cityTwo = [];
         this.cityList.cityThree = [];
         this.cityList.cityFour = [];
         this.queryCitys(1,this.citys[0].cityId);
      },
      cityTwoId:function(newVal,oldVal){
        if(!newVal) return;
        this.dealWithCity(1,newVal);
        this.cityList.cityThree = [];
        this.cityList.cityFour = [];
        this.queryCitys(2,this.citys[1].cityId);
      },
      cityThreeId:function(newVal,oldVal){
        if(!newVal) return;
        this.dealWithCity(2,newVal);
        this.cityList.cityFour = [];
        this.queryCitys(3,this.citys[2].cityId);
      },
      cityFourId:function(newVal,oldVal){
        if(!newVal) return;
        this.dealWithCity(3,newVal);
      }
    },
    activated:function(){
      this.initData();
    },
    methods: {
      selectOneCity:function(idx){
         let list =[];
         let dom = document.getElementById('ssId'+idx);
         let _this = this;
         setTimeout(function(){
           if(idx==0){
             list = _this.cityList.cityOne;
           }else if(idx==1){
             list = _this.cityList.cityTwo;
           }else if(idx==2){
             list = _this.cityList.cityThree;
           }else if(idx==3){
             list = _this.cityList.cityFour;
           }
           if(!list||list.length<=0){
             return;
           }
//           list.forEach(function (item) {
//             if(item.id==dom.value){
//               _this.citys[idx].name = item.name;
//               _this.citys[idx].cityId = item.id;
//               if(idx==0){
//                 _this.queryCitys(idx+1,_this.citys[idx].cityId);
//               }else if(idx==1){
//                 _this.queryCitys(idx+1,_this.citys[idx].cityId);
//               }else if(idx==2){
//                 _this.queryCitys(idx+1,_this.citys[idx].cityId);
//               }
//               return;
//             }
//           });
         },100)
      },
      dealWithCity:function(index,newVal){
        let _this = this;
        _this.citys[index].cityId = newVal;
        let cityList = [];
        if(index==0){
          cityList = _this.cityList.cityOne;
        }else if(index==1){
          cityList = _this.cityList.cityTwo;
        }else if(index==2){
          cityList = _this.cityList.cityThree;
        }else if(index==3){
          cityList = _this.cityList.cityFour;
        }
        cityList.forEach(function(item){
          if(item.id==newVal){
            _this.citys[index].name = item.name;
            return;
          }
        });
        if(index===0){
          this.citys[1] = {name:'请选择',cityId:''};
          this.citys[2] = {name:'请选择',cityId:''};
          this.citys[3] = {name:'请选择',cityId:''};
        }else if(index===1){
          this.citys[2] = {name:'请选择',cityId:''};
          this.citys[3] = {name:'请选择',cityId:''};
        }else if(index===2){
          this.citys[3] = {name:'请选择',cityId:''};
        }
      },
      initData:function(){
        localStorage.removeItem('citys');
        this.citys = [{name:'请选择',cityId:''},{name:'请选择',cityId:''},{name:'请选择',cityId:''},{name:'请选择',cityId:''}];
        this.isLoading = false;
        this.queryCitys(0);
      },
      backPage:function(){
        if(this.$route.query.name=='tianjiadayCity'){
          let xcCityInfo = {name:'dayCity'};
          localStorage.setItem('xcCityInfo',JSON.stringify(xcCityInfo));
        }else if(this.$route.query.name=='tianjiaCity'){//团队行程，行程添加
          let xcCityInfo = {cityType:this.$route.query.cityType};
          localStorage.setItem('xcCityInfo',JSON.stringify(xcCityInfo));
        }else if(this.$route.query.name=='XGcity'){//行程修改
          let xcCityInfo = {name:'xiugaiCity',cityType:this.$route.query.cityType};
          localStorage.setItem('xcCityInfo',JSON.stringify(xcCityInfo));
        }else if(this.$route.query.name=='xiugaidayCity'){
          let xcCityInfo = {name:'dayxiugaiCity',cityType:this.$route.query.cityType};
          localStorage.setItem('xcCityInfo',JSON.stringify(xcCityInfo));
        }
        this.$router.goBack();
      },
      clickArea:function(idx){
        if(this.isLoading){
          let load = Toast('检索中...');
          setTimeout(() => {
            load.close();
          }, 1000);
          return;
        }
        this.cityIdx = idx;
        if(idx==1){
           if(this.citys[0].cityId==''){
             let instance = Toast('请先选择地区');
             setTimeout(() => {
              instance.close();
             }, 1000);
           }
        }else if(idx==2){
          if(this.citys[1].cityId==''){
            let instance = Toast('请先选择国家');
            setTimeout(() => {
              instance.close();
            }, 1000);
            return;
          }
          if(this.cityList.cityThree.length<=0){
            let instance = Toast('此国家没有省州');
            setTimeout(() => {
              instance.close();
            }, 1000);
          }
        }else if(idx==3){
          if(this.citys[2].cityId==''){
            let instance = Toast('请先选择省州');
            setTimeout(() => {
              instance.close();
            }, 1000);
            return;
          }
          if(this.cityList.cityFour.length<=0){
            let instance = Toast('此省州没有城市');
            setTimeout(() => {
              instance.close();
            }, 1000);
          }
        }
      },
      saveCity:function(){
        if(this.citys[0].cityId==''){
          let instance = Toast('请选择洲');
          setTimeout(() => {
            instance.close();
          }, 1000);
          return;
        }else if(this.citys[1].cityId==''){
          let instance = Toast('此国家或地区');
          setTimeout(() => {
            instance.close();
          }, 1000);
          return;
        }
        localStorage.setItem('citys',JSON.stringify(this.citys));
        this.backPage();
      },
      queryCityId:function(slot,name){
         for(let i=0;i<slot.values.length;i++){
           if(name==slot.values[i]){
              return slot.citysId[i];
           }
         }
      },
      queryCitys:function(idx,cityId){
        if(idx!=0&&!cityId) return;
        this.isLoading = true;
        let _this=this;
        if(this.timeout) return;
        this.timeout = setTimeout(()=>{
          this.timeout = 0;
          clearTimeout(this.timeout);
          this.$axios({
            method: 'post',
            url: 'sysCity/selectAllSysCityOrderByParentId?t='+Math.random(),
            data:this.$qs.stringify({
              userId:LoginUser.getUser().userId,
              cityId:cityId?cityId:''
            })
          }).then(function (res) {
            _this.isLoading = false;
            if(res.data.msg=='success'||res.data.data){
              if(idx==0){
                _this.cityList.cityOne =  [{name:'请选择',id:''}];
                _this.cityList.cityOne = _this.cityList.cityOne.concat(res.data.data);
                //_this.cityList.cityOne = res.data.data;
              }else if(idx==1){
                _this.cityList.cityTwo =  [{name:'请选择',id:''}];
                _this.cityList.cityTwo = _this.cityList.cityTwo.concat(res.data.data);
                //_this.cityList.cityTwo = res.data.data;
              }else  if(idx==2){
                _this.cityList.cityThree =  [{name:'请选择',id:''}];
                _this.cityList.cityThree = _this.cityList.cityThree.concat(res.data.data);
                //_this.cityList.cityThree = res.data.data;
              }else  if(idx==3){
                _this.cityList.cityFour =  [{name:'请选择',id:''}];
                _this.cityList.cityFour = _this.cityList.cityFour.concat(res.data.data);
                //_this.cityList.cityFour = res.data.data;
              }
              //console.log(_this.cityList)
            }
          });
        },200);
      }
    }
  }
</script>

<style scoped>

  .tdxcContent {
    overflow-y: auto;
    height: calc(100vh - .62rem);
    background: #FFFFFF;
    padding: 0 .1rem;
    -webkit-overflow-scrolling: touch;
    overflow-x: hidden;
  }
 .item{
   overflow: auto;
   margin-bottom: .1rem;
 }
.tdxcContent span{

  display: block;
  line-height: .36rem;
  text-align: left;
}
.title{
  font-size: .16rem;
  line-height: .40rem;
}
</style>




