<style scoped>
  .commonHelpBox{text-align: left;background: #f0f0f0;height:100%;overflow-y:auto;}
  .headBox{height:60px;}
  .chCont{}
  .chList{display: inline-block;width:.3rem;height:.3rem;text-align:center;border-radius:50%;margin-right:.18rem;}
  .chList i{line-height:.3rem;color:#fff;font-size:.2rem;}
  .chTit{background:#f0f0f0;line-height:.3rem;height:.3rem;padding:.05rem 10px 0 10px;font-size:.14rem;color:#666;}
  .chList.chList1{background:linear-gradient(298deg,#104da2 0%,#3cd7f1 100%);box-shadow:0 0 .08rem rgba(36,144,26,.43);}
  .chList.chList1 i{line-height:.32rem;}
  .chList.chList2{background:linear-gradient(298deg,#f26504 0%,#ff9852 100%);box-shadow:0 0 .08rem rgba(245,112,20,.45);}
  .chList.chList2 i{line-height:.32rem;}
  .chList.chList3{background:linear-gradient(310deg,#ecbd01 0%,#f4d802 100%);box-shadow:0 0 .08rem rgba(238,196,1,.45);}
  .chList.chList3 i{line-height:.32rem;}
  .chList.chList4{background:linear-gradient(298deg,#ff6a01 0%,#ffb70a 74%);box-shadow:0 0 .08rem rgba(147,109,226,.45);}
  .chList.chList4 i{line-height:.32rem;}
  .chList.chList5{background:linear-gradient(298deg,#21a2ff 0%,#6bdfff 100%);box-shadow:0 0 .08rem rgba(49,175,255,.45);}
  .chList.chList5 i{line-height:.32rem;}
  .chList.chList6{background:linear-gradient(298deg,#f25656 0%,#fb8c81 100%);box-shadow:0 0 .08rem rgba(244,97,95,.45);}
  .chList.chList6 i{line-height:.32rem;}
  .chList.chList7{background:linear-gradient(298deg,#6fd036 0%,#59c251 100%);box-shadow:0 0 .08rem rgba(121,213,68,.45);}
  .chList.chList7 i{line-height:.3rem;}
  .chList.chList8{background:linear-gradient(298deg,#3c70bb 0%,#16a3e4 100%);box-shadow:0 0 .08rem rgba(70,123,198,.45);}
  .chList.chList8 i{line-height:.3rem;}
</style>
<style>

</style>
<template>
  <!--<transition name="slide">-->
  <div class="commonHelpBox">
    <div class="headBox">
      <mt-header fixed title="常用救助机构">
        <mt-button  slot="left" icon="back" @click="backHelp">返回</mt-button>
      </mt-header>
    </div>
    <div class="chCont">
      <div class="chTit">报警救援</div>
      <!--<mt-cell title="匪警" is-link :to='`tel:${data.tpoliceNum}`'>-->
      <mt-cell title="匪警" is-link @click.native="callPhone(data.tpoliceNum)">
        <div slot="icon" class="chList chList1"><i class="iconfont icon-feijing"></i></div>
      </mt-cell>
      <mt-cell title="火警" is-link @click.native="callPhone(data.tfireNum)">
        <div slot="icon" class="chList chList2"><i class="iconfont icon-huojing"></i></div>
      </mt-cell>
      <mt-cell title="急救中心" is-link @click.native="callPhone(data.tfirstAidNum)">
        <div slot="icon" class="chList chList3"><i class="iconfont icon-icon-test"></i></div>
      </mt-cell>
      <mt-cell title="交通事故报警" is-link @click.native="callPhone(data.ttrafficNum)">
        <div slot="icon" class="chList chList4"><i class="iconfont icon-alarm"></i></div>
      </mt-cell>
      <mt-cell title="蓝天救援" is-link is-link @click.native="callPhone(data.tblueairNum)">
        <div slot="icon" class="chList chList5"><i class="iconfont icon-jiuyuanfeiji"></i></div>
      </mt-cell>
      <div class="chTit">政府机构</div>
      <mt-cell title="外交部领事保护与服务" is-link @click.native="callPhone(data.tmofacpasnum)">
        <div slot="icon" class="chList chList6"><i class="iconfont icon-protect"></i></div>
      </mt-cell>
      <mt-cell title="市民服务热线" is-link @click.native="callPhone(data.tpublicNum)">
        <div slot="icon" class="chList chList7"><i class="iconfont icon-dianhua"></i></div>
      </mt-cell>
      <div class="chTit">公共服务</div>
      <mt-cell title="114查询号" is-link @click.native="callPhone(data.toneonefourNum)">
        <div slot="icon" class="chList chList8"><i class="iconfont icon-sousuo"></i></div>
      </mt-cell>
    </div>
  </div>
  <!--</transition>-->
</template>
<script>
  import {LoginUser} from '@/common/js/common';
  import { Indicator } from 'mint-ui';
  export default {
    name: 'help',
    data () {
      return {
        msg: '求助',
        loginUser:'',
        data:[]
      }
    },
    created:function(){
      this.queryList();
      this.loginUser = LoginUser.getUser();
    },
    activated:function () {
      this.queryList();
      this.loginUser = LoginUser.getUser();
    },
    methods:{
      queryList:function () {
        Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        });
        this.loginUser=LoginUser.getUser();
        let it = this;
        this.$axios({
          method: 'post',
          url: '/thelpOrg/selectHelpInfo',
          data:this.$qs.stringify({
              userId:it.loginUser.userId,
              ip:returnCitySN["cip"]
          })
        }).then(function (response) {
          Indicator.close();
          it.data = response.data.data;
        }).catch(function(){
          Indicator.close();
          throw new Error('网络异常');
        });
      },
      callPhone:function(phoneNum){
        //console.log(phoneNum)
        window.location.href = 'myapp://parameter?type=PhoneCall&Phone=' + phoneNum;
      },
      backHelp:function () {
        //this.$router.push('/help');
        this.$router.goBack();
      }
    }
  }
</script>




