<style scoped>
  .teamBox{text-align: left;background: #f0f0f0;height:100vh;overflow:hidden;}
  .headBox{height:60px;}
.teamSetBox{}
.teamSetList{}
.h10{height:10px;background:#f0f0f0;}
.teamSetButton{width:100%;}
  .teamSetButton input{width:100%;background:#fff;color:#d50c0c;font-size:.18rem;border:none;height:.44rem;line-height:.44rem;}
</style>
<style>
  .teamSetList .mint-cell-title{padding: .14rem 0 .14rem .4rem!important;position: relative;}
  .teamSetList .mint-cell-title .iconfont{top:.2rem;left:0;position: absolute;font-size:.3rem;}
  .teamSetList .mint-cell-title .icon-iconziti17{color:#44a979;}
</style>
<template>
  <transition name="slide">
  <div class="teamBox">
      <mt-header title="团队设置">
        <mt-button  slot="left" icon="back" @click="backTeamView">返回</mt-button>
      </mt-header>

    <div class="teamSetBox hdBody">
      <div class="teamSetName">
        <mt-cell title="团队名称" :value=teamName></mt-cell>
      </div>
      <div class="h10"></div>
      <div class="teamSetList">
        <mt-cell title="团队邀请" label="邀请他人加入" is-link @click.native="addPeople">
          <i slot="icon" class="iconfont icon-iconziti17"></i>
        </mt-cell>
      </div>
      <div class="h10"></div>
      <div class="teamSetButton">
        <input type="button" value="退出团队" @click="exitGroup">
      </div>
    </div>

  </div>
  </transition>
</template>
<script>
  import {LoginUser} from '@/common/js/common';
  import { MessageBox } from 'mint-ui';
  export default {
    name: 'teamSet',
    data () {
      return {
        msg: '团队设置',
        teamName:this.$route.query.groupName,
        loginUser:{}
      }
    },
    created:function() {
      this.loginUser = LoginUser.getUser();
    },
    methods:{
      backTeamView:function(){
        let groupId = this.$route.query.groupId;
        let groupName = this.$route.query.groupName;
        this.$router.push({path: '/teamView', query: { groupId:groupId,groupName:groupName }});
      },
      exitGroup:function () {
        let it = this;
        let groupId = this.$route.query.groupId;
        MessageBox.confirm('确定退出团队?').then(action => {
          this.$axios({
            method: 'post',
            url: '/group/exitGroup',
            data: this.$qs.stringify({
              userId: it.loginUser.userId,
              groupId:groupId
            })
          }).then(function (response) {
            console.log(it.loginUser.userId)
            console.log(groupId)
            if (response.data.code >= 0) {
              it.$router.push('/teamManage/tmTeam');
            }
          })
        });

      },
      addPeople:function () {
        appMethod = this.getAddPeople;
        window.location.href = 'myapp://parameter?type=AddTeamMember&method=callbackAddPeople';
      },
      getAddPeople:function (data) {
        let phones = '',memeberNames='';
        for(let i=0;i<data.data.length;i++){
          phones += data.data[i].phone+',';
          memeberNames += data.data[i].name+',';
          //this.memeberName.push(data.data[i].name);
          //this.phone.push(data.data[i].phone);
        }
        phones = phones.substring(0,phones.length-1);
        memeberNames = memeberNames.substring(0,memeberNames.length-1);
        let it = this;
        let groupId = this.$route.query.groupId;
        let groupName = this.$route.query.groupName;
        this.$axios({
          method: 'post',
          url: '/group/addGroupMember',
          data:this.$qs.stringify({
            userId:it.loginUser.userId,
            groupName:groupName,
            groupId:groupId,
            memeberName:memeberNames,
            phone:phones
          })
        }).then(function (response) {
          let groupId = it.$route.query.groupId;
          let groupName = it.$route.query.groupName;
          it.$router.push({path: '/teamView', query: { groupId:groupId,groupName:groupName }});
        });
      }
    }
  }
</script>




