<template>
  <div class="xcsj">
    <mt-header title="发布随记">
      <mt-button icon="back"  slot="left" @click="backPage">返回</mt-button>
      <mt-button slot="right" @click="fabu">
        发布
      </mt-button>
    </mt-header>
    <div class="xcsjContent">
      <mt-field class="mf"  placeholder="标题" v-model="sjInfo.ftitle"></mt-field>
      <mt-field  class="mf"  placeholder="说点什么吧" type="textarea"   rows="5" v-model="sjInfo.fcontent"></mt-field>
      <div class="scxc-content">
        <div class="xcImages">
          <div class="item" @click="lookImg(idx)" v-for="(image,idx) in imageList.minImages" v-bind:style="{backgroundImage: 'url('+image+')'}">
            <i @click="deleImage($event,idx)" class="shanchu iconfont icon-shanchu"></i>
          </div>
          <div class="item up-img" v-if="imageList.minImages.length<9?true:false">
            <i class="iconfont icon-camera" @click="openSheet"></i>
            <span class="text">上传照片</span>
          </div>
        </div>
      </div>
      <div class="zuobiao">
      <span class="name">地点</span><span v-if="value" class="areaName">{{sjInfo.faddress}}</span><mt-switch  v-model="value"></mt-switch>
    </div>
      <div class="mint-cell" @click="kjfw()"><span class="mint-cell-mask"></span>
        <div class="mint-cell-left"></div>
        <div class="mint-cell-wrapper">
          <div class="mint-cell-title"><!---->
            <div><i class="indexicon icon-indicator"></i> <span>可见范围</span></div>
          </div>
          <div class="mint-cell-value is-link" style="width: 70%"><span class="Fwcontainer">{{groupNameStr}}</span></div>
        </div>
        <div class="mint-cell-right"></div>
        <i  class="mint-cell-allow-right"></i>
      </div>
    </div>
    <mt-actionsheet :actions="[
       {name:'拍照',method:() => selectCamera()},
       {name:'从相册中选择',method:() => selectPicture()}
     ]" v-model="sheetVisible">
    </mt-actionsheet>
  </div>
</template>
<script>
  import { Toast } from 'mint-ui';
  import { MessageBox } from 'mint-ui';
  import {LoginUser} from '@/common/js/common';
  import { Indicator } from 'mint-ui';
  export default {
    name: 'fabusuiji',
    data () {
      return {
        imageList:{
          minImages:[],
          maxImages:[]
        },
        sjInfo:{
          groupId:[],
          userId:LoginUser.getUser().userId,
          ftitle:"",
          fcontent:"",
          faddress:"",
          flat:"",
          flong:"",
          fphoto:[],
          photoNames:[]
        },
        isDisabled:false,
        title:"",
        selectGroupList:[],
        groupNameStr:"",
        value:false,
        sheetVisible:false,
        isShow:""
      }
    },
    activated:function(){
      this.isDisabled=false;
      if(this.value==true){this.openLocation();}  //如果位置采集开关是开启状态，就获取位置信息
      let name = localStorage.getItem('name');
      let sjInfo = JSON.parse(localStorage.getItem('sjInfo'));
      localStorage.removeItem('sjInfo');
      if(!sjInfo){            //从查看随记页面跳转过来要清空
         this.sjInfo={
          groupId:[],
            userId:LoginUser.getUser().userId,
            ftitle:"",
            fcontent:"",
            faddress:"",
            flat:"",
            flong:"",
            fphoto:[],
            photoNames:[]
        };
        this.selectGroupList=[];
        this.imageList.maxImages=[];
        this.imageList.minImages=[];
        this.getGroupFn();
      }
      if(sjInfo&&sjInfo.name&&sjInfo.groupId){
        this.sjInfo.groupId=sjInfo.groupId;
        this.groupNameStr=sjInfo.groupNameStr;
      }
    },
    methods:{
      backPage() {
        MessageBox.confirm('确定放弃当前随记编辑?').then(action => {
          //this.$router.push('/xcheng/tripNote');
          this.$router.goBack();
        });
      },
      deleImage:function(e,index){
          e.stopPropagation();
          MessageBox.confirm('确定删除吗?').then(action => {
            this.imageList.minImages.splice(index,1);
            this.imageList.maxImages.splice(index,1);
            this.sjInfo.photoNames.splice(index,1);
        });
      },
      getGroupFn(){
        Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        });
        let thisTemp=this;
        this.$axios({
          method: 'post',
          url: '/ttravel/selectGroup',
          data:this.$qs.stringify({userId:this.sjInfo.userId})
        }).then(function (response) {
          Indicator.close();
          thisTemp.selectGroupList=response.data.data;
          if(!response.data.data) return;
          thisTemp.groupNameStr=response.data.data[0].groupName;
          thisTemp.sjInfo.groupId.push(response.data.data[0].groupId)
        }).catch(function(){
          Indicator.close();
          throw new Error('网络异常');
        });
      },
      openLocation(){
        appMethod = this.getLocation;
        window.location.href = 'myapp://parameter?type=GetCurrentLocation&method=getAddress';
      },
      getLocation:function(data){
        this.sjInfo.faddress= data.data.LocationName;
        this.sjInfo.flat= data.data.Latitude ;
        this.sjInfo.flong= data.data.Longitude;
      },
      openSheet(){
        this.sheetVisible=true;
      },
      selectPicture:function(){
        appMethod = this.insertImg;
        this.sheetVisible = false;
        let limitNum=9-this.imageList.minImages.length;
        window.location.href = "myapp://parameter?type=TakeAlbum&method=fileImg&imageNum="+limitNum;
      },
      selectCamera:function(){
        appMethod = this.insertImg;
        this.sheetVisible = false;
        let limitNum=9-this.imageList.minImages.length;
        window.location.href = "myapp://parameter?type=TakePhoto&method=fileImg&imageNum="+limitNum;
      },
      lookImg:function(idx){
        let data = {
          ImagesUrlArray:this.imageList.maxImages,
          ImageIndex:idx
        };
        window.location.href = 'myapp://parameter?type=ShowImage&imageData='+JSON.stringify(data);
      },
      insertImg:function(data){
        for(let i=0;i<data.data.length;i++){
          this.sjInfo.photoNames.push(data.data[i].photoName);
          this.imageList.minImages.push(data.data[i].thumbnailURL);
          this.imageList.maxImages.push(data.data[i].sourcePhotoURL);
        }
        appMethod = null;
      },
      fabu:function(){
        if(this.sjInfo.ftitle===''){
          let instance = Toast('标题不能为空');
          setTimeout(() => {
            instance.close();
          }, 2000);
          return;
        }
        if(this.sjInfo.fcontent===''){
          let instance = Toast('内容不能为空');
          setTimeout(() => {
            instance.close();
          }, 2000);
          return;
        }
        let thisTemp=this;
        if(this.isDisabled==true){return};
        this.isDisabled=true;
        Indicator.open({
          text: '发布中...',
          spinnerType: 'fading-circle'
        });
        this.$axios({
          method: 'post',
          url: 'ttravel/insertSj',
          data:this.$qs.stringify(this.sjInfo)
        }).then(function (response) {
          Indicator.close();
          if(response.data.msg=='success'){
            let instance = Toast('发布成功');
            setTimeout(() => {
              instance.close();
              thisTemp.$router.push('/xcheng/tripNote');
            }, 2000);
          }else{
            let instance = Toast('发布失败');
            this.isDisabled==false;
            setTimeout(() => {
              instance.close();
            }, 2000);
          }
        }).catch(function(){
          Indicator.close();
          throw new Error('网络异常');
        });
      },
      kjfw:function(){
        this.$router.push({name:'pushDiary',params:{
          selectVals:{
            groupId:this.sjInfo.groupId,
            groupName:this.groupNameStr,
          },
          groupList:this.selectGroupList
        }})
      }
    },
    watch:{
      value(val){
       if(val==true){
         this.openLocation()
       }else{
         this.sjInfo.faddress="";
         this.sjInfo.flat="";
         this.sjInfo.flong="";
       }
      }
    }
  }
</script>
<style>
  .mf .mint-cell-value{
    width: 100%;
    max-width: 100%;
  }
  .mf .mint-field-state.is-default{
    display: none;
  }
  .xcsjContent  .mint-field-core{
    text-align: left;
    font-size: .16rem;
  }
</style>
<style scoped>
.mint-switch{
  display: inline-block;
  float: right;
  margin-right: .14rem;
  padding-top: .08rem;
}

.zuobiao{
  overflow: hidden;
  height: .5rem;
  line-height: .5rem;
  background: #ffffff;
  border-top: 1px solid #f0f0f0;

}
.xcsjContent .mint-cell-title{
  text-align: left;
  font-size: .16rem;
}
.mint-cell-value.is-link{
  margin-right: 0;
}
.zuobiao .name{
  display: inline-block;
  float: left;
  width: 20%;
  font-size: .16rem;
  text-indent: .1rem;
  text-align: left;
  color: #414141;
}
.zuobiao .areaName{
  display: inline-block;
  width: 50%;
  float: left;
  text-align: right;
  white-space:nowrap;
  text-overflow:ellipsis;
  overflow:hidden;
}
  .fr{
    float: right;
  }
  .xcsj{
    height: 100%;
    width: 100%;
    overflow: hidden;
    background: #f0f0f0;
  }
  .xcsjContent{
    overflow-y: auto;
    height: calc(100vh - .7rem);
    -webkit-overflow-scrolling: touch;
    overflow-x: hidden;
  }


  .scxc-content {
    overflow: hidden;
    padding-left: .1rem;
    padding-right: .1rem;
    background: #ffffff;
  }
  .scxc-content ul{
    display: block;
    width: 100%;
    overflow: hidden;
  }
  .scxc-content li {
    width: 30%;
    height: .68rem;
    float: left;
    margin-right: 5%;
    margin-bottom: .18rem;
  }

  .scxc-content li:nth-child(3n+0) {
    margin-right: 0;
  }

.scxc-content li div{
  width: 100%;
  height: 70px;
  background-size: cover;
}
.scxc-content .onSheet{
  background-size: 100% 100% !important;
  background-image: url("../../../../static/img/load-pic.jpg");
}
.xcImages{
  /*width: 98%;*/
  margin: 10px auto;
  text-align: left;
  font-size: 0;
}
.xcImages .item{
  height: 70px;
  width: 31.333333%;
  background-color: #f8f8f8;
  display: inline-block;
  margin: 0.1rem 1% 0 1%;
  background-size: cover;
  background-position:center center;
  position: relative;
  text-align: center;
}
.xcImages .item .icon-camera {
  font-size: 45px;color: #cfcfcf;
}
.xcImages .item .text {
  display: block; font-size: 14px; color: #8f8f8f;    line-height: 20px;
}
.xcImages .item .shanchu{
  display: block;
  position: absolute;
  right: -.04rem;
  top: -.04rem;
  color: red;
}
  .Fwcontainer{
    display:block;
    width: 100%;
    height: 20px;
    line-height: 20px;
    float: right;
    text-align: right;
    white-space:nowrap;
    text-overflow:ellipsis;
    -webkit-line-clamp:1;
    -webkit-box-orient:vertical;
    overflow:hidden;
    padding-right: 0.2rem;
    box-sizing: border-box;
  }
.thImg{overflow:hidden;}
.thImg img{float:left;margin:.1rem;width:1rem;height:.68rem;}

</style>




