<template>
  <div class="xcsj">
    <mt-header title="可见范围">
      <mt-button icon="back"  slot="left" @click="backPage">返回</mt-button>
      <mt-button slot="right" @click="wancheng">
        完成
      </mt-button>
    </mt-header>
    <div class="hdBody">
      <mt-checklist class="allStyle"
        align="right"
        v-model="All"
        :options="allOptions" >
      </mt-checklist>
      <div id="checkboxId">
        <mt-checklist
          :id="value"
          align="right"
          v-model="value"
          :options="options">
        </mt-checklist>
      </div>
    </div>
  </div>
</template>
<script>
  import { Toast } from 'mint-ui';
  import { MessageBox } from 'mint-ui';
  import {LoginUser} from '@/common/js/common';
  export default {
    name: 'fabusuiji',
    data () {
      return {
        value:[],
        All:[],
        allOptions:["全部团队"],
        options: [],
        allGroupId:[],
        userId:LoginUser.getUser().userId,
        groupObj:{}
      }
    },
    activated:function(){
      this.value = [];
      this.initData();
    },
    watch:{
      All:function(val){
        if(val=="全部团队"){
         this.value=this.allGroupId;
        }else{
          this.value=[];
        }
      }
    },
    methods:{
      toastFn:function(text){
        let instance = Toast(text);
        setTimeout(function () {
          instance.close();
        }, 2000);
      },
      initData:function(){
        var groupList=this.$route.params.groupList;
        let _this=this;
        this.options=[];
        this.allGroupId=[];
        groupList.forEach(function(item){
          var newObj=  {
            label: item.groupName,
            value: item.groupId
          };
          let attr=item.groupId;
          _this.groupObj[attr]=item.groupName;
          _this.allGroupId.push(item.groupId);
          _this.options.push(newObj);
        });
        let selectVals=this.$route.params.selectVals;
        setTimeout(function(){
          if(_this.options.length<=0) return;
          _this.value = selectVals.groupId;
        },100);
      },
      wancheng:function() {
        if(this.value.length<1){
          this.toastFn("请选择可见范围");
          return;
        }
        let _this=this;
        let groupNameArr=[];
        this.value.forEach(function(item){
          groupNameArr.push(_this.groupObj[item]);
        });
        let groupNameStr=groupNameArr.join(",");
        //this.$router.push({path:"/fabusuiji",query:{name:"kjfw",groupId:this.value,groupNameStr:groupNameStr}})
        let sjInfo = {name:"kjfw",groupId:this.value,groupNameStr:groupNameStr};
        localStorage.setItem('sjInfo',JSON.stringify(sjInfo));
        this.$router.goBack();
      },
      backPage(){
        //this.$router.push({path:"/fabusuiji",query:{name:"kjfw"}});
        let sjInfo = {name:"kjfw"};
        localStorage.setItem('sjInfo',JSON.stringify(sjInfo));
        this.$router.goBack();
      }
    }
  }
</script>
<style>
  .xcsj .mint-checklist-title{
    margin: 0;
  }
  .allStyle a.mint-cell{
    background-image: none;
  }
  .allStyle  .mint-cell-wrapper{
    background-image: none;
  }
  .xcsj .mint-checkbox-label{
    font-size: .16rem;
  }
  .xcsj .mint-checklist-label{
    text-align: left;
  }
</style>




