<template>
  <div id="app">
    <div class="shadow network" style="" id="newWorkId">
      <span>网络异常</span>
    </div>
    <div class="shadow" id="shadowId">
      <div class="logoutBox">
        <div class="header">
          <div class="title">提示</div>
        </div>
        <div class="content">您的帐号已在其他设备登录</div>
        <div class="btn" id="logOutOkId">确认</div>
      </div>
    </div>
    <keep-alive>
      <router-view name="body"></router-view>
    </keep-alive>
    <router-view name="footer"></router-view>
  </div>
</template>

<script>
export default {
  name: 'app'
}
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
  }
  .shadow {
    display: none;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height:100%;
    width: 100%;
    background-color: rgba(0,0,0,0.4);
    z-index: 999;
    transition: opacity 0.3s;
    opacity: 1;
  }
  .network{
    transition: opacity .3s;
    background-color: rgba(0,0,0,0);
  }
  .network span{
    display: block;
    position: absolute;
    top: 0;left: 0;right: 0;bottom: 0;
    margin: auto;
    height: 0.3rem;
    width: 0.8rem;
    text-align: center;
    line-height: 0.3rem;
    background-color: rgba(0,0,0,.6);
    border-radius:3px;
    color: #fff;
  }
  .shadow:after {
     content: "";
     display: inline-block;
     height: 100%;
     width: 0;
     vertical-align: middle;
   }
  .logoutBox {
    display: inline-block;
    min-width: 60%;
    background-color: #fff;
    border-radius: 5px;
  }
  .logoutBox .header {
    padding: 15px 20px 0;
  }
  .logoutBox .title {
    font-size: 0.16rem;
    font-weight: 700;
    height: 18px;
    color: #333;
  }
  .logoutBox .content {
    padding: 15px 20px;
    font-size: 0.14rem;
  }
  .logoutBox .btn {
    padding: 0 20px 15px;
    font-size: 0.16rem;
    color: #0f5bf5;
  }
</style>



