<template>
  <div class="msgBody">
    <div class="no-data-pub" v-if="xcList.length<=0">
      <i class="iconfont  icon-zanwuxiaoxi"></i>
      <p class="text">暂无行程信息</p>
      <p class="sub-text">点击“创建行程”试试吧</p>
    </div>
      <div class="notices xcBody" v-else>
          <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom"  :autoFill="false"  :bottom-all-loaded="allLoaded" ref="loadmore">
            <div class="container">
              <div class="box"     :val="idx" :dType="dType" @click="clickList(xc.fid)"  v-longtouch="timeOutEvent" v-for="(xc,idx) in xcList">
                <div class="msgTitle">
                  {{xc.fname}}
                </div>
                <div class="item">
                  <span><i class="iconfont icon-lvxinghover"></i>{{xc.fcityFrom}}-{{xc.fcityTo}}</span>
                  <span><i class="iconfont icon-bourn2"></i>{{xc.fnum}}个目的地</span>
                </div>
                <div class="item">

                  <span class="time1"><i class="iconfont icon-rili"></i>{{xc.fstartAt}}到{{xc.fbackAt}}</span>
                </div>
                <div class="item">
                  <span class="luxian"  @click="clickluxian">行程路线</span>
                </div>
              </div>
            </div>
       </mt-loadmore>
    </div>
</div>
</template>

<script>
  import {LoginUser} from '@/common/js/common';
  import { Toast } from 'mint-ui';
  import { MessageBox } from 'mint-ui';
  import shared1 from '@/components/xcheng/shared1/shared1'
  export default {
    name: 'gerenxingcheng',
    data () {
      return {
        dType:"geren",
        deleIndex:0,
        timeOutEvent:1,
        xcList:[],
        pageNum:1,
        pageSize:10,
        allLoaded:false
      }
    },
    activated:function(){
      longTouchMethodG = this.longA;
      this.xcList=[];
      this.pageNum=1;
      this.queryList();
    },
    created:function(){
      longTouchMethodG = this.longA;
      this.xcList=[];
      this.pageNum=1;
      this.queryList();
    },
    methods: {
      longA:function(e){

        let flag = true,dom= rootDom(e,'box');
        let obj=this.xcList[dom.getAttribute('val')];
        this.deleIndex=dom.getAttribute('val');
        if(obj.fshared!=0){
          let instance = Toast('禁止操作');
          setTimeout(() => {
            instance.close();
        }, 2000);
        }else{
          MessageBox.confirm('确定删除该行程吗?').then(action => {
            this.delFn(obj.fid)
        });
        }
      },
      delFn(fid){
        let _this=this;
        this.$axios({
          method: 'post',
          url: 'ttravel/deleteTravelGe',
          data:this.$qs.stringify({
            userId:LoginUser.getUser().userId,
            fid:fid
          })
        }).then(function(response){
          if(response.data.msg=='success'){
            let instance = Toast('删除成功');
            _this.xcList.splice(_this.deleIndex, 1);
            setTimeout(() => {
              instance.close();
          }, 2000);
          }
        });
      },
      loadTop:function() {
        this.pageNum = 1;
        this.allLoaded = false;
        this.queryList();
        this.$refs.loadmore.onTopLoaded();
      },
      loadBottom:function(){
        this.pageNum++;
        this.queryList();
      },
      postion:function(){
        if(this.pageNum===1){
          this.$nextTick(function() {
            var dom = document.getElementsByClassName('mint-loadmore');
            dom[0].parentNode.scrollTop = 0;
          })
        }
      },
      queryList(){
        let _this = this;
        this.$axios({
          method: 'post',
          url: 'ttravel/ListTravelGe',
          data: _this.$qs.stringify({
            userId: LoginUser.getUser().userId,
            pageNum: _this.pageNum,
            pageSize: _this.pageSize
          })
        }).then(function (response) {
          if(response.data.msg=='success'){
            if(!response.data.data.list||response.data.data.list.length<=0){
              _this.allLoaded = true;
            }
            if(_this.pageNum===1){
              _this.xcList = response.data.data.list;
            }else{
              _this.xcList = _this.xcList.concat(response.data.data.list);
            }
          }
          if(!_this.$refs.loadmore) return;
          _this.$refs.loadmore.onBottomLoaded();
          _this.postion();
        });
      },
      clickList(idx){
        //this.$router.push({name:"xcInfo",params:{name:"gerenList",fid:idx}})
        let routerInfo = {name:"gerenList",fid:idx};
        localStorage.setItem("routerInfo",JSON.stringify(routerInfo));
        _this.$router.push({name:'xcInfo'});
      },
      clickluxian (e){
        e.stopPropagation();
        let instance = Toast('暂无行程路线');
        setTimeout(function () {
          instance.close();
        }, 2000);
      }
  }
  }
</script>

<style scoped>
  .mt{
    width: 100%;
  }
  .msgBody {
    width: 100%;
    text-align: left;
    overflow-y: scroll;
    background-color: #f0f0f0;
    -webkit-overflow-scrolling: touch;
    overflow-x: hidden;
  }
  .notices .box{
    padding: 0.16rem 0.15rem 0.3rem 0.15rem ;
    margin-top: 0.16rem;
    background-color: #fff;
    overflow: hidden;
  }
  .msgTitle{
    font-size: 1.2em;
    padding-bottom: 10px;
    line-height: .32rem;
    overflow: hidden;
    white-space:nowrap;
    text-overflow:ellipsis;
    overflow:hidden;
    height: .32rem;
  }
  .box .item{
    width: 100%;
    border-top: 1px  solid #d9d9d9;
    line-height: .3rem;
    padding-top: .1rem;

  }
  .msgBody .box .item:last-child{
    margin-top: 15px;
  }
  .box .item span{
    display: inline-block;
    vertical-align: middle;
    font-size: 0.16rem;
  }

  .box .item span:first-child{
    width: 36%;
    white-space:nowrap;
    text-overflow:ellipsis;
    overflow:hidden;
  }
  .box .item span.time1{
    width: 100%;
  }
  .icon-lvxinghover{ color: #8a68d7;}
  .icon-bourn2{color: #33aa59;}
  .icon-tuandui3{color: #f6be61;}
  .icon-rili{color: #f66755;}
  .iconfont{
    margin-right: 5px;
  }

  .luxian{
    font-size: .16rem;
    color: #2074e7;
    float: right;
  }
  .xcBody{
    height: calc(100vh - .6rem - 1.1rem);
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    overflow-x: hidden;
  }
  .container{
    min-height: calc(100vh - .6rem - 1.1rem);
  }
</style>




