<template>
  <div class="sharedContainer">
    <div class="meng"  @click="clickqx"></div>
    <div class="fengxiangBox">
      <h3>分享团队</h3>
      <div class="container">
        <mt-checklist
          v-model="value"
          :options="options">
        </mt-checklist>
      </div>
      <div class="item">
        <span class="caozuo fr"  @click="clickfx($event)">分享</span>
        <span class="caozuo fr"  @click="clickqx($event)">取消</span>

      </div>
    </div>
  </div>


</template>
<script>
  import {LoginUser} from '@/common/js/common';
  import { Toast } from 'mint-ui';
  import { MessageBox } from 'mint-ui';
  export default {
    name: 'shared1',
    props:[],
    data () {
      return {
        userId:LoginUser.getUser().userId,
        value:[],
        options:[]
      }
    },
    created(){
      this.queryList();
    },
    methods: {
      queryList(){
        this.value=[];
        let thisTemp=this;
        this.$axios({
          method: 'post',
          url: '/ttravel/selectGroup',
          data:this.$qs.stringify({userId:this.userId})
        }).then(function (response) {
          thisTemp.options=[];
          thisTemp.allGroupId=[];
          response.data.data.forEach(function(item){
            var newObj=  {
              label: item.groupName,
              value: item.groupId,
            }
            thisTemp. allGroupId.push(item.groupId);
            thisTemp.options.push(newObj);
          });
        });
      },
      clickfx(e){
        e.stopPropagation();
        if(this.value.length<1){
          let instance = Toast('请选择分享团队');
          setTimeout(() => {
            instance.close();
          }, 1000);
          return;
        }
        var obj={
          groupId:this.value
        };
        this.$emit("fxEvent",obj)
      },
      clickqx(e){
        e.stopPropagation();
        this.$emit("qxEvent")
      }
    }
  }
</script>
<style scoped>



  .fr{
    float: right;
  }
  .fl{
    float: left;
  }
  .caozuo{
    font-size: .16rem;
    color: #2074e7;
    display: block;
    width: 50%;
    text-align: center;
  }

  .meng{
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    position: fixed;
    background: rgba(0,0,0,.5);
  }
  .fengxiangBox{
    width: 80%;
    height: 3rem;
    position: fixed;
    z-index: 10;
    background: #ffffff;
    top: 1.5rem;
    left: 10%;
  }
  .fengxiangBox h3{
    text-align: center;
    line-height: .36rem;
    padding-top: .08rem;
  }
  .fengxiangBox .item .caozuo{
    width: .6rem;
    line-height: .4rem;
  }
  .fengxiangBox  .container{
    height: 2rem;
    overflow: auto;
  }
</style>



