<style scoped>
  .msgBody{
    width: 100%;
    height: calc(100vh);
    text-align: left;
    overflow: hidden;
    background-color: #f0f0f0;
  }
  .notices .box{
    padding: 0.2rem 0.15rem;
    margin-top: 0.48rem;
    background-color: #fff;
    position: relative;
  }
  .msgTitle{
    font-size: 1.2em;
    padding-bottom: 10px;
  }
  .box div:first-child{
    position: absolute;
    text-align: center;
    width: calc(100% - 0.3rem);
    top: -30px;
  }
  .box div:first-child label{
    display: inline-block;
    text-align: center;
    padding: 0 10px;
    border-radius: 3px;
    color: #fff;
    line-height: 20px;
    background-color: #c5c5c5;
  }
  .msgContent{
    color: #666;
    opacity: 0.8;
    position: relative;
  }
  .msgContent span{
    display: inline-block;
    padding-left: 0.3rem;
  }
  .notices .icon-xiaoxi{
    position: absolute;
    display: block;
    width: 0.16rem;
    height: 0.18rem;
    background-image: url("./xiaoxi.png");
    background-size: 100% 100%;
  }
  .msg_sign span label{
    color: #666;
    padding-left: 6px;
  }
  .msg_sign span i{
    display: inline-block;
    width: 0.2rem;
    height: 0.2rem;
    background-image: url("./yaoqing.png");
    background-size: 100% 100%;
    position: relative;
    top:3px;
    margin-right: 0.05rem;
  }
</style>
<template>
  <transition name="slide">
  <div class="msgBody">
    <mt-header title="团队邀请">
      <mt-button slot="left" icon="back" @click="backPage">返回</mt-button>
    </mt-header>
    <div class="notices hdBody">
      <mt-loadmore :bottom-method="loadBottom" :top-method="loadTop" :bottom-all-loaded="allLoaded" :autoFill="false" ref="loadmore">
        <div class="j_loadmore">
          <div class="box" v-for="invite in inviteList">
            <div><label>{{invite.date|FormartDate}}</label></div>
            <div class="msgTitle">
              团队名称：{{invite.groupName}}
            </div>
            <div class="msg_sign">
              <span><i></i><label>{{invite.inviteName}}</label></span>
            </div>
          </div>
        </div>
      </mt-loadmore>
      <!--<div class="box">-->
        <!--<div><label>10:21</label></div>-->
        <!--<div class="msgTitle">-->
          <!--团队名称：XXXXXXX-->
        <!--</div>-->
        <!--<div class="msg_sign">-->
          <!--<span><i></i><label>张大雷</label></span>-->
        <!--</div>-->
      <!--</div>-->
      <!--<div class="box">-->
        <!--<div><label>2017年5月20日</label></div>-->
        <!--<div class="msgTitle">-->
          <!--团队名称：XXXXXXX-->
        <!--</div>-->
        <!--<div class="msg_sign">-->
          <!--<span><i></i><label>张大雷</label></span>-->
        <!--</div>-->
      <!--</div>-->
    </div>
  </div>
  </transition>
</template>
<script>
  import {LoginUser} from '@/common/js/common';
  import { Toast } from 'mint-ui';
  import { Loadmore } from 'mint-ui';
  export default {
    name: 'teamInvite',
    data () {
      return {
         pageNum:1,
         pageSize:20,
         allLoaded:false,
         userId:LoginUser.getUser().userId,
         inviteList:[]
      }
    },
    components:{Loadmore},
    activated:function(){
      this.queryList();
    },
    created:function(){
       this.queryList();
    },
    methods:{
      loadTop:function() {
        this.pageNum = 1;
        this.allLoaded = false;
        this.queryList();
        this.$refs.loadmore.onTopLoaded();
      },
      loadBottom:function(){
        this.pageNum++;
        this.queryList();
      },
      postion:function(){
        if(this.pageNum===1){
          this.$nextTick(function() {
            var dom = document.getElementsByClassName('mint-loadmore');
            dom[0].parentNode.scrollTop = 0;
          })
        }
      },
      backPage:function(){
        if(this.$route.query.param == 'teamManage'){
          this.$router.push('/teamManage');
        }else if(this.$route.query.param){
          this.$router.push('/msgList?storage=true&param='+this.$route.query.param);
        }else if(this.$route.query.isMsg){
          this.$router.push('/msgList?param=home');
        }else{
          this.$router.push('/msgList?storage=true');
        }
      },
      queryList:function(){
        let _this = this;
        this.$axios({
           method: 'post',
           url: 'groupMessage/getGroupInvite',
           data:this.$qs.stringify({
              userId:this.userId,
              pageNum:_this.pageNum,
              pageSize:_this.pageSize
           })
        }).then(function (response) {
          if(response.data.msg=='success'){
            if(!response.data.data.list||response.data.data.list.length<=0){
              _this.allLoaded = true;
            }
            if(_this.pageNum===1){
              _this.inviteList = response.data.data.list;
            }else{
              _this.inviteList = _this.inviteList.concat(response.data.data.list);
            }
          }
          _this.$refs.loadmore.onBottomLoaded();
          _this.postion();
        });
      }
    }
  }
</script>




