<style scoped>
  .rg_body{
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    color: #fff;
    background-color: #f0f0f0 !important;
    margin:-1px 0 0 0;
    padding: 0;
  }
  .rgfooter, .rg_input, .mint-button--primary{
     width: 76%;
     margin: 0 auto;
  }
  .rg_input{
     margin: 0 auto 0 auto;
     position: relative;
  }
  .rg_input input{
     width: calc(100% - 0.24rem);
     margin-top: 0.12rem;
     height: 0.36rem;
     border-radius: 0.18rem;
     padding: 0 0.12rem;
     outline: none;
     border: solid 1px #bababa;
  }
  .rg_input input[name='check']{
     padding-right: 45%;
     width: calc(100% - 0.12rem - 45%);
  }
  .rg_input span{
      display: block;
      position: absolute;
      top: 0.19rem;
      right: 0.14rem;
      height: 0.2rem;
      border-radius: 0.15rem;
      line-height: 0.2rem;
      padding: 0.02rem 0.1rem;
      background:-webkit-gradient(linear, 0 0, right 0, from(rgba(255,183,10,0.8)), to(#ff6a01));
  }
  .rgfooter{
     margin: 0.2rem auto 0 auto;
     padding-left: 0.2rem;
     text-align: left;
     color: #bababa;
     font-size: 0.8em;
  }
  .rgfooter span:nth-child(2) a,.footer span:nth-child(2) a{
     color: #1d8ee6;
  }
  .mint-button--primary{
     margin-top: 0.3rem;
     background:-webkit-gradient(linear, 0 0, right 0, from(rgba(22,163,228,0.8)), to(#0f5bf5));
     height: 0.5rem;
     border-radius: 0.25rem;
  }
  .footer{
    margin: 0.2rem auto 0 auto;
    text-align: center;
    color: #bababa;
  }
  .getTime {
    background: #ccc !important;
  }
</style>
<template>
  <div class="rg_body">
    <mt-header title="忘记密码">
        <mt-button slot="left" icon="back" @click="closePage">登录</mt-button>
    </mt-header>
    <div class="rg_input" style="margin-top: 8%;">
      <input v-model="user.phoneNumber" type="number" placeholder="请输入手机号">
    </div>
    <div class="rg_input">
      <span @click="getVerificationCode" v-show="getVCode">获取验证码</span>
      <span class="getTime" v-show="!getVCode">重新获取({{getTime}}s)</span>
      <input v-model="user.smsCode" placeholder="验证码" name="check">
      <input v-model="user.password" type="password" placeholder="请输入新密码">
      <input v-model="user.rePwd" type="password" placeholder="请再次输入密码">
    </div>
    <mt-button size="large" type="primary" @click="resetPwd">提交并重置密码</mt-button>
    <div class="footer">
      <span>已有账号</span>
      <span><a @click="closePage">快速登录</a></span>
    </div>
  </div>
</template>
<script>
  import { Toast } from 'mint-ui';
  import { Indicator } from 'mint-ui';
  import {LoginUser} from '@/common/js/common';
  export default {
    name: 'register',
    data () {
      return {
        getVCode:true,
        getTime:60,
        user: {}
      }
    },
    activated:function() {
      this.user={};
    },
    methods:{
      closePage:function(){
        this.$router.go(-1);
      },
      phoneNumValidate:function (num) {
        let tel = /^1(3|4|5|7|8)\d{9}$/;
        return tel.test(num);
      },
      getVerificationCode:function () {
        let _this = this;
        if (!_this.getVCode){ return }
        let phoneNumber = this.user.phoneNumber;

        if (!this.phoneNumValidate(phoneNumber)){
          let instance = Toast("请输入正确的手机号码");
          setTimeout(() => {
            instance.close();
          }, 1000);
          return false;
        }
        Indicator.open({
          text: '发送中...',
          spinnerType: 'fading-circle'
        });
        this.$axios({
          method: 'post',
          url: 'register/smsSendPassword',
          data:{phoneNumber:phoneNumber}
        }).then(function (response) {
          Indicator.close();
          if(response.data.msg=='success'){
            console.log(response.data);
            let instance = Toast("短信已发送,请注意查收");
            _this.getVCode = false;
            let countDown = setInterval(() => {
              _this.getTime--;
              if(_this.getTime<1){
                clearInterval(countDown);
                _this.getVCode = true;
                _this.getTime = 60;
              }
            },1000);
            setTimeout(() => {
              instance.close();
            }, 1000);
          }else{
            let instance = Toast(response.data.msg);
            setTimeout(() => {
              instance.close();
            }, 1000);
          }
        }).catch(function(){
          Indicator.close();
          throw new Error('网络异常');
        });
      },
      resetPwd:function(){
        let tpThis = this;
        if (!this.phoneNumValidate(tpThis.user.phoneNumber)){
          let instance = Toast("请输入正确的手机号码");
          setTimeout(() => {
            instance.close();
          }, 1000);
          return false;
        }
        if (!tpThis.user.smsCode){
          let instance = Toast("请输入验证码");
          setTimeout(() => {
            instance.close();
          }, 1000);
          return false;
        }
        if (!tpThis.user.password){
          let instance = Toast("请输入密码");
          setTimeout(() => {
            instance.close();
          }, 1000);
          return false;
        }
        if (!tpThis.user.rePwd){
          let instance = Toast("请再次输入密码");
          setTimeout(() => {
            instance.close();
          }, 1000);
          return false;
        }
        if (tpThis.user.password != tpThis.user.rePwd){
          let instance = Toast("俩次输入的密码不一致");
          setTimeout(() => {
            instance.close();
          }, 1000);
          return false;
        }
        Indicator.open({
          text: '提交中...',
          spinnerType: 'fading-circle'
        });
        let stime = setTimeout(() => {
          Indicator.close();
        }, 5000);
        this.$axios({
          method: 'post',
          url: 'register/resetPassword',
          data:this.$qs.stringify(this.user)
        }).then(function (response) {
          Indicator.close();
          clearTimeout(stime);
          if(response.data.code==1){
            LoginUser.setUser({
              username:tpThis.user.phoneNumber
            });
            let instance = Toast("密码修改成功");
            setTimeout(() => {
              instance.close();
            }, 1000);
            tpThis.closePage();
          }else{
            let instance = Toast(response.data.msg);
            setTimeout(() => {
              instance.close();
            }, 1000);
          }
        }).catch(function(){
          Indicator.close();
          clearTimeout(stime);
          throw new Error('网络异常');
        });
      }
    }
  }
</script>




